import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

export async function extractSnippetsFromCapturesForGo(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForGo]")

	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	const functionCaptureNames = new Set([
		"name.definition.function",
		"name.definition.method",
		"parameters.definition.function",
		"body.definition.function",
		"definition.function",
	])
	const functionNodeMap = new Map<string, Parser.QueryCapture>()

	captures.forEach((capture) => {
		if (functionCaptureNames.has(capture.name)) {
			const node = capture.node
			const key = `${node.startIndex}:${node.endIndex}`
			if (!functionNodeMap.has(key)) {
				functionNodeMap.set(key, capture)
			}
		}
	})

	// 处理 import
	captures.forEach((capture) => {
		if (capture.name === "name.definition.import") {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) return
			seenHashes.add(snippetHash)
			const name = sourceCode.substring(node.startIndex, node.endIndex)
			const scope = buildScopeChain(node)
			snippets.push({
				name,
				type: SnippetType.ImportOrInclude,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				scope,
				fileHash,
			})
		}
	})

	// 处理 function/method
	for (const capture of captures) {
		if (capture.name !== "definition.function" && capture.name !== "definition.method") continue
		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 判断是 function 还是 method
		const isMethod = capture.name === "definition.method"
		const nameCap = captures.find(
			(c) =>
				c.name === (isMethod ? "name.definition.method" : "name.definition.function") &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		const paramCaps = captures.filter(
			(c) =>
				c.name === (isMethod ? "parameters.definition.method" : "parameters.definition.function") &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		const parameters: ParameterInfo[] = paramCaps.map((paramCap) => ({
			name: sourceCode.substring(paramCap.node.startIndex, paramCap.node.endIndex),
		}))

		const bodyCap = captures.find(
			(c) =>
				c.name === (isMethod ? "body.definition.method" : "body.definition.function") &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)

		let rangeText: string
		let implementText: string | undefined = undefined
		if (bodyCap) {
			rangeText = sourceCode.substring(node.startIndex, bodyCap.node.startIndex).trimEnd()
			implementText = sourceCode.substring(node.startIndex, node.endIndex)
		} else {
			rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		}

		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		// 查找 returnType（result.definition.function/method）
		const resultCap = captures.find(
			(c) =>
				c.name === (isMethod ? "result.definition.method" : "result.definition.function") &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		let returnType = ""
		if (resultCap) {
			returnType = sourceCode.substring(resultCap.node.startIndex, resultCap.node.endIndex).trim()
		}

		const paramStr = parameters.map((p) => p.name).join(", ")
		const signature = `${name}(${paramStr})`
		const scope = buildScopeChain(node)

		// 提取 field/class 名称（仅 method 有）
		let field: string | undefined = undefined
		if (isMethod) {
			const selfCap = captures.find(
				(c) =>
					c.name === "self.definition.method" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
			if (selfCap) {
				// Go query: (type_identifier) @class.definition.method
				const classCap = captures.find(
					(c) =>
						c.name === "class.definition.method" &&
						c.node.startIndex >= selfCap.node.startIndex &&
						c.node.endIndex <= selfCap.node.endIndex,
				)
				if (classCap) {
					field = sourceCode.substring(classCap.node.startIndex, classCap.node.endIndex)
				}
			}
		}

		snippets.push({
			name,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			definition: {
				name,
				type: node.type,
				parameters,
				returnType,
			},
			parameters,
			signature,
			language: CodeLanguageType.Go,
			...(implementText ? { implementText } : {}),
		})
	}

	// 处理 struct/interface/type
	const typeDefs = [
		{ capture: "name.definition.struct", outlineType: "struct" },
		{ capture: "name.definition.interface", outlineType: "interface" },
		{ capture: "name.definition.type", outlineType: "type" },
	]
	for (const { capture, outlineType } of typeDefs) {
		const defCaptureName = `definition.${outlineType}`
		for (const cap of captures.filter((c) => c.name === capture)) {
			const node = cap.node
			// 判断是否在函数/方法内
			const isInFunc = captures.some(
				(fc) =>
					(fc.name === "definition.function" || fc.name === "definition.method") &&
					fc.node.startIndex < node.startIndex &&
					node.endIndex < fc.node.endIndex,
			)
			if (isInFunc) continue
			// 优先用 @definition.xxx 区间
			const defCap = captures.find(
				(c) =>
					c.name === defCaptureName &&
					c.node.startIndex <= node.startIndex &&
					node.endIndex <= c.node.endIndex,
			)
			const rangeText = defCap
				? sourceCode.substring(defCap.node.startIndex, defCap.node.endIndex)
				: sourceCode.substring(node.startIndex, node.endIndex)
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)
			const name = sourceCode.substring(node.startIndex, node.endIndex)
			snippets.push({
				name,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				implementText: rangeText,
				scope: [],
				fileHash,
				field: "",
				definition: { name, type: outlineType },
				language: CodeLanguageType.Go,
			})
		}
	}

	// 处理 const/var（全局变量/常量）
	const varDefs = [
		{ capture: "name.definition.const", outlineType: "const" },
		{ capture: "name.definition.var", outlineType: "var" },
	]
	for (const { capture, outlineType } of varDefs) {
		const defCaptureName = `definition.${outlineType}`
		for (const cap of captures.filter((c) => c.name === capture)) {
			const node = cap.node
			// 判断是否在函数/方法内
			const isInFunc = captures.some(
				(fc) =>
					(fc.name === "definition.function" || fc.name === "definition.method") &&
					fc.node.startIndex < node.startIndex &&
					node.endIndex < fc.node.endIndex,
			)
			if (isInFunc) continue
			// 优先用 @definition.xxx 区间
			const defCap = captures.find(
				(c) =>
					c.name === defCaptureName &&
					c.node.startIndex <= node.startIndex &&
					node.endIndex <= c.node.endIndex,
			)
			const rangeText = defCap
				? sourceCode.substring(defCap.node.startIndex, defCap.node.endIndex)
				: sourceCode.substring(node.startIndex, node.endIndex)
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)
			const name = sourceCode.substring(node.startIndex, node.endIndex)
			snippets.push({
				name,
				type: SnippetType.VariableOrConstant,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				implementText: rangeText,
				scope: [],
				fileHash,
				field: "",
				definition: { name, type: outlineType },
				language: CodeLanguageType.Go,
			})
		}
	}

	// filter snippets by snippetTypes
	return snippets.filter((snippet) => snippetTypes.includes(snippet.type)).sort((a, b) => a.startLine - b.startLine)
}

// VSCode Outline Entry 类型
export interface OutlineEntry {
	type: string
	name: string
	file: string // 表示当前文件
	field?: string // 空字符串代表当前文件的global
	description?: string
}

/**
 * 根据 Go 文件的所有 snippet 生成大纲（Outline），支持 import、const、var、function、method（带 field）、全局变量
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): OutlineEntry[] {
	const outline: OutlineEntry[] = []

	// 只保留全局定义（scope 为空或全为 ""）
	const isGlobal = (s: ISnippetMeta) => !s.scope || s.scope.length === 0 || s.scope.every((x) => !x)

	for (const s of snippets) {
		if (!isGlobal(s)) continue
		if (s.type === SnippetType.ImportOrInclude) {
			outline.push({
				type: "import",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.FunctionOrMethod) {
			if (s.field) {
				outline.push({
					type: "method",
					name: s.name,
					file: s.filePath,
					field: s.field,
					description: s.definitionText,
				})
			} else {
				outline.push({
					type: "function",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.definitionText,
				})
			}
		} else if (s.type === SnippetType.VariableOrConstant) {
			const t = s.definition?.type === "const" ? "const" : "var"
			outline.push({
				type: t,
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.ClassOrInterfaceOrStructOrEnum) {
			const t = s.definition?.type || "type"
			outline.push({
				type: t,
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.definitionText,
			})
		}
		// 可扩展 channel/goroutine 等
	}

	return outline
}

/**
 * 将 OutlineEntry[] 格式化为文本，同一个 field 的放在一起。
 * @param outline OutlineEntry[]
 * @returns string
 */
export function formatOutlineText(outline: OutlineEntry[]): string {
	// 按 field 分组
	const groups: Record<string, OutlineEntry[]> = {}
	for (const entry of outline) {
		const key = entry.field ?? ""
		if (!groups[key]) groups[key] = []
		groups[key].push(entry)
	}

	// 合成文本
	let result = ""
	for (const field of Object.keys(groups).sort()) {
		const group = groups[field]
		const title = field === "" ? "[global]" : `[${field}]`
		result += title + "\n"
		for (const entry of group) {
			let line = `  ${entry.type}`
			if (entry.description) line += `: ${entry.description.replace(/\n/g, " ")}`
			result += line + "\n"
		}
		result += "\n"
	}
	return result.trimEnd()
}
