import * as fs from "fs/promises"
import * as path from "path"
import type { Dirent } from "fs"

export interface TreeViewOptions {
	showDotDirs?: boolean
	allowOtherExtensions?: boolean
}

// 缓存.gitignore规则
const gitignoreCache = new Map<
	string,
	{ mtimeMs: number; rules: string[]; filter: (p: string, isDir: boolean) => boolean }
>()

async function getGitignoreFilter(dirPathRoot: string): Promise<(p: string, isDir: boolean) => boolean> {
	const gitignorePath = path.join(dirPathRoot, ".gitignore")
	try {
		const stat = await fs.stat(gitignorePath)
		const cached = gitignoreCache.get(gitignorePath)
		if (cached && cached.mtimeMs === stat.mtimeMs) {
			return cached.filter
		}
		const content = await fs.readFile(gitignorePath, "utf8")
		const rules = content
			.split(/\r?\n/)
			.map((l) => l.trim())
			.filter((l) => l && !l.startsWith("#"))
		// 简单实现：只支持前缀匹配和目录匹配
		const matchers = rules.map((rule) => {
			if (rule.endsWith("/")) {
				// 目录
				const dirRule = rule.replace(/\/$/, "")
				return (p: string, isDir: boolean) => isDir && p.includes(dirRule)
			} else {
				return (p: string, isDir: boolean) => p.includes(rule)
			}
		})
		const filter = (p: string, isDir: boolean) => !matchers.some((fn) => fn(p, isDir))
		gitignoreCache.set(gitignorePath, { mtimeMs: stat.mtimeMs, rules, filter })
		return filter
	} catch {
		// 没有.gitignore
		return () => true
	}
}

/**
 * 受限tree命令实现
 * @param dirPathRoot 根目录
 * @param filePaths 需要展开的文件路径（绝对或相对dirPathRoot）
 * @param limitNum 每层最多显示多少项
 * @param maxDepth 最大递归深度
 * @param options 额外选项
 * @returns tree结构字符串
 */
export async function treeView(
	dirPathRoot: string,
	filePaths: string[],
	limitNum: number,
	maxDepth: number,
	options: TreeViewOptions = {},
): Promise<string> {
	const { showDotDirs = false, allowOtherExtensions = false } = options
	// 1. 归一化filePaths为绝对路径
	const absRoot = path.resolve(dirPathRoot)
	const normFilePaths = filePaths.map((fp) => path.resolve(dirPathRoot, fp))
	// 2. 需要重点展开的所有目录集合（filePaths的所有父目录）
	const expandDirs = new Set<string>()
	// 3. 需要显示所有同级文件/目录的目录集合（filePaths的父目录）
	const siblingDirs = new Set<string>()
	// 4. 记录每个filePath的后缀名集合
	const fileExts = new Map<string, Set<string>>()
	for (const fileAbs of normFilePaths) {
		let cur = path.dirname(fileAbs)
		siblingDirs.add(cur) // filePaths的父目录
		if (!fileExts.has(cur)) fileExts.set(cur, new Set())
		fileExts.get(cur)!.add(path.extname(fileAbs))
		while (cur.startsWith(absRoot)) {
			expandDirs.add(cur)
			if (cur === absRoot) break
			cur = path.dirname(cur)
		}
	}

	// 获取.gitignore过滤器
	const gitignoreFilter = await getGitignoreFilter(absRoot)

	// 递归遍历，生成tree风格符号
	async function walk(dir: string, depth: number, prefix: string, isLast: boolean): Promise<string[]> {
		if (depth > maxDepth) {
			const entries = (await fs.readdir(dir, { withFileTypes: true })) as Dirent[]
			return [prefix + ellipsisLine(entries.length, "(超出深度)")]
		}
		let entries: Dirent[] = []
		try {
			entries = (await fs.readdir(dir, { withFileTypes: true })) as Dirent[]
		} catch {
			return [prefix + "[无法读取目录]"]
		}
		// 过滤.开头的目录（除非filePaths的某个文件在该目录下）
		const isSiblingDir = siblingDirs.has(dir)
		if (!showDotDirs && !isSiblingDir) {
			entries = entries.filter((e) => !e.name.startsWith("."))
		}
		// 过滤.gitignore
		entries = entries.filter((e) =>
			gitignoreFilter(path.relative(absRoot, path.join(dir, e.name)), e.isDirectory()),
		)
		// 排序：
		// 1. 需要展开的目录（expandDirs）优先文件，再目录，再名称
		// 2. 其它目录优先目录，再文件，再名称
		entries.sort((a, b) => {
			const aPath = path.join(dir, a.name)
			const bPath = path.join(dir, b.name)
			const aExpand = expandDirs.has(dir)
			const bExpand = expandDirs.has(dir)
			if (aExpand && bExpand) {
				// 需要展开的目录，文件优先
				if (a.isDirectory() !== b.isDirectory()) return Number(a.isDirectory()) - Number(b.isDirectory())
				return a.name.localeCompare(b.name)
			} else {
				// 其它目录，目录优先
				if (a.isDirectory() !== b.isDirectory()) return Number(b.isDirectory()) - Number(a.isDirectory())
				return a.name.localeCompare(b.name)
			}
		})
		const lines: string[] = []
		let shown = 0
		let omitted = 0
		// 判断当前目录是否为filePaths的父目录（需要显示所有同级文件/目录）
		const showAllSiblings = isSiblingDir
		// 获取当前目录需要的后缀名集合（如果有）
		const requiredExts = fileExts.get(dir)

		if (showAllSiblings) {
			// 先分组文件和目录，优先显示目标文件
			const siblingFiles = entries.filter(
				(e) =>
					!e.isDirectory() &&
					(allowOtherExtensions || !requiredExts || requiredExts.has(path.extname(e.name))),
			)
			const siblingDirs = entries.filter((e) => e.isDirectory())
			// 文件优先
			for (let i = 0; i < siblingFiles.length && shown < limitNum; i++, shown++) {
				const entry = siblingFiles[i]
				const isEntryLast =
					shown === limitNum - 1 ||
					(i === siblingFiles.length - 1 && shown + siblingDirs.length >= limitNum) ||
					shown === siblingFiles.length + siblingDirs.length - 1
				const branch = isEntryLast ? "└── " : "├── "
				lines.push(prefix + branch + entry.name)
			}
			// 目录
			for (let i = 0; i < siblingDirs.length && shown < limitNum; i++, shown++) {
				const entry = siblingDirs[i]
				const entryPath = path.join(dir, entry.name)
				const isEntryLast =
					shown === limitNum - 1 ||
					i === siblingDirs.length - 1 ||
					shown === siblingFiles.length + siblingDirs.length - 1
				const branch = isEntryLast ? "└── " : "├── "
				const nextPrefix = prefix + (isEntryLast ? "    " : "│   ")
				if (expandDirs.has(entryPath)) {
					lines.push(prefix + branch + entry.name + "/")
					const sub = await walk(entryPath, depth + 1, nextPrefix, isEntryLast)
					lines.push(...sub)
				} else {
					const subEntries = (await fs.readdir(entryPath, { withFileTypes: true })) as Dirent[]
					// 过滤.gitignore
					const filteredSubEntries = subEntries.filter((e) =>
						gitignoreFilter(path.relative(absRoot, path.join(entryPath, e.name)), e.isDirectory()),
					)
					lines.push(prefix + branch + entry.name + `/ ...(${filteredSubEntries.length}项)`)
				}
			}
			const total = siblingFiles.length + siblingDirs.length
			if (shown < total) {
				lines.push(prefix + ellipsisLine(total - shown))
			}
			return lines
		}
		for (let i = 0; i < entries.length; i++) {
			const entry = entries[i]
			const entryPath = path.join(dir, entry.name)
			const isDir = entry.isDirectory()
			const isEntryLast = shown === limitNum - 1 || i === entries.length - 1 || shown === entries.length - 1
			if (shown >= limitNum) {
				omitted = entries.length - shown
				lines.push(prefix + ellipsisLine(omitted))
				break
			}
			const branch = isEntryLast ? "└── " : "├── "
			const nextPrefix = prefix + (isEntryLast ? "    " : "│   ")
			if (isDir) {
				if (expandDirs.has(entryPath)) {
					lines.push(prefix + branch + entry.name + "/")
					const sub = await walk(entryPath, depth + 1, nextPrefix, isEntryLast)
					lines.push(...sub)
				} else {
					const subEntries = (await fs.readdir(entryPath, { withFileTypes: true })) as Dirent[]
					// 过滤.gitignore
					const filteredSubEntries = subEntries.filter((e) =>
						gitignoreFilter(path.relative(absRoot, path.join(entryPath, e.name)), e.isDirectory()),
					)
					lines.push(prefix + branch + entry.name + `/ ...(${filteredSubEntries.length}项)`)
				}
			} else {
				// 如果是filePaths的父目录，显示所有同级文件，否则只显示filePaths本身
				if (showAllSiblings) {
					// 只显示相同后缀，除非allowOtherExtensions为true
					if (allowOtherExtensions || !requiredExts || requiredExts.has(path.extname(entry.name))) {
						lines.push(prefix + branch + entry.name)
					}
				} else if (normFilePaths.includes(entryPath)) {
					lines.push(prefix + branch + entry.name)
				}
			}
			shown++
		}
		return lines
	}

	function ellipsisLine(count: number, note?: string) {
		return `...(${count}项${note ? note : ""})`
	}

	// 根目录名
	const rootName = path.basename(absRoot) || absRoot
	const lines = await walk(absRoot, 1, "", true)
	return [rootName + "/", ...lines].join("\n")
}
