import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

export async function extractSnippetsFromCapturesForTypeScript(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[TS extract]")
	const snippets: ISnippetMeta[] = []
	const seen = new Set<string>()
	if (snippetTypes.length === 0) return snippets

	// --- imports ---
	for (const c of captures.filter((c) => c.name === "name.import")) {
		const node = c.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1
		const text = sourceCode.slice(node.startIndex, node.endIndex)
		const hash = generateSnippetHash(filePath, startLine, endLine, text)
		if (seen.has(hash)) continue
		seen.add(hash)
		snippets.push({
			name: text,
			type: SnippetType.ImportOrInclude,
			filePath,
			startLine,
			endLine,
			rangeText: text,
			definitionText: text,
			scope: buildScopeChain(node),
			fileHash,
		})
	}

	// --- functions & methods ---
	for (const def of captures.filter((c) => c.name === "definition.function" || c.name === "definition.method")) {
		const node = def.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 名称
		const nameCap = captures.find(
			(c) =>
				c.name === (def.name === "definition.method" ? "name.method" : "name.function") &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.slice(nameCap.node.startIndex, nameCap.node.endIndex)

		// 参数列表
		const paramsCap = captures.find(
			(c) =>
				c.name === "parameters.function" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		const parameters: ParameterInfo[] = []
		if (paramsCap) {
			const raw = sourceCode.slice(paramsCap.node.startIndex, paramsCap.node.endIndex)
			raw.replace(/^\(|\)$/g, "")
				.split(",")
				.map((p) => p.trim())
				.filter((p) => p)
				.forEach((p) => parameters.push({ name: p }))
		}

		// 是否是类方法
		const isMethod = def.name === "definition.method"
		// 提取方法所属 class
		let field: string | undefined
		if (isMethod) {
			// 在TypeScript中，method在class内部，所以查找包含这个method的class
			// 需要找到包含当前method节点的class declaration
			const classDefCap = captures.find(
				(c) =>
					c.name === "definition.class" &&
					c.node.startIndex < node.startIndex &&
					c.node.endIndex > node.endIndex,
			)
			if (classDefCap) {
				const classCap = captures.find(
					(c) =>
						c.name === "name.class" &&
						c.node.startIndex >= classDefCap.node.startIndex &&
						c.node.endIndex <= classDefCap.node.endIndex,
				)
				if (classCap) {
					field = sourceCode.slice(classCap.node.startIndex, classCap.node.endIndex)
				}
			}
		}

		// 签名、hash
		const sig = `${name}(${parameters.map((p) => p.name).join(", ")})`
		const hash = generateSnippetHash(filePath, startLine, endLine, sig)
		if (seen.has(hash)) continue
		seen.add(hash)

		snippets.push({
			name,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText: sourceCode.slice(node.startIndex, node.endIndex),
			definitionText: sig,
			scope: buildScopeChain(node),
			fileHash,
			field,
			parameters,
			signature: sig,
			definition: {
				name,
				type: def.name === "definition.method" ? "method" : "function",
				parameters,
				returnType: "",
			},
			language: CodeLanguageType.TypeScript,
			implementText: sourceCode.slice(node.startIndex, node.endIndex),
		})
	}

	// --- classes / interfaces / enums / types ---
	const typeDefs = [
		{
			name: "name.class",
			def: "definition.class",
			kind: "class",
			type: SnippetType.ClassOrInterfaceOrStructOrEnum,
		},
		{
			name: "name.interface",
			def: "definition.interface",
			kind: "interface",
			type: SnippetType.ClassOrInterfaceOrStructOrEnum,
		},
		{ name: "name.enum", def: "definition.enum", kind: "enum", type: SnippetType.ClassOrInterfaceOrStructOrEnum },
		{
			name: "name.type_alias",
			def: "definition.type_alias",
			kind: "type",
			type: SnippetType.ClassOrInterfaceOrStructOrEnum,
		},
	]
	for (const { name: capName, def: defName, kind, type } of typeDefs) {
		for (const c of captures.filter((c) => c.name === capName)) {
			const node = c.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const text = sourceCode.slice(node.startIndex, node.endIndex)

			// 判断是否在函数/方法内
			const inFunc = captures.some(
				(fc) =>
					(fc.name === "definition.function" || fc.name === "definition.method") &&
					fc.node.startIndex < node.startIndex &&
					node.endIndex < fc.node.endIndex,
			)
			if (inFunc) continue

			// 优先使用definition区间
			const defCap = captures.find(
				(dc) =>
					dc.name === defName && dc.node.startIndex <= node.startIndex && node.endIndex <= dc.node.endIndex,
			)
			const defNode = defCap ? defCap.node : node.parent || node

			const hash = generateSnippetHash(filePath, startLine, endLine, text)
			if (seen.has(hash)) continue
			seen.add(hash)

			snippets.push({
				name: text,
				type,
				filePath,
				startLine,
				endLine,
				rangeText: sourceCode.slice(defNode.startIndex, defNode.endIndex),
				definitionText: text,
				implementText: sourceCode.slice(defNode.startIndex, defNode.endIndex),
				scope: buildScopeChain(node),
				fileHash,
				definition: { name: text, type: kind },
				language: CodeLanguageType.TypeScript,
				field: "",
			})
		}
	}

	// --- 变量 / 常量 ---
	for (const c of captures.filter((c) => c.name === "name.variable")) {
		const node = c.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1
		const name = sourceCode.slice(node.startIndex, node.endIndex)

		// 跳过函数内部局部变量
		const inFunc = captures.some(
			(fc) =>
				(fc.name === "definition.function" || fc.name === "definition.method") &&
				fc.node.startIndex < node.startIndex &&
				node.endIndex < fc.node.endIndex,
		)
		if (inFunc) continue

		const hash = generateSnippetHash(filePath, startLine, endLine, name)
		if (seen.has(hash)) continue
		seen.add(hash)

		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText: sourceCode.slice(node.startIndex, node.endIndex),
			definitionText: name,
			implementText: name,
			scope: buildScopeChain(node),
			fileHash,
			definition: { name, type: "variable" },
			language: CodeLanguageType.TypeScript,
			field: "",
		})
	}

	// --- properties ---
	for (const c of captures.filter((c) => c.name === "name.property")) {
		const node = c.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1
		const name = sourceCode.slice(node.startIndex, node.endIndex)

		// 查找所属的interface或class
		let field: string | undefined
		const parentInterface = captures.find(
			(ic) =>
				ic.name === "definition.interface" &&
				ic.node.startIndex < node.startIndex &&
				ic.node.endIndex > node.endIndex,
		)
		const parentClass = captures.find(
			(cc) =>
				cc.name === "definition.class" &&
				cc.node.startIndex < node.startIndex &&
				cc.node.endIndex > node.endIndex,
		)

		if (parentInterface) {
			const interfaceCap = captures.find(
				(ic) =>
					ic.name === "name.interface" &&
					ic.node.startIndex >= parentInterface.node.startIndex &&
					ic.node.endIndex <= parentInterface.node.endIndex,
			)
			if (interfaceCap) {
				field = sourceCode.slice(interfaceCap.node.startIndex, interfaceCap.node.endIndex)
			}
		} else if (parentClass) {
			const classCap = captures.find(
				(cc) =>
					cc.name === "name.class" &&
					cc.node.startIndex >= parentClass.node.startIndex &&
					cc.node.endIndex <= parentClass.node.endIndex,
			)
			if (classCap) {
				field = sourceCode.slice(classCap.node.startIndex, classCap.node.endIndex)
			}
		}

		const hash = generateSnippetHash(filePath, startLine, endLine, name)
		if (seen.has(hash)) continue
		seen.add(hash)

		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText: sourceCode.slice(node.startIndex, node.endIndex),
			definitionText: name,
			implementText: name,
			scope: buildScopeChain(node),
			fileHash,
			field,
			definition: { name, type: "property" },
			language: CodeLanguageType.TypeScript,
		})
	}

	return snippets
}

export interface OutlineEntry {
	type: string
	name: string
	file: string
	field?: string
	description?: string
}

export function buildSummaryFromSnippets(snips: ISnippetMeta[]): OutlineEntry[] {
	const outline: OutlineEntry[] = []
	for (const s of snips) {
		if (s.type === SnippetType.ImportOrInclude) {
			outline.push({ type: "import", name: s.name, file: s.filePath, field: "", description: s.definitionText })
		} else if (s.type === SnippetType.FunctionOrMethod) {
			outline.push({
				type: s.field ? "method" : "function",
				name: s.name,
				file: s.filePath,
				field: s.field || "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.ClassOrInterfaceOrStructOrEnum) {
			outline.push({
				type: s.definition?.type || "type",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.name,
			})
		} else if (s.type === SnippetType.VariableOrConstant) {
			outline.push({ type: "variable", name: s.name, file: s.filePath, field: "", description: s.definitionText })
		}
	}
	return outline
}

export function formatOutlineText(outline: OutlineEntry[]): string {
	const groups: Record<string, OutlineEntry[]> = {}
	for (const e of outline) {
		const key = e.field || ""
		;(groups[key] ||= []).push(e)
	}
	let out = ""
	for (const field of Object.keys(groups).sort()) {
		out += (field ? `[${field}]` : "[global]") + "\n"
		for (const e of groups[field]) {
			out += `  ${e.type}: ${e.description}\n`
		}
		out += "\n"
	}
	return out.trimEnd()
}
