import { SnippetType, ISnippetMeta } from "../types"
import Parser from "web-tree-sitter"
import path from "path"
import crypto from "crypto"
import { Logger } from "../Logger"

export function isFunctionNode(node: Parser.SyntaxNode): boolean {
	const functionTypes = [
		"function_declaration",
		"function_definition", // Go
		"method_declaration", // Go methods
		"method_definition", // Class methods
		"arrow_function",
		"function_expression",
	]
	return functionTypes.includes(node.type)
}

export function isClassNode(node: Parser.SyntaxNode): boolean {
	const classTypes = [
		"class_declaration",
		"class_definition",
		"interface_declaration",
		"struct_specifier", // C++ structs
	]
	return classTypes.includes(node.type)
}

export function findIdentifierInNode(node: Parser.SyntaxNode): Parser.SyntaxNode | null {
	const identifierTypes = new Set([
		"identifier",
		"name",
		"property_identifier",
		"field_identifier",
		"method_name",
		"function_name",
		"class_name",
		"type_identifier",
	])

	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child && identifierTypes.has(child.type)) {
			return child
		}
	}

	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child) {
			const found = findIdentifierInNode(child)
			if (found) return found
		}
	}

	return null
}

export function isScopeNode(node: Parser.SyntaxNode): boolean {
	const scopeTypes = new Set([
		"function_declaration",
		"method_definition",
		"arrow_function",
		"function_expression",
		"class_declaration",
		"interface_declaration",
		"namespace_declaration",
		"module_declaration",
		"function_definition",
		"class_definition",
	])
	return scopeTypes.has(node.type)
}

export function extractScopeName(node: Parser.SyntaxNode): string | null {
	const identifierNode = findIdentifierInNode(node)
	if (identifierNode) {
		return node.text.substring(
			identifierNode.startIndex - node.startIndex,
			identifierNode.endIndex - node.startIndex,
		)
	}
	return node.type
}

export function generateSnippetHash(filePath: string, startLine: number, endLine: number, rangeText: string): string {
	const content = `${filePath}:${startLine}-${endLine}:${rangeText.substring(0, 100)}`
	return crypto.createHash("sha256").update(content).digest("hex").substring(0, 16)
}

export function extractSymbolName(node: Parser.SyntaxNode, sourceCode: string): string {
	const identifierNode = findIdentifierInNode(node)
	if (identifierNode) {
		return sourceCode.substring(identifierNode.startIndex, identifierNode.endIndex)
	}
	return `${node.type}_${node.startPosition.row + 1}`
}

export function buildScopeChain(node: Parser.SyntaxNode): string[] {
	const scope: string[] = []
	let current = node.parent

	while (current) {
		if (isScopeNode(current)) {
			const scopeName = extractScopeName(current)
			if (scopeName) {
				scope.unshift(scopeName)
			}
		}
		current = current.parent
	}

	return scope
}

/**
 * 根据 Query 捕获确定 snippet 类型
 */
export function determineSnippetTypeFromCapture(
	capture: Parser.QueryCapture,
	allowedTypes: SnippetType[],
): SnippetType {
	// 根据 capture 名称映射类型
	const captureTypeMap: Record<string, SnippetType> = {
		import: SnippetType.ImportOrInclude,
		"import.statement": SnippetType.ImportOrInclude,
		"function.definition": SnippetType.FunctionOrMethod,
		"class.definition": SnippetType.ClassOrInterfaceOrStructOrEnum,
		"method.definition": SnippetType.FunctionOrMethod,
		"variable.definition": SnippetType.VariableOrConstant,
	}

	const mappedType = captureTypeMap[capture.name]
	if (mappedType && allowedTypes.includes(mappedType)) {
		return mappedType
	}

	// 根据节点类型判断
	if (capture.node.type.includes("import") && allowedTypes.includes(SnippetType.ImportOrInclude)) {
		return SnippetType.ImportOrInclude
	}

	if (isFunctionNode(capture.node) && allowedTypes.includes(SnippetType.FunctionOrMethod)) {
		return SnippetType.FunctionOrMethod
	}

	if (isClassNode(capture.node) && allowedTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		return SnippetType.ClassOrInterfaceOrStructOrEnum
	}

	// 默认使用第一个允许的类型
	return allowedTypes[0]
}

export function createSnippetFromQueryCapture(
	capture: Parser.QueryCapture,
	allCaptures: Parser.QueryCapture[],
	sourceCode: string,
	lines: string[],
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
	seenHashes: Set<string>,
): ISnippetMeta | null {
	const node = capture.node
	const startLine = node.startPosition.row + 1
	const endLine = node.endPosition.row + 1
	const startColumn = node.startPosition.column
	const endColumn = node.endPosition.column
	const lineCount = endLine - startLine + 1

	if (lineCount < Math.max(1, options.minSnippetLines - 2) || lineCount > options.maxSnippetLines * 2) {
		return null
	}

	const rangeText = sourceCode.substring(node.startIndex, node.endIndex)

	const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
	if (seenHashes.has(snippetHash)) {
		return null
	}
	seenHashes.add(snippetHash)

	const name = extractSymbolName(node, sourceCode)
	const scope = buildScopeChain(node)

	// 🔧 根据 capture 名称和节点类型确定 snippet 类型
	const snippetType = determineSnippetTypeFromCapture(capture, snippetTypes)

	return {
		name,
		type: snippetType,
		filePath,
		startLine,
		endLine,
		startColumn,
		endColumn,
		rangeText,
		scope,
		fileHash,
	}
}

export async function extractSnippetsFromCapturesForGeneral(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForGeneral]")
	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()
	const lines = sourceCode.split("\n")

	try {
		logger.info(`[CodeContext] Starting query execution for: ${path.basename(filePath)}`)

		// const captures = query.captures(tree.rootNode)
		logger.info(`[CodeContext] Query found ${captures.length} captures`)

		// 按捕获类型分组
		const capturesByType = new Map<string, Parser.QueryCapture[]>()
		captures.forEach((capture) => {
			const captureType = capture.name
			if (!capturesByType.has(captureType)) {
				capturesByType.set(captureType, [])
			}
			capturesByType.get(captureType)!.push(capture)
		})

		logger.info(`[CodeContext] Capture types found:`, Array.from(capturesByType.keys()))

		// 处理所有找到的捕获
		for (const [captureType, captureList] of capturesByType) {
			logger.info(`[CodeContext] Processing ${captureList.length} ${captureType} captures`)

			for (const capture of captureList) {
				const snippet = createSnippetFromQueryCapture(
					capture,
					captures,
					sourceCode,
					lines,
					filePath,
					fileHash,
					snippetTypes,
					options,
					seenHashes,
				)

				if (snippet) {
					snippets.push(snippet)
					logger.info(
						`"[CodeContext] Created snippet: ${snippet.name} (${snippet.startLine}-${snippet.endLine}) from ${captureType}`,
					)
				}
			}
		}

		logger.info(`[CodeContext] Total snippets extracted: ${snippets.length}`)
	} catch (error) {
		logger.error(`[CodeContext] Error executing query:`, error)
	}

	return snippets
}
