import { ILogger, LogLevel } from "./types"

const logLevelPriorityMap = {
	[LogLevel.Debug]: 0,
	[LogLevel.Info]: 1,
	[LogLevel.Warn]: 2,
	[LogLevel.Error]: 3,
}

export class Logger implements ILogger {
	private static _instance: Logger | null = null
	prefix: string = ""
	level: LogLevel = LogLevel.Warn

	constructor(prefix?: string, level?: LogLevel) {
		if (prefix) {
			this.prefix = prefix
		}
		if (level) {
			this.level = level
		}
	}

	static getDefaultLogger(): ILogger {
		if (!Logger._instance) {
			Logger._instance = new Logger()
		}
		return Logger._instance
	}

	static setDefaultLogger(logger: ILogger) {
		Logger._instance = logger
	}

	debug(message?: any, ...optionalParams: any[]): void {
		if (logLevelPriorityMap[this.level] <= logLevelPriorityMap[LogLevel.Debug]) {
			console.debug(`${this.prefix}${message} ${optionalParams.join(" ")}`)
		}
	}
	info(message?: any, ...optionalParams: any[]): void {
		if (logLevelPriorityMap[this.level] <= logLevelPriorityMap[LogLevel.Info]) {
			console.log(`${this.prefix}${message} ${optionalParams.join(" ")}`)
		}
	}
	warn(message?: any, ...optionalParams: any[]): void {
		if (logLevelPriorityMap[this.level] <= logLevelPriorityMap[LogLevel.Warn]) {
			console.warn(`${this.prefix}${message} ${optionalParams.join(" ")}`)
		}
	}
	error(message?: any, ...optionalParams: any[]): void {
		if (logLevelPriorityMap[this.level] <= logLevelPriorityMap[LogLevel.Error]) {
			console.error(`${this.prefix}${message} ${optionalParams.join(" ")}`)
		}
	}
	with(prefix: string): ILogger {
		return new Logger(this.prefix + prefix + " ")
	}
}
