/*
- class definitions
- method definitions (including decorated methods)
- named function declarations
- arrow functions and function expressions assigned to variables
- variable declarations (global and class member variables)
- JSON object and array definitions (for JSON files)
- decorators and decorated elements
*/
export default `
(
  (comment)* @doc
  .
  (method_definition
    name: (property_identifier) @name
    parameters: (formal_parameters) @parameters.definition.method) @definition.method
  (#not-eq? @name "constructor")
  (#strip! @doc "^[\\s\\*/]+|^[\\s\\*/]$")
  (#select-adjacent! @doc @definition.method)
)

(
  (comment)* @doc
  .
  [
    (class
      name: (_) @name.definition.class)
    (class_declaration
      name: (_) @name.definition.class)
  ] @definition.class
  (#strip! @doc "^[\\s\\*/]+|^[\\s\\*/]$")
  (#select-adjacent! @doc @definition.class)
)

(
  (comment)* @doc
  .
  [
    (function_declaration
      name: (identifier) @name.definition.function
      parameters: (formal_parameters) @parameters.definition.function
      body: (statement_block) @body.definition.function) @definition.function
    (generator_function_declaration
      name: (identifier) @name.definition.function
      parameters: (formal_parameters) @parameters.definition.function) @definition.function
  ]
  (#strip! @doc "^[\\s\\*/]+|^[\\s\\*/]$")
  (#select-adjacent! @doc @definition.function)
)

(
  (comment)* @doc
  .
  (lexical_declaration
    (variable_declarator
      name: (identifier) @name
      value: [
        (arrow_function
          parameters: (formal_parameters) @parameters.definition.function)
        (function_expression
          parameters: (formal_parameters) @parameters.definition.function)
      ]) @definition.function)
  (#strip! @doc "^[\\s\\*/]+|^[\\s\\*/]$")
  (#select-adjacent! @doc @definition.function)
)

(
  (comment)* @doc
  .
  (variable_declaration
    (variable_declarator
      name: (identifier) @name
      value: [
        (arrow_function
          parameters: (formal_parameters) @parameters.definition.function)
        (function_expression
          parameters: (formal_parameters) @parameters.definition.function)
      ]) @definition.function)
  (#strip! @doc "^[\\s\\*/]+|^[\\s\\*/]$")
  (#select-adjacent! @doc @definition.function)
)

; Variable declarations (let, const, var) - capture each declarator separately
(variable_declarator
  name: (identifier) @name.definition.variable) @definition.variable

; Also capture from parent declarations for context
(variable_declaration
  (variable_declarator
    name: (identifier) @name.definition.variable)) @definition.variable

(lexical_declaration
  (variable_declarator
    name: (identifier) @name.definition.variable)) @definition.variable

; Destructuring assignments - capture all identifiers in destructuring patterns
(shorthand_property_identifier_pattern) @name.definition.variable @definition.variable

; Array destructuring - capture the whole pattern for manual processing
(lexical_declaration
  (variable_declarator
    name: (array_pattern) @array_pattern)) @definition.variable

(variable_declaration
  (variable_declarator
    name: (array_pattern) @array_pattern)) @definition.variable

; Class field declarations
(field_definition
  property: (property_identifier) @name.definition.member_variable) @definition.variable

; Assignment expressions for member variables (this.property = value)
(assignment_expression
  left: (member_expression
    object: (this)
    property: (property_identifier) @name.definition.member_variable)) @definition.variable

; Object property assignments
(assignment_expression
  left: (member_expression
    property: (property_identifier) @name.definition.member_variable)) @definition.variable

; JSON object definitions
(object) @object.definition

; JSON object key-value pairs
(pair
  key: (string) @property.name.definition
  value: [
    (object) @object.value
    (array) @array.value
    (string) @string.value
    (number) @number.value
    (true) @boolean.value
    (false) @boolean.value
    (null) @null.value
  ]
) @property.definition

; JSON array definitions
(array) @array.definition

; Decorated method definitions
(
  [
    (method_definition
      decorator: (decorator)
      name: (property_identifier) @name
      parameters: (formal_parameters) @parameters.definition.method) @definition.method
    (method_definition
      decorator: (decorator
        (call_expression
          function: (identifier) @decorator_name))
      name: (property_identifier) @name
      parameters: (formal_parameters) @parameters.definition.method) @definition.method
  ]
  (#not-eq? @name "constructor")
)

; Decorated class definitions
(
  [
    (class
      decorator: (decorator)
      name: (_) @name.definition.class) @definition.class
    (class_declaration
      decorator: (decorator)
      name: (_) @name.definition.class) @definition.class
  ]
)

; Capture method names in decorated classes
(
  (class_declaration
    decorator: (decorator)
    body: (class_body
      (method_definition
        name: (property_identifier) @name
        parameters: (formal_parameters) @parameters.definition.method) @definition.method))
  (#not-eq? @name "constructor")
)
`
