import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

export async function extractSnippetsFromCapturesForRust(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForRust]")

	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 收集所有 struct/enum/trait/impl captures 用于确定 field
	const containerCaptures = captures
		.filter(
			(c) =>
				c.name === "definition.struct" ||
				c.name === "definition.enum" ||
				c.name === "definition.trait" ||
				c.name === "definition.impl" ||
				c.name === "definition.impl_trait",
		)
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			name: c.name,
			nameCap: findNameCaptureForContainer(c, captures, sourceCode),
		}))
		.filter((c) => c.nameCap) // 只保留有名称的containers

	// 处理 use declarations (imports)
	if (snippetTypes.includes(SnippetType.ImportOrInclude)) {
		captures.forEach((capture) => {
			if (capture.name === "definition.use_declaration") {
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1
				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
				const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

				if (seenHashes.has(snippetHash)) return
				seenHashes.add(snippetHash)

				// 从 use 语句中提取导入的名称
				const name = extractUseStatementName(rangeText)
				const scope = buildScopeChain(node)

				snippets.push({
					name,
					type: SnippetType.ImportOrInclude,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText: rangeText,
					scope,
					fileHash,
				})
			}
		})
	}

	// 处理 functions 和 methods
	if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
		for (const capture of captures) {
			if (capture.name !== "definition.function") continue
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1

			// 查找函数名
			const nameCap = captures.find(
				(c) =>
					c.name === "name.definition.function" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
			if (!nameCap) continue
			const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

			// 查找参数
			const paramCap = captures.find(
				(c) =>
					c.name === "parameters.definition.function" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			const parameters: ParameterInfo[] = []
			if (paramCap) {
				const paramText = sourceCode.substring(paramCap.node.startIndex, paramCap.node.endIndex)
				const cleanParams = removeParentheses(paramText).trim()
				if (cleanParams) {
					const paramList = parseRustParameters(cleanParams)
					parameters.push(...paramList.map((param) => ({ name: param })))
				}
			}

			// 查找函数体
			const bodyCap = captures.find(
				(c) =>
					c.name === "body.definition.function" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			// 生成函数签名
			let definitionText: string
			let implementText: string | undefined = undefined

			if (bodyCap) {
				// 从函数开始到函数体开始（不包括函数体）
				const functionHeader = sourceCode.substring(node.startIndex, bodyCap.node.startIndex).trimEnd()
				definitionText = functionHeader.endsWith("{") ? functionHeader.slice(0, -1).trim() : functionHeader
				implementText = sourceCode.substring(node.startIndex, node.endIndex)
			} else {
				// 如果没有函数体，使用整个节点内容
				definitionText = sourceCode.substring(node.startIndex, node.endIndex)
			}

			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)

			// 确定是否属于某个容器（设置field）
			let field: string | undefined = undefined
			// 优先匹配 impl_trait，然后才是其他容器
			const sortedContainers = containerCaptures.sort((a, b) => {
				if (a.name === "definition.impl_trait" && b.name !== "definition.impl_trait") return -1
				if (a.name !== "definition.impl_trait" && b.name === "definition.impl_trait") return 1
				return 0
			})
			for (const container of sortedContainers) {
				if (node.startIndex > container.start && node.endIndex <= container.end && container.nameCap) {
					field = container.nameCap
					break
				}
			}

			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)

			const scope = buildScopeChain(node)
			snippets.push({
				name,
				type: SnippetType.FunctionOrMethod,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText,
				implementText,
				parameters,
				field,
				scope,
				fileHash,
			})
		}

		// 处理 methods (在 impl 块中的函数)
		for (const capture of captures) {
			if (capture.name !== "name.definition.method") continue
			const methodNode = capture.node
			const name = sourceCode.substring(methodNode.startIndex, methodNode.endIndex)

			// 找到包含这个 method 的 impl 块
			const implCapture = captures.find(
				(c) =>
					(c.name === "definition.impl" || c.name === "definition.impl_trait") &&
					c.node.startIndex <= methodNode.startIndex &&
					c.node.endIndex >= methodNode.endIndex,
			)

			if (!implCapture) continue

			// 找到对应的函数节点
			const functionNode = findParentFunctionNode(methodNode, sourceCode)
			if (!functionNode) continue

			const startLine = functionNode.startPosition.row + 1
			const endLine = functionNode.endPosition.row + 1

			// 查找参数
			const paramCap = captures.find(
				(c) =>
					c.name === "parameters.definition.method" &&
					c.node.startIndex >= functionNode.startIndex &&
					c.node.endIndex <= functionNode.endIndex,
			)

			const parameters: ParameterInfo[] = []
			if (paramCap) {
				const paramText = sourceCode.substring(paramCap.node.startIndex, paramCap.node.endIndex)
				const cleanParams = removeParentheses(paramText).trim()
				if (cleanParams) {
					const paramList = parseRustParameters(cleanParams)
					parameters.push(...paramList.map((param) => ({ name: param })))
				}
			}

			// 查找方法体
			const bodyCap = captures.find(
				(c) =>
					c.name === "body.definition.method" &&
					c.node.startIndex >= functionNode.startIndex &&
					c.node.endIndex <= functionNode.endIndex,
			)

			// 生成方法签名
			let definitionText: string
			let implementText: string | undefined = undefined

			if (bodyCap) {
				const methodHeader = sourceCode.substring(functionNode.startIndex, bodyCap.node.startIndex).trimEnd()
				definitionText = methodHeader.endsWith("{") ? methodHeader.slice(0, -1).trim() : methodHeader
				implementText = sourceCode.substring(functionNode.startIndex, functionNode.endIndex)
			} else {
				definitionText = sourceCode.substring(functionNode.startIndex, functionNode.endIndex)
			}

			const rangeText = sourceCode.substring(functionNode.startIndex, functionNode.endIndex)

			// 确定所属的结构体/特征
			let field: string | undefined = undefined
			// 优先匹配 impl_trait，然后才是其他容器
			const sortedContainers = containerCaptures.sort((a, b) => {
				if (a.name === "definition.impl_trait" && b.name !== "definition.impl_trait") return -1
				if (a.name !== "definition.impl_trait" && b.name === "definition.impl_trait") return 1
				return 0
			})
			for (const container of sortedContainers) {
				if (
					functionNode.startIndex > container.start &&
					functionNode.endIndex <= container.end &&
					container.nameCap
				) {
					field = container.nameCap
					break
				}
			}

			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)

			const scope = buildScopeChain(functionNode)
			snippets.push({
				name,
				type: SnippetType.FunctionOrMethod,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText,
				implementText,
				parameters,
				field,
				scope,
				fileHash,
			})
		}
	}

	// 处理 structs, enums, traits, impls
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const structureTypes = [
			"definition.struct",
			"definition.enum",
			"definition.trait",
			"definition.impl",
			"definition.impl_trait",
		]

		// 处理 structs, enums, traits, impls - 避免重复处理
		const processedNodes = new Set<string>()

		for (const capture of captures) {
			if (!structureTypes.includes(capture.name)) continue
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1

			// 避免重复处理同一个节点
			const nodeKey = `${node.startIndex}:${node.endIndex}`
			if (processedNodes.has(nodeKey)) continue

			// 对于impl块，优先处理impl_trait而不是impl
			if (capture.name === "definition.impl") {
				const hasImplTrait = captures.some(
					(c) =>
						c.name === "definition.impl_trait" &&
						c.node.startIndex === node.startIndex &&
						c.node.endIndex === node.endIndex,
				)
				if (hasImplTrait) continue // 跳过，让impl_trait处理
			}

			processedNodes.add(nodeKey)

			let nameCap: Parser.QueryCapture | undefined
			let name: string = ""

			// 根据不同类型查找名称
			switch (capture.name) {
				case "definition.struct":
					nameCap = captures.find(
						(c) =>
							c.name === "name.definition.struct" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)
					if (nameCap) {
						name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)
					}
					break
				case "definition.enum":
					nameCap = captures.find(
						(c) =>
							c.name === "name.definition.enum" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)
					if (nameCap) {
						name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)
					}
					break
				case "definition.trait":
					nameCap = captures.find(
						(c) =>
							c.name === "name.definition.trait" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)
					if (nameCap) {
						name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)
					}
					break
				case "definition.impl":
					nameCap = captures.find(
						(c) =>
							c.name === "name.definition.impl" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)
					if (nameCap) {
						name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)
					}
					break
				case "definition.impl_trait":
					const traitCap = captures.find(
						(c) =>
							c.name === "name.definition.impl_trait" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)
					const forCap = captures.find(
						(c) =>
							c.name === "name.definition.impl_for" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)
					if (traitCap && forCap) {
						const traitName = sourceCode.substring(traitCap.node.startIndex, traitCap.node.endIndex)
						const forName = sourceCode.substring(forCap.node.startIndex, forCap.node.endIndex)
						name = `${traitName} for ${forName}`
					}
					break
			}

			if (!name) continue

			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)

			const scope = buildScopeChain(node)
			snippets.push({
				name,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				scope,
				fileHash,
			})
		}
	}

	// 处理 variables, constants, statics
	if (snippetTypes.includes(SnippetType.VariableOrConstant)) {
		const variableTypes = [
			{ captureName: "definition.variable", nameCapture: "name.definition.variable" },
			{ captureName: "definition.constant", nameCapture: "name.definition.constant" },
			{ captureName: "definition.static", nameCapture: "name.definition.static" },
		]

		for (const { captureName, nameCapture } of variableTypes) {
			for (const capture of captures) {
				if (capture.name !== captureName) continue
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1

				const nameCap = captures.find(
					(c) =>
						c.name === nameCapture &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				if (!nameCap) continue
				const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
				const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

				if (seenHashes.has(snippetHash)) continue
				seenHashes.add(snippetHash)

				// 确定是否属于某个容器
				let field: string | undefined = undefined
				for (const container of containerCaptures) {
					if (node.startIndex > container.start && node.endIndex <= container.end && container.nameCap) {
						field = container.nameCap
						break
					}
				}

				const scope = buildScopeChain(node)
				snippets.push({
					name,
					type: SnippetType.VariableOrConstant,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText: rangeText,
					field,
					scope,
					fileHash,
				})
			}
		}
	}

	return snippets
}

// 辅助函数：解析 Rust 参数
function parseRustParameters(paramText: string): string[] {
	const params: string[] = []
	let current = ""
	let depth = 0
	let inString = false
	let escapeNext = false

	for (let i = 0; i < paramText.length; i++) {
		const char = paramText[i]

		if (escapeNext) {
			current += char
			escapeNext = false
			continue
		}

		if (char === "\\") {
			escapeNext = true
			current += char
			continue
		}

		if (char === '"' || char === "'") {
			inString = !inString
			current += char
			continue
		}

		if (inString) {
			current += char
			continue
		}

		if (char === "<" || char === "(" || char === "[" || char === "{") {
			depth++
		} else if (char === ">" || char === ")" || char === "]" || char === "}") {
			depth--
		}

		if (char === "," && depth === 0) {
			const param = current.trim()
			if (param) {
				// 提取参数名（在 : 之前的部分）
				const colonIndex = param.indexOf(":")
				const paramName = colonIndex > -1 ? param.substring(0, colonIndex).trim() : param
				// 处理 self, &self, &mut self
				if (paramName === "self" || paramName === "&self" || paramName === "&mut self") {
					params.push(paramName)
				} else {
					params.push(paramName)
				}
			}
			current = ""
		} else {
			current += char
		}
	}

	// 处理最后一个参数
	const param = current.trim()
	if (param) {
		const colonIndex = param.indexOf(":")
		const paramName = colonIndex > -1 ? param.substring(0, colonIndex).trim() : param
		if (paramName === "self" || paramName === "&self" || paramName === "&mut self") {
			params.push(paramName)
		} else {
			params.push(paramName)
		}
	}

	return params
}

// 辅助函数：从 use 语句中提取名称
function extractUseStatementName(useStatement: string): string {
	// 移除 use 关键字和分号
	let cleaned = removeUseKeywordAndSemicolon(useStatement).trim()

	// 处理 as 重命名
	const asIndex = cleaned.lastIndexOf(" as ")
	if (asIndex > -1) {
		return cleaned.substring(asIndex + 4).trim()
	}

	// 处理花括号导入 {a, b, c}
	if (cleaned.includes("{") && cleaned.includes("}")) {
		const braceContent = extractBraceContent(cleaned)
		if (braceContent) {
			const items = braceContent.split(",").map((s: string) => s.trim())
			return items.join(", ")
		}
	}

	// 处理路径导入，取最后一个部分
	const parts = cleaned.split("::")
	return parts[parts.length - 1].trim()
}

// 辅助函数：为容器查找名称capture
function findNameCaptureForContainer(
	container: Parser.QueryCapture,
	captures: Parser.QueryCapture[],
	sourceCode: string,
): string | undefined {
	let nameCaptureName: string

	switch (container.name) {
		case "definition.struct":
			nameCaptureName = "name.definition.struct"
			break
		case "definition.enum":
			nameCaptureName = "name.definition.enum"
			break
		case "definition.trait":
			nameCaptureName = "name.definition.trait"
			break
		case "definition.impl":
			nameCaptureName = "name.definition.impl"
			break
		case "definition.impl_trait":
			const traitCap = captures.find(
				(c) =>
					c.name === "name.definition.impl_trait" &&
					c.node.startIndex >= container.node.startIndex &&
					c.node.endIndex <= container.node.endIndex,
			)
			const forCap = captures.find(
				(c) =>
					c.name === "name.definition.impl_for" &&
					c.node.startIndex >= container.node.startIndex &&
					c.node.endIndex <= container.node.endIndex,
			)
			if (traitCap && forCap) {
				const traitName = sourceCode.substring(traitCap.node.startIndex, traitCap.node.endIndex)
				const forName = sourceCode.substring(forCap.node.startIndex, forCap.node.endIndex)
				return `${traitName} for ${forName}`
			}
			return undefined
		default:
			return undefined
	}

	const nameCap = captures.find(
		(c) =>
			c.name === nameCaptureName &&
			c.node.startIndex >= container.node.startIndex &&
			c.node.endIndex <= container.node.endIndex,
	)

	return nameCap ? sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex) : undefined
}

// 辅助函数：查找父函数节点
function findParentFunctionNode(methodNameNode: Parser.SyntaxNode, sourceCode: string): Parser.SyntaxNode | undefined {
	let current = methodNameNode.parent
	while (current) {
		if (current.type === "function_item") {
			return current
		}
		current = current.parent
	}
	return undefined
}

// 辅助函数：移除圆括号
function removeParentheses(text: string): string {
	if (text.startsWith("(") && text.endsWith(")")) {
		return text.slice(1, -1)
	}
	return text
}

// 辅助函数：移除use关键字和分号
function removeUseKeywordAndSemicolon(useStatement: string): string {
	let result = useStatement.trim()

	// 移除开头的 "use" 关键字
	if (result.startsWith("use ")) {
		result = result.substring(4)
	}

	// 移除结尾的分号
	if (result.endsWith(";")) {
		result = result.slice(0, -1)
	}

	return result
}

// 辅助函数：提取花括号内容
function extractBraceContent(text: string): string | null {
	const startIndex = text.indexOf("{")
	const endIndex = text.lastIndexOf("}")

	if (startIndex > -1 && endIndex > startIndex) {
		return text.substring(startIndex + 1, endIndex)
	}

	return null
}

// 构建摘要信息
export interface RustSummary {
	functions: Array<{ name: string; parameters: string[]; field?: string }>
	structs: Array<{ name: string }>
	enums: Array<{ name: string }>
	traits: Array<{ name: string }>
	impls: Array<{ name: string }>
	constants: Array<{ name: string; field?: string }>
	variables: Array<{ name: string; field?: string }>
	imports: Array<{ name: string }>
}

export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): RustSummary {
	const summary: RustSummary = {
		functions: [],
		structs: [],
		enums: [],
		traits: [],
		impls: [],
		constants: [],
		variables: [],
		imports: [],
	}

	for (const snippet of snippets) {
		switch (snippet.type) {
			case SnippetType.FunctionOrMethod:
				summary.functions.push({
					name: snippet.name,
					parameters: snippet.parameters?.map((p) => p.name) || [],
					field: snippet.field,
				})
				break
			case SnippetType.ClassOrInterfaceOrStructOrEnum:
				// 根据定义文本判断是什么类型的结构
				const defText = snippet.definitionText || ""
				if (defText.startsWith("struct ")) {
					summary.structs.push({ name: snippet.name })
				} else if (defText.startsWith("enum ")) {
					summary.enums.push({ name: snippet.name })
				} else if (defText.startsWith("trait ")) {
					summary.traits.push({ name: snippet.name })
				} else if (defText.startsWith("impl ")) {
					summary.impls.push({ name: snippet.name })
				}
				break
			case SnippetType.VariableOrConstant:
				const varDefText = snippet.definitionText || ""
				if (varDefText.startsWith("const ") || varDefText.startsWith("static ")) {
					summary.constants.push({
						name: snippet.name,
						field: snippet.field,
					})
				} else {
					summary.variables.push({
						name: snippet.name,
						field: snippet.field,
					})
				}
				break
			case SnippetType.ImportOrInclude:
				summary.imports.push({ name: snippet.name })
				break
		}
	}

	return summary
}

export function formatOutlineText(summary: RustSummary): string {
	let outlineText = ""

	if (summary.imports.length > 0) {
		outlineText += "Imports:\n"
		for (const imp of summary.imports) {
			outlineText += `  - ${imp.name}\n`
		}
		outlineText += "\n"
	}

	if (summary.structs.length > 0) {
		outlineText += "Structs:\n"
		for (const struct of summary.structs) {
			outlineText += `  - ${struct.name}\n`
		}
		outlineText += "\n"
	}

	if (summary.enums.length > 0) {
		outlineText += "Enums:\n"
		for (const enumItem of summary.enums) {
			outlineText += `  - ${enumItem.name}\n`
		}
		outlineText += "\n"
	}

	if (summary.traits.length > 0) {
		outlineText += "Traits:\n"
		for (const trait of summary.traits) {
			outlineText += `  - ${trait.name}\n`
		}
		outlineText += "\n"
	}

	if (summary.impls.length > 0) {
		outlineText += "Implementations:\n"
		for (const impl of summary.impls) {
			outlineText += `  - ${impl.name}\n`
		}
		outlineText += "\n"
	}

	if (summary.functions.length > 0) {
		outlineText += "Functions/Methods:\n"
		for (const func of summary.functions) {
			const paramStr = func.parameters.length > 0 ? `(${func.parameters.join(", ")})` : "()"
			const fieldStr = func.field ? ` [in ${func.field}]` : ""
			outlineText += `  - ${func.name}${paramStr}${fieldStr}\n`
		}
		outlineText += "\n"
	}

	if (summary.constants.length > 0) {
		outlineText += "Constants/Statics:\n"
		for (const constant of summary.constants) {
			const fieldStr = constant.field ? ` [in ${constant.field}]` : ""
			outlineText += `  - ${constant.name}${fieldStr}\n`
		}
		outlineText += "\n"
	}

	if (summary.variables.length > 0) {
		outlineText += "Variables:\n"
		for (const variable of summary.variables) {
			const fieldStr = variable.field ? ` [in ${variable.field}]` : ""
			outlineText += `  - ${variable.name}${fieldStr}\n`
		}
		outlineText += "\n"
	}

	return outlineText.trim()
}
