#!/usr/bin/env node
const { program } = require("commander") 

import { CodeSnippets } from "../autocompletion/v2/CodeSnippets"
import { CodeLanguageType, ICodeFiles, SnippetType } from "../autocompletion/v2/types";
import { CodeContext } from "../autocompletion/v2/CodeContext"

const codeContext = new CodeContext()

function getExtension(filePath: string): string {
    return filePath.substring(filePath.lastIndexOf(".") + 1, filePath.length) 
}
async function index(codeFiles: ICodeFiles, maxSnippetLines?: number): Promise<CodeSnippets> {
    const maxLines = maxSnippetLines || 100 
    const allSnippets = new CodeSnippets() 

    const indexPromises = codeFiles.files.map(async (file) => {
        try {
            if (file.language === undefined || !Object.values(CodeLanguageType).includes(file.language as CodeLanguageType)) {
                console.log(`Unsupported language: ${file.language}`)
                return new CodeSnippets()
            }
            // 读取文件内容
            const content = file.content
            if (!content) {
                console.log(`Content is empty for file: ${file.filePath}`)
                return new CodeSnippets()
            }
            file.fileExtension = getExtension(file.filePath) 

            // 对每个文件进行 chunk
            const fileSnippets = await codeContext.chunk(content, {
                file: file,
                maxSnippetLines: maxLines,
                snippetTypes: [
                    SnippetType.FunctionOrMethod,
                    SnippetType.ClassOrInterfaceOrStructOrEnum,
                    SnippetType.VariableOrConstant,
                    SnippetType.ImportOrInclude,
                ]
            })
            return fileSnippets
        } catch (error) {
            return new CodeSnippets()
        }
    })

    // 等待所有文件处理完成
    const fileSnippetsArray = await Promise.all(indexPromises)

    // 合并所有片段
    for (const fileSnippets of fileSnippetsArray) {
        await allSnippets.merge(fileSnippets)
    }

    return allSnippets
}

program
    .command("index <input> ") 
    .option("-m <number>", "--maxlines <number>", "Maximum number of lines for each snippet")
    .description("Index the given files.") 
    .action(async (input: string, cmd: any) => {
        const codeFiles: ICodeFiles = { files: JSON.parse(input) }

        const indexResult = await index(
            codeFiles,
            cmd.maxlines ? parseInt(cmd.maxlines, 10) : undefined
        )
        
        console.log(`${JSON.stringify(indexResult.getSnippets(), null, 2)}`)
    })

program.parse(process.argv) 