import { Logger } from "../Logger"
import { CodeLanguageType, ISnippetMeta, SnippetType, ParameterInfo } from "../types"
import Parser from "web-tree-sitter"
import * as crypto from "crypto"

/**
 * 提取修饰符（private, protected, internal, public等）
 */
function extractModifiers(node: Parser.SyntaxNode, sourceCode: string): string[] {
	const modifiers: string[] = []

	// 查找修饰符节点
	const modifierNode = node.children.find((child) => child.type === "modifiers")
	if (modifierNode) {
		modifierNode.children.forEach((child) => {
			if (child.type === "visibility_modifier" || child.type === "modifier") {
				const modifier = sourceCode.substring(child.startIndex, child.endIndex)
				modifiers.push(modifier)
			}
		})
	}

	return modifiers
}

/**
 * 从import语句中提取导入路径（避免使用正则表达式）
 */
function extractImportPath(importNode: Parser.SyntaxNode, sourceCode: string): string {
	// 先尝试查找完整的import路径，包括通配符
	const fullImportText = sourceCode.substring(importNode.startIndex, importNode.endIndex)

	// 查找import关键字的位置
	const importKeywordIndex = fullImportText.indexOf("import")
	if (importKeywordIndex === -1) return fullImportText.trim()

	// 提取import后面的内容
	const importKeywordEnd = importKeywordIndex + 6 // "import".length
	let pathPart = fullImportText.substring(importKeywordEnd).trim()

	// 移除可能的换行符和分号
	pathPart = pathPart.split("\n")[0].split("\r")[0].split(";")[0].trim()

	return pathPart
}

/**
 * 根据AST节点确定定义类型（避免使用字符串匹配）
 */
function getDefinitionType(node: Parser.SyntaxNode, sourceCode: string): string {
	// 检查节点类型
	if (node.type === "object_declaration") return "object"
	if (node.type === "type_alias") return "typealias"

	if (node.type === "class_declaration") {
		// 查找修饰符节点来确定类型
		const modifierNode = node.children.find((child) => child.type === "modifiers")
		if (modifierNode) {
			const modifierTexts = modifierNode.children.map((child) =>
				sourceCode.substring(child.startIndex, child.endIndex),
			)

			// 检查修饰符组合
			if (modifierTexts.includes("sealed")) return "sealed class"
			if (modifierTexts.includes("data")) return "data class"
			if (modifierTexts.includes("enum")) return "enum"
			if (modifierTexts.includes("annotation")) return "annotation class"
		}

		// 检查是否有enum_class_body
		const hasEnumBody = node.children.some((child) => child.type === "enum_class_body")
		if (hasEnumBody) return "enum"

		// 检查是否为接口（通过查找interface关键字节点）
		const hasInterfaceKeyword = node.children.some((child) => {
			if (child.type === "interface") return true
			const childText = sourceCode.substring(child.startIndex, child.endIndex)
			return childText === "interface"
		})
		if (hasInterfaceKeyword) return "interface"

		return "class"
	}

	return "class"
}

/**
 * 查找父类或接口名称
 */
function findParentClassName(
	captures: Parser.QueryCapture[],
	node: Parser.SyntaxNode,
	sourceCode: string,
): string | undefined {
	// 查找包含当前节点的类声明
	const classCapture = captures.find(
		(c) =>
			c.name === "definition.class" && c.node.startIndex <= node.startIndex && c.node.endIndex >= node.endIndex,
	)

	if (classCapture) {
		const nameNode = classCapture.node.children.find((child) => child.type === "type_identifier")
		return nameNode ? sourceCode.substring(nameNode.startIndex, nameNode.endIndex) : undefined
	}

	return undefined
}

/**
 * 解析单个参数的信息（避免使用正则表达式）
 */
function parseParameterInfo(paramNode: Parser.SyntaxNode, sourceCode: string): ParameterInfo {
	// 查找参数名称
	const nameNode = paramNode.children.find((child) => child.type === "simple_identifier")
	const name = nameNode ? sourceCode.substring(nameNode.startIndex, nameNode.endIndex) : ""

	// 查找类型注解
	const typeNode = paramNode.children.find((child) => child.type === "user_type" || child.type === "type_identifier")
	const type = typeNode ? sourceCode.substring(typeNode.startIndex, typeNode.endIndex) : undefined

	// 查找默认值
	const defaultValueNode = paramNode.children.find((child) => child.type === "default_value")
	const defaultValue = defaultValueNode
		? sourceCode.substring(defaultValueNode.startIndex, defaultValueNode.endIndex)
		: undefined

	return {
		name: name.trim(),
		type: type?.trim(),
		defaultValue: defaultValue?.trim(),
		isOptional: !!defaultValue,
	}
}

/**
 * 提取函数参数
 */
function extractParameters(node: Parser.SyntaxNode, sourceCode: string): ParameterInfo[] {
	const parameters: ParameterInfo[] = []

	// 查找参数列表
	const parameterList = node.children.find((child) => child.type === "function_value_parameters")
	if (parameterList) {
		// 查找所有参数
		const paramNodes = parameterList.children.filter((child) => child.type === "parameter")
		paramNodes.forEach((paramNode) => {
			const paramInfo = parseParameterInfo(paramNode, sourceCode)
			if (paramInfo.name) {
				parameters.push(paramInfo)
			}
		})
	}

	return parameters
}

/**
 * 提取函数返回类型
 */
function extractReturnType(node: Parser.SyntaxNode, sourceCode: string): string {
	// 查找返回类型
	const returnTypeNode = node.children.find((child) => child.type === "user_type" || child.type === "type_identifier")
	if (returnTypeNode) {
		return sourceCode.substring(returnTypeNode.startIndex, returnTypeNode.endIndex)
	}

	return "Unit"
}

/**
 * 从Kotlin代码的AST捕获中提取代码片段
 */
export async function extractSnippetsFromCapturesForKotlin(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForKotlin]")
	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 处理 import 和 package
	if (snippetTypes.includes(SnippetType.ImportOrInclude)) {
		captures.forEach((capture) => {
			if (capture.name === "definition.import") {
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1

				// 提取import路径
				const importPath = extractImportPath(node, sourceCode)
				const name = importPath
				const scope = ["global"]
				const rangeText = sourceCode.substring(node.startIndex, node.endIndex).trim()

				// 生成唯一的哈希值
				const hash = crypto.createHash("md5").update(`${name}-${filePath}-${startLine}`).digest("hex")
				if (seenHashes.has(hash)) {
					return
				}
				seenHashes.add(hash)

				snippets.push({
					name,
					type: SnippetType.ImportOrInclude,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText: rangeText,
					scope,
					fileHash,
					language: CodeLanguageType.Kotlin,
					definition: {
						name,
						type: "import",
					},
				})
			}
		})
	}

	// 处理类、接口、对象等声明
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		captures.forEach((capture) => {
			if (capture.name === "definition.class" || capture.name === "definition.object") {
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1

				// 提取名称
				const nameNode = node.children.find((child) => child.type === "type_identifier")
				const name = nameNode ? sourceCode.substring(nameNode.startIndex, nameNode.endIndex) : ""

				if (!name) return

				const scope = ["global"]
				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)

				// 生成唯一的哈希值
				const hash = crypto.createHash("md5").update(`${name}-${filePath}-${startLine}`).digest("hex")
				if (seenHashes.has(hash)) {
					return
				}
				seenHashes.add(hash)

				// 提取修饰符和类型
				const modifiers = extractModifiers(node, sourceCode)
				const definitionType = getDefinitionType(node, sourceCode)

				snippets.push({
					name,
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText: rangeText,
					scope,
					fileHash,
					language: CodeLanguageType.Kotlin,
					definition: {
						name,
						type: definitionType,
						visibility: modifiers.includes("private")
							? "private"
							: modifiers.includes("protected")
								? "protected"
								: modifiers.includes("internal")
									? "internal"
									: "public",
					},
				})
			}
		})
	}

	// 处理函数和方法相关声明
	if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
		captures.forEach((capture) => {
			if (capture.name === "definition.function") {
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1

				// 提取函数名称
				const nameNode = node.children.find((child) => child.type === "simple_identifier")
				const name = nameNode ? sourceCode.substring(nameNode.startIndex, nameNode.endIndex) : ""

				if (!name) return

				const scope = ["global"]
				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)

				// 生成唯一的哈希值
				const hash = crypto.createHash("md5").update(`${name}-${filePath}-${startLine}`).digest("hex")
				if (seenHashes.has(hash)) {
					return
				}
				seenHashes.add(hash)

				// 提取函数参数和返回类型
				const parameters = extractParameters(node, sourceCode)
				const returnType = extractReturnType(node, sourceCode)
				const modifiers = extractModifiers(node, sourceCode)

				// 查找所属类
				const className = findParentClassName(captures, node, sourceCode)

				snippets.push({
					name,
					type: SnippetType.FunctionOrMethod,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText: rangeText,
					scope,
					fileHash,
					language: CodeLanguageType.Kotlin,
					parameters,
					...(className && { field: className }),
					definition: {
						name,
						type: "function",
						parameters,
						returnType,
						visibility: modifiers.includes("private")
							? "private"
							: modifiers.includes("protected")
								? "protected"
								: modifiers.includes("internal")
									? "internal"
									: "public",
						...(className && { className }),
					},
				})
			}
		})
	}

	// 处理变量和常量相关声明
	if (snippetTypes.includes(SnippetType.VariableOrConstant)) {
		captures.forEach((capture) => {
			if (capture.name === "definition.property") {
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1

				// 提取属性名称 - 尝试多种方式查找名称
				let nameNode = node.children.find((child) => child.type === "simple_identifier")
				if (!nameNode) {
					// 如果没有找到，尝试在variable_declaration中查找
					const variableDecl = node.children.find((child) => child.type === "variable_declaration")
					if (variableDecl) {
						nameNode = variableDecl.children.find((child) => child.type === "simple_identifier")
					}
				}
				const name = nameNode ? sourceCode.substring(nameNode.startIndex, nameNode.endIndex) : ""

				if (!name) return

				const scope = ["global"]
				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)

				// 生成唯一的哈希值
				const hash = crypto.createHash("md5").update(`${name}-${filePath}-${startLine}`).digest("hex")
				if (seenHashes.has(hash)) {
					return
				}
				seenHashes.add(hash)

				// 提取修饰符
				const modifiers = extractModifiers(node, sourceCode)

				// 查找所属类
				const className = findParentClassName(captures, node, sourceCode)

				snippets.push({
					name,
					type: SnippetType.VariableOrConstant,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText: rangeText,
					scope,
					fileHash,
					language: CodeLanguageType.Kotlin,
					...(className && { field: className }),
					definition: {
						name,
						type: "property",
						visibility: modifiers.includes("private")
							? "private"
							: modifiers.includes("protected")
								? "protected"
								: modifiers.includes("internal")
									? "internal"
									: "public",
						...(className && { className }),
					},
				})
			}
		})
	}

	// 处理 typealias 声明
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		captures.forEach((capture) => {
			if (capture.name === "definition.type_alias") {
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1

				// 提取 typealias 名称
				const nameNode = node.children.find((child) => child.type === "type_identifier")
				const name = nameNode ? sourceCode.substring(nameNode.startIndex, nameNode.endIndex) : ""

				if (!name) return

				const scope = ["global"]
				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)

				// 生成唯一的哈希值
				const hash = crypto.createHash("md5").update(`${name}-${filePath}-${startLine}`).digest("hex")
				if (seenHashes.has(hash)) {
					return
				}
				seenHashes.add(hash)

				snippets.push({
					name,
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText: rangeText,
					scope,
					fileHash,
					language: CodeLanguageType.Kotlin,
					definition: {
						name,
						type: "type_alias",
					},
				})
			}
		})
	}

	return snippets
}

/**
 * 构建Kotlin代码片段的摘要
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): any {
	const summary = {
		imports: snippets.filter((s) => s.type === SnippetType.ImportOrInclude),
		classes: snippets.filter((s) => s.type === SnippetType.ClassOrInterfaceOrStructOrEnum),
		functions: snippets.filter((s) => s.type === SnippetType.FunctionOrMethod),
		variables: snippets.filter((s) => s.type === SnippetType.VariableOrConstant),
	}

	return summary
}

/**
 * 格式化大纲文本
 */
export function formatOutlineText(summary: any): string {
	const lines: string[] = []

	// 处理imports
	if (summary.imports?.length > 0) {
		lines.push("# Imports")
		summary.imports.forEach((item: ISnippetMeta) => {
			lines.push(`  import: ${item.name}`)
		})
		lines.push("")
	}

	// 处理classes
	if (summary.classes?.length > 0) {
		lines.push("# Classes/Interfaces/Objects")
		summary.classes.forEach((item: ISnippetMeta) => {
			const type = item.definition?.type || "class"
			lines.push(`  ${type}: ${item.name}`)
		})
		lines.push("")
	}

	// 处理functions
	if (summary.functions?.length > 0) {
		lines.push("# Functions/Methods")
		summary.functions.forEach((item: ISnippetMeta) => {
			const params = item.definition?.parameters?.map((p) => `${p.name}: ${p.type || "Any"}`).join(", ") || ""
			const returnType = item.definition?.returnType || "Unit"
			const className = item.field
			const prefix = className ? `[${className}] ` : ""
			lines.push(`  ${prefix}${item.name}(${params}): ${returnType}`)
		})
		lines.push("")
	}

	// 处理variables
	if (summary.variables?.length > 0) {
		lines.push("# Variables/Properties")
		summary.variables.forEach((item: ISnippetMeta) => {
			const className = item.field
			const prefix = className ? `[${className}] ` : ""
			lines.push(`  ${prefix}property: ${item.name}`)
		})
		lines.push("")
	}

	return lines.join("\n")
}
