import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"

// Helper function to remove parentheses from parameter string
function removeParentheses(text: string): string {
	let result = text.trim()
	if (result.startsWith("(") && result.endsWith(")")) {
		result = result.slice(1, -1).trim()
	}
	return result
}

// Helper function to remove brackets from array destructuring
function removeBrackets(text: string): string {
	let result = text.trim()
	if (result.startsWith("[") && result.endsWith("]")) {
		result = result.slice(1, -1).trim()
	}
	return result
}

// Helper function to check if a string is a valid JavaScript identifier
function isValidJavaScriptIdentifier(name: string): boolean {
	if (!name) return false

	// First character must be letter, underscore, or dollar sign
	const firstChar = name[0]
	if (
		!(firstChar >= "a" && firstChar <= "z") &&
		!(firstChar >= "A" && firstChar <= "Z") &&
		firstChar !== "_" &&
		firstChar !== "$"
	) {
		return false
	}

	// Remaining characters can be letters, digits, underscore, or dollar sign
	for (let i = 1; i < name.length; i++) {
		const char = name[i]
		if (
			!(char >= "a" && char <= "z") &&
			!(char >= "A" && char <= "Z") &&
			!(char >= "0" && char <= "9") &&
			char !== "_" &&
			char !== "$"
		) {
			return false
		}
	}

	return true
}

// Helper function to replace newlines with spaces for formatting
function replaceNewlinesWithSpaces(text: string): string {
	return text.split("\n").join(" ")
}

// Helper function to extract parameters from method text using string parsing
function extractParametersFromMethodText(methodText: string): string | null {
	const openParen = methodText.indexOf("(")
	const closeParen = methodText.lastIndexOf(")")

	if (openParen !== -1 && closeParen !== -1 && closeParen > openParen) {
		return methodText.substring(openParen + 1, closeParen).trim()
	}

	return null
}

export async function extractSnippetsFromCapturesForJavaScript(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	// --- Function/Method ---
	// Collect all class captures first for context
	const classCaptures = captures
		.filter((c) => c.name === "definition.class")
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			nameCap: captures.find(
				(nc) =>
					nc.name === "name" &&
					nc.node.startIndex >= c.node.startIndex &&
					nc.node.endIndex <= c.node.endIndex,
			),
		}))

	// Process function declarations
	for (const capture of captures) {
		if (capture.name !== "definition.function") continue
		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Find function name
		const nameCap = captures.find(
			(c) =>
				(c.name === "name.definition.function" || c.name === "name") &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// Find parameters
		const paramCap = captures.find(
			(c) =>
				c.name === "parameters.definition.function" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)

		const parameters: ParameterInfo[] = []
		if (paramCap) {
			const paramText = sourceCode.substring(paramCap.node.startIndex, paramCap.node.endIndex)
			const cleanParams = removeParentheses(paramText)
			if (cleanParams) {
				// JavaScript parameter parsing - handle destructuring, defaults, etc.
				const paramList = splitJavaScriptParameters(cleanParams)
				parameters.push(...paramList.map((param) => ({ name: param })))
			}
		}

		// Find function body
		const bodyCap = captures.find(
			(c) =>
				c.name === "body.definition.function" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)

		let rangeText: string
		let implementText: string | undefined = undefined
		if (bodyCap) {
			rangeText = sourceCode.substring(node.startIndex, bodyCap.node.startIndex).trimEnd()
			implementText = sourceCode.substring(node.startIndex, node.endIndex)
		} else {
			rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		}

		// Check if function belongs to a class
		let field: string | undefined = undefined
		for (const cls of classCaptures) {
			if (node.startIndex > cls.start && node.endIndex < cls.end && cls.nameCap) {
				field = sourceCode.substring(cls.nameCap.node.startIndex, cls.nameCap.node.endIndex)
				break
			}
		}

		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const paramStr = parameters.map((p) => p.name).join(", ")
		const signature = `${name}(${paramStr})`
		const scope = buildScopeChain(node)

		// Detect function type (async, generator, arrow, etc.)
		let functionType = "function"
		const nodeText = sourceCode.substring(node.startIndex, Math.min(node.startIndex + 100, node.endIndex))
		if (nodeText.includes("async")) functionType = "async function"
		if (nodeText.includes("function*")) functionType = "generator function"
		if (nodeText.includes("=>")) functionType = "arrow function"

		snippets.push({
			name,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			definition: {
				name,
				type: functionType,
				parameters,
				returnType: "", // JavaScript doesn't have explicit return types
			},
			parameters,
			signature,
			language: CodeLanguageType.JavaScript,
			...(implementText ? { implementText } : {}),
		})
	}

	// --- Method definitions ---
	for (const capture of captures) {
		if (capture.name !== "definition.method") continue
		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Find method name
		const nameCap = captures.find(
			(c) => c.name === "name" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// Find method parameters - prioritize captured parameters
		const paramCap = captures.find(
			(c) =>
				c.name === "parameters.definition.method" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)

		const parameters: ParameterInfo[] = []
		if (paramCap) {
			// Use captured parameters
			const paramText = sourceCode.substring(paramCap.node.startIndex, paramCap.node.endIndex)
			const cleanParams = removeParentheses(paramText)
			if (cleanParams) {
				const paramList = splitJavaScriptParameters(cleanParams)
				parameters.push(...paramList.map((param) => ({ name: param })))
			}
		} else {
			// Fallback to string parsing
			const methodText = sourceCode.substring(node.startIndex, node.endIndex)
			const paramString = extractParametersFromMethodText(methodText)
			if (paramString) {
				const paramList = splitJavaScriptParameters(paramString)
				parameters.push(...paramList.map((param) => ({ name: param })))
			}
		}

		// Find containing class - we'll do this after all classes are processed
		let field: string | undefined = undefined

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const paramStr = parameters.map((p) => p.name).join(", ")
		const signature = `${name}(${paramStr})`
		const scope = buildScopeChain(node)

		// Detect method type
		let methodType = "method"
		const methodText = sourceCode.substring(node.startIndex, node.endIndex)
		if (methodText.includes("async")) methodType = "async method"
		if (methodText.includes("static")) methodType = "static method"
		if (methodText.includes("get ")) methodType = "getter"
		if (methodText.includes("set ")) methodType = "setter"

		snippets.push({
			name,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			definition: {
				name,
				type: methodType,
				parameters,
				returnType: "",
			},
			parameters,
			signature,
			language: CodeLanguageType.JavaScript,
			implementText: rangeText,
		})
	}

	// --- Class definitions ---
	for (const capture of captures) {
		if (capture.name !== "definition.class") continue
		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		const nameCap = captures.find(
			(c) =>
				c.name === "name.definition.class" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)
		const scope = buildScopeChain(node)

		snippets.push({
			name,
			type: SnippetType.ClassOrInterfaceOrStructOrEnum,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			definition: {
				name,
				type: "class",
			},
			language: CodeLanguageType.JavaScript,
		})
	}

	// --- Fix method field associations ---
	// Get all classes to determine field associations
	const allClasses = snippets.filter((s) => s.type === SnippetType.ClassOrInterfaceOrStructOrEnum)

	// Update method field associations
	for (const method of snippets) {
		if (method.type === SnippetType.FunctionOrMethod && !method.field) {
			for (const cls of allClasses) {
				if (method.startLine >= cls.startLine && method.endLine <= cls.endLine) {
					method.field = cls.name
					break
				}
			}
		}
	}

	// --- Variable definitions ---

	for (const capture of captures) {
		if (capture.name !== "definition.variable") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Check if this is an array destructuring pattern
		const arrayPatternCap = captures.find(
			(c) =>
				c.name === "array_pattern" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)

		if (arrayPatternCap) {
			// Handle array destructuring manually
			const arrayPatternText = sourceCode.substring(
				arrayPatternCap.node.startIndex,
				arrayPatternCap.node.endIndex,
			)
			// Extract variable names from array pattern like [first, second]
			const variableNames = removeBrackets(arrayPatternText)
				.split(",")
				.map((name) => name.trim())
				.filter((name) => name && isValidJavaScriptIdentifier(name)) // Valid identifiers only

			// Process each variable separately
			for (const varName of variableNames) {
				// Skip local variables (inside functions)
				const scope = buildScopeChain(node)
				const isInsideFunction =
					scope.length > 0 &&
					scope.some((scopeItem) => {
						return !allClasses.some((cls) => cls.name === scopeItem)
					})

				if (isInsideFunction) continue

				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
				const snippetHash = generateSnippetHash(filePath, startLine, endLine, `${rangeText}_${varName}`)
				if (seenHashes.has(snippetHash)) continue
				seenHashes.add(snippetHash)

				// Determine if this is a class member variable
				let field: string | undefined = undefined
				let variableType = "global_variable"

				// Check if variable is inside a class
				for (const cls of allClasses) {
					if (startLine >= cls.startLine && endLine <= cls.endLine) {
						field = cls.name
						variableType = "member_variable"
						break
					}
				}

				// Create definition text
				let definitionText = varName
				if (variableType === "global_variable") {
					definitionText = replaceNewlinesWithSpaces(rangeText).trim()
				}

				snippets.push({
					name: varName,
					type: SnippetType.VariableOrConstant,
					filePath,
					startLine,
					endLine,
					rangeText,
					definitionText,
					scope,
					fileHash,
					field,
					definition: {
						name: varName,
						type: variableType,
					},
					language: CodeLanguageType.JavaScript,
				})
			}
			continue
		}

		const nameCap =
			captures.find(
				(c) =>
					c.name === "name.definition.variable" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			) ||
			captures.find(
				(c) =>
					c.name === "name.definition.member_variable" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// Skip local variables (inside functions)
		const scope = buildScopeChain(node)

		// Check if this is a member variable assignment (this.property)
		const isMemberVariable = captures.some(
			(c) =>
				c.name === "name.definition.member_variable" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex &&
				sourceCode.substring(c.node.startIndex, c.node.endIndex) === name,
		)

		// A variable is local if it's inside any function (scope has function names)
		// But we need to allow class member variables (inside classes but not functions)
		// and this.property assignments even if they're inside constructors
		const isInsideFunction =
			scope.length > 0 &&
			scope.some((scopeItem) => {
				// Check if this scope item is a function by looking at the node type
				// For now, we'll use a heuristic: if it's not a class name, it's likely a function
				return !allClasses.some((cls) => cls.name === scopeItem)
			})

		// Skip if it's a local variable but not a member variable
		if (isInsideFunction && !isMemberVariable) continue

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		// Determine if this is a class member variable
		let field: string | undefined = undefined
		let variableType = "global_variable"

		// Check if variable is inside a class
		for (const cls of allClasses) {
			if (startLine >= cls.startLine && endLine <= cls.endLine) {
				field = cls.name
				variableType = "member_variable"
				break
			}
		}

		// Create definition text (just the variable name for member variables)
		let definitionText = name
		if (variableType === "global_variable") {
			// For global variables, show the full declaration
			definitionText = replaceNewlinesWithSpaces(rangeText).trim()
		}

		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText,
			scope,
			fileHash,
			field,
			definition: {
				name,
				type: variableType,
			},
			language: CodeLanguageType.JavaScript,
		})
	}

	// Filter by requested snippet types
	return snippets.filter((snippet) => snippetTypes.includes(snippet.type)).sort((a, b) => a.startLine - b.startLine)
}

/**
 * Split JavaScript parameters handling destructuring, defaults, rest parameters, etc.
 */
function splitJavaScriptParameters(params: string): string[] {
	const result: string[] = []
	let current = ""
	let braceDepth = 0
	let bracketDepth = 0
	let parenDepth = 0
	let inString = false
	let stringChar = ""

	for (let i = 0; i < params.length; i++) {
		const char = params[i]
		const prevChar = i > 0 ? params[i - 1] : ""

		// Handle string literals
		if (!inString && (char === '"' || char === "'" || char === "`")) {
			inString = true
			stringChar = char
		} else if (inString && char === stringChar && prevChar !== "\\") {
			inString = false
			stringChar = ""
		}

		if (!inString) {
			// Track nesting depth
			if (char === "{") braceDepth++
			else if (char === "}") braceDepth--
			else if (char === "[") bracketDepth++
			else if (char === "]") bracketDepth--
			else if (char === "(") parenDepth++
			else if (char === ")") parenDepth--
			else if (char === "," && braceDepth === 0 && bracketDepth === 0 && parenDepth === 0) {
				// Split only at top-level commas
				if (current.trim()) {
					result.push(current.trim())
				}
				current = ""
				continue
			}
		}

		current += char
	}

	if (current.trim()) {
		result.push(current.trim())
	}

	return result
}
// VSCode Outline Entry 类型
export interface OutlineEntry {
	type: string
	name: string
	file: string // 表示当前文件
	field?: string // 空字符串代表当前文件的global
	description?: string
}

/**
 * 根据 JavaScript 文件的所有 snippet 生成大纲（Outline），支持 import、global variables、function、method（带 field）、class
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): OutlineEntry[] {
	const outline: OutlineEntry[] = []

	for (const s of snippets) {
		// 判断是否是顶层项或类成员
		const isTopLevelFunction =
			s.type === SnippetType.FunctionOrMethod && !s.field && (!s.scope || s.scope.length === 0)
		const isClassOrGlobalVar =
			(s.type === SnippetType.ClassOrInterfaceOrStructOrEnum || s.type === SnippetType.VariableOrConstant) &&
			!s.scope?.some((scopeItem) => scopeItem.startsWith("function"))
		const isMethodOrMemberVar = !!s.field
		const isImport = s.type === SnippetType.ImportOrInclude

		if (!(isTopLevelFunction || isClassOrGlobalVar || isMethodOrMemberVar || isImport)) {
			continue
		}

		if (s.type === SnippetType.ImportOrInclude) {
			outline.push({
				type: "import",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.FunctionOrMethod) {
			if (s.field) {
				outline.push({
					type: "method",
					name: s.name,
					file: s.filePath,
					field: s.field,
					description: s.definitionText,
				})
			} else {
				outline.push({
					type: "function",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.definitionText,
				})
			}
		} else if (s.type === SnippetType.VariableOrConstant) {
			const t = s.definition?.type === "global_variable" ? "variable" : "member_variable"
			// 对于成员变量，显示不带this.前缀的名称
			const displayName = s.name.startsWith("this.") ? s.name.substring(5) : s.name
			outline.push({
				type: t,
				name: displayName,
				file: s.filePath,
				field: s.field || "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.ClassOrInterfaceOrStructOrEnum) {
			outline.push({
				type: "class",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.name,
			})
		}
	}

	return outline
}

/**
 * 将 OutlineEntry[] 格式化为文本，同一个 field 的放在一起。
 * @param outline OutlineEntry[]
 * @returns string
 */
export function formatOutlineText(outline: OutlineEntry[]): string {
	// 按 field 分组
	const groups: Record<string, OutlineEntry[]> = {}
	for (const entry of outline) {
		const key = entry.field ?? ""
		if (!groups[key]) groups[key] = []
		groups[key].push(entry)
	}

	// 合成文本
	let result = ""
	for (const field of Object.keys(groups).sort()) {
		if (groups[field].length === 0) continue
		const group = groups[field]
		const title = field === "" ? "[global]" : `[${field}]`
		result += title + "\n"
		for (const entry of group) {
			let line = `  ${entry.type}`
			// 对于变量类型，显示名称而不是完整描述
			if (entry.type === "variable" || entry.type === "member_variable") {
				line += `: ${entry.name}`
			} else if (entry.description) {
				line += `: ${replaceNewlinesWithSpaces(entry.description)}`
			}
			result += line + "\n"
		}
		result += "\n"
	}
	return result.trimEnd()
}
