const symbolPairs: Record<string, string> = {
	"(": ")",
	"[": "]",
	"{": "}",
	// '<': '>',
	"'": "'",
	'"': '"',
	"`": "`",
}
const openSymbols = new Set(Object.keys(symbolPairs))
const closeSymbols = new Set(Object.values(symbolPairs))

/**
 * splitStr 将 fullStr 分为 A 和 B 两个字符串，
 * 负责查找 B 部分中第一个与 A 部分最后一个入栈符号匹配的闭合符号
 * @param fullStr 完整字符串
 * @param splitStr 分割字符串
 * @returns B部分中第一个匹配的闭合符号，B 部分中该符号前的字符串
 */
export function findFirstMatchingCloseSymbol(
	fullStr: string,
	splitStr: string,
): { closeSymbol: string; suffixPrefix: string } {
	const splitIndex = fullStr.indexOf(splitStr)
	if (splitIndex === -1) {
		return { closeSymbol: "", suffixPrefix: "" }
	}

	const partA = fullStr.substring(0, splitIndex)
	const partB = fullStr.substring(splitIndex + splitStr.length)
	const stackA: string[] = []

	// 处理A部分的符号入栈
	for (let i = 0; i < partA.length; i++) {
		const char = partA[i]

		// 处理转义字符
		if (char === "\\") {
			i++ // 跳过下一个字符
			continue
		}

		if (openSymbols.has(char)) {
			// 对于单字符引号，需要检查栈顶是否已经是相同的引号（表示闭合）
			if (
				(char === "'" || char === '"' || char === "`") &&
				stackA.length > 0 &&
				stackA[stackA.length - 1] === char
			) {
				stackA.pop() // 匹配到闭合引号
			} else {
				stackA.push(char) // 压入新的开符号
			}
		} else if (closeSymbols.has(char)) {
			if (stackA.length === 0) continue
			const lastOpen = stackA[stackA.length - 1]
			if (symbolPairs[lastOpen] === char) {
				stackA.pop() // 匹配到闭合符号
			}
		}
	}

	if (stackA.length === 0) {
		return { closeSymbol: "", suffixPrefix: "" }
	}

	const lastOpenSymbol = stackA[stackA.length - 1]
	const expectedCloseSymbol = symbolPairs[lastOpenSymbol]
	const stackB: string[] = []

	// 在B部分查找第一个匹配的闭合符号
	for (let i = 0; i < partB.length; i++) {
		const char = partB[i]

		// 处理转义字符
		if (char === "\\") {
			i++ // 跳过下一个字符
			continue
		}
		// 检查单字符闭合符号
		if (stackB.length === 0 && char === expectedCloseSymbol) {
			return { closeSymbol: char, suffixPrefix: partB.slice(0, i) }
		}
		if (openSymbols.has(char)) {
			// 对于单字符引号，需要检查栈顶是否已经是相同的引号（表示闭合）
			if (
				(char === "'" || char === '"' || char === "`") &&
				stackB.length > 0 &&
				stackB[stackB.length - 1] === char
			) {
				stackB.pop() // 匹配到闭合引号
			} else {
				stackB.push(char) // 压入新的开符号
			}
		} else if (closeSymbols.has(char)) {
			if (stackB.length === 0) continue
			const lastOpen = stackB[stackB.length - 1]
			if (symbolPairs[lastOpen] === char) {
				stackB.pop() // 匹配到闭合符号
			}
		}
	}

	return { closeSymbol: "", suffixPrefix: "" }
}

/**
 * 查找 str1 的后缀中与 str2 的前缀中的最大相同字符串
 * @param str1 第一个字符串
 * @param str2 第二个字符串
 * @returns 最大的公共子串，如果没有则返回空字符串
 */
export function findMaxOverlap(str1: string, str2: string): string {
	// 如果任一字符串为空，直接返回空字符串
	if (str1 === "" || str2 === "") return ""

	let maxOverlap = ""

	// 从可能的最大长度开始递减遍历（最大为 min(a.length, b.length)）
	for (let len = Math.min(str1.length, str2.length); len > 0; len--) {
		const suffix = str1.substring(str1.length - len)
		const prefix = str2.substring(0, len)

		// 如果后缀等于前缀，则找到有效重叠
		if (suffix === prefix) {
			maxOverlap = suffix
			break // 找到最大重叠后立即退出循环
		}
	}

	// 如果直接匹配失败，尝试去除str2前导空格后匹配（修复空格差异问题）
	if (maxOverlap === "") {
		const str2Trimmed = str2.trimStart()

		for (let len = Math.min(str1.length, str2Trimmed.length); len > 0; len--) {
			const suffix = str1.substring(str1.length - len)
			const prefix = str2Trimmed.substring(0, len)

			if (suffix === prefix) {
				maxOverlap = suffix
				break
			}
		}
	}

	return maxOverlap
}

/**
 * 获取字符串中在指定闭合符号前的内容，处理符号嵌套
 * @param str 输入字符串
 * @param closeSymbol 要查找的闭合符号
 * @returns 闭合符号前的内容
 */
export function getStringBeforeCloseSymbol(str: string, closeSymbol: string): string {
	if (!closeSymbols.has(closeSymbol)) {
		return ""
	}

	const stack: string[] = []
	let result = ""

	for (let i = 0; i < str.length; i++) {
		const char = str[i]

		// 处理转义字符
		if (char === "\\") {
			result += char
			if (i + 1 < str.length) {
				result += str[++i]
			}
			continue
		}

		// 检查是否匹配目标闭合符号
		if (stack.length === 0 && char === closeSymbol) {
			return result
		}

		// 处理开符号
		if (openSymbols.has(char)) {
			// 对于单字符引号，检查栈顶是否相同
			if (
				(char === "'" || char === '"' || char === "`") &&
				stack.length > 0 &&
				stack[stack.length - 1] === char
			) {
				stack.pop()
			} else {
				stack.push(char)
			}
		}
		// 处理闭符号
		else if (closeSymbols.has(char)) {
			if (stack.length > 0) {
				const lastOpen = stack[stack.length - 1]
				if (symbolPairs[lastOpen] === char) {
					stack.pop()
				}
			}
		}

		result += char
	}

	return result
}

export function postProcessCompletionSynthesis(
	completion: string,
	prefix: string,
	suffix: string,
	closeSymbolChar: string,
	suffixPrefixString: string,
): string {
	// TODO: 暂时注释掉截断到一行的实现，永远保持多行
	// 检查是否需要截断到一行
	// const lastLineBeforeCursor = prefix.split("\n").pop() || ""
	// const hasValidCharBeforeCursor = /\S/.test(lastLineBeforeCursor)

	// 如果换行前有有效字符，截断到一行
	// if (hasValidCharBeforeCursor) {
	// 	// 如果completion以换行符开头，先去掉开头的换行符和空白字符
	// 	let processedCompletion = completion
	// 	if (processedCompletion.startsWith("\n")) {
	// 		processedCompletion = processedCompletion.substring(1)
	// 		// 去掉开头的缩进空白，但保留第一个非空白字符后的内容
	// 		processedCompletion = processedCompletion.replace(/^\s*/, "")
	// 	}
	//
	// 	// 截断到第一个换行符
	// 	const firstLineIndex = processedCompletion.indexOf("\n")
	// 	if (firstLineIndex !== -1) {
	// 		// this.outputChannel.appendLine("截断多行补全到单行")
	// 		const truncatedCompletion = processedCompletion.substring(0, firstLineIndex)
	// 		completion = truncatedCompletion
	// 	} else {
	// 		completion = processedCompletion
	// 	}
	// }

	// 保持多行completion，只处理符号匹配和重叠部分
	if (closeSymbolChar && closeSymbolChar.length > 0) {
		let cutCompletion = getStringBeforeCloseSymbol(completion, closeSymbolChar)
		let commonSubstring = findMaxOverlap(cutCompletion, suffixPrefixString)
		completion = cutCompletion.slice(0, cutCompletion.length - commonSubstring.length)
	} else {
		let commonSubstring = findMaxOverlap(completion, suffix)
		if (commonSubstring.length >= 10) {
			completion = completion.slice(0, completion.length - commonSubstring.length)
		}
	}
	return completion
}

/**
 * 判断光标是否在单词内部
 * @param prefix 光标前的文本
 * @param suffix 光标后的文本
 * @param logCallback 可选的日志回调函数
 * @returns 如果在单词内部返回true，否则返回false
 */
export function isInsideWord(prefix: string, suffix: string, logCallback?: (message: string) => void): boolean {
	// 从 prefix 的末尾获取光标前的字符
	const charBefore = prefix.length > 0 ? prefix[prefix.length - 1] : ""
	// 从 suffix 的开头获取光标后的文本
	const charAfter = suffix.length > 0 ? suffix[0] : ""

	// 检查是否都是英文字母（大小写） - 使用字符编码判断
	const isLetter = (char: string): boolean => {
		if (char.length !== 1) return false
		const code = char.charCodeAt(0)
		return (
			(code >= 65 && code <= 90) || // A-Z
			(code >= 97 && code <= 122)
		) // a-z
	}

	const hasLetterBefore = isLetter(charBefore)
	const hasLetterAfter = isLetter(charAfter)

	// 如果前后都是字母，说明在单词内部
	if (hasLetterBefore && hasLetterAfter) {
		if (logCallback) {
			logCallback(`Skipping completion: cursor inside word (${charBefore}|${charAfter})`)
		}
		return true
	}

	return false
}
