// CTCode/src/autocompletion/v2/CodeSnippets.ts
// Default implementation of code snippets collection and related operations

import { ISnippetMeta, ICodeSnippets, IFileMeta } from "./types"

export class CodeSnippets implements ICodeSnippets {
	private _nextId = 0
	private _idMap: Map<number, ISnippetMeta> = new Map()

	private _byName: Map<string, Set<number>> = new Map()
	private _fileIndex: Map<string, { allIds: Set<number>; byName: Map<string, Set<number>> }> = new Map()

	// 基于结构化信息的索引
	private _byDefinitionType: Map<string, Set<number>> = new Map() // 按定义类型索引
	private _bySignature: Map<string, Set<number>> = new Map() // 按签名索引

	snippets: ISnippetMeta[] = []

	filesHash: Map<string, string> = new Map()

	private _addToIndices(id: number, snippet: ISnippetMeta) {
		// byName
		if (!this._byName.has(snippet.name)) this._byName.set(snippet.name, new Set())
		this._byName.get(snippet.name)!.add(id)

		// fileIndex
		if (!this._fileIndex.has(snippet.filePath)) {
			this._fileIndex.set(snippet.filePath, {
				allIds: new Set(),
				byName: new Map(),
			})
		}
		const bucket = this._fileIndex.get(snippet.filePath)!
		bucket.allIds.add(id)

		if (!bucket.byName.has(snippet.name)) bucket.byName.set(snippet.name, new Set())
		bucket.byName.get(snippet.name)!.add(id)

		// 结构化信息索引
		this._addToStructuredIndices(id, snippet)
	}

	private _removeFromIndices(id: number, snippet: ISnippetMeta) {
		// byName
		const nameSet = this._byName.get(snippet.name)
		if (nameSet) {
			nameSet.delete(id)
			if (nameSet.size === 0) this._byName.delete(snippet.name)
		}

		// fileIndex
		const bucket = this._fileIndex.get(snippet.filePath)
		if (bucket) {
			bucket.allIds.delete(id)
			const nameSetInFile = bucket.byName.get(snippet.name)
			if (nameSetInFile) {
				nameSetInFile.delete(id)
				if (nameSetInFile.size === 0) bucket.byName.delete(snippet.name)
			}
			if (bucket.allIds.size === 0) this._fileIndex.delete(snippet.filePath)
		}

		// 移除结构化信息索引
		this._removeFromStructuredIndices(id, snippet)
	}

	/**
	 * 添加结构化信息索引
	 */
	private _addToStructuredIndices(id: number, snippet: ISnippetMeta) {
		// 按定义类型索引
		if (snippet.definition?.type) {
			if (!this._byDefinitionType.has(snippet.definition.type)) {
				this._byDefinitionType.set(snippet.definition.type, new Set())
			}
			this._byDefinitionType.get(snippet.definition.type)!.add(id)
		}

		// 按签名索引
		if (snippet.signature) {
			if (!this._bySignature.has(snippet.signature)) {
				this._bySignature.set(snippet.signature, new Set())
			}
			this._bySignature.get(snippet.signature)!.add(id)
		}
	}

	/**
	 * 移除结构化信息索引
	 */
	private _removeFromStructuredIndices(id: number, snippet: ISnippetMeta) {
		// 按定义类型索引
		if (snippet.definition?.type) {
			const typeSet = this._byDefinitionType.get(snippet.definition.type)
			if (typeSet) {
				typeSet.delete(id)
				if (typeSet.size === 0) this._byDefinitionType.delete(snippet.definition.type)
			}
		}

		// 按签名索引
		if (snippet.signature) {
			const signatureSet = this._bySignature.get(snippet.signature)
			if (signatureSet) {
				signatureSet.delete(id)
				if (signatureSet.size === 0) this._bySignature.delete(snippet.signature)
			}
		}
	}

	/**
	 * 按名称查找单个片段
	 */
	async findByName(name: string): Promise<ISnippetMeta | undefined> {
		const results = await this.find({ name })
		return results.length > 0 ? results[0] : undefined
	}

	/**
	 * 按文件查找片段
	 */
	async findByFile(filePath: string): Promise<ISnippetMeta[]> {
		return await this.find({ filePath })
	}

	/**
	 * 按定义类型查找片段
	 */
	async findByDefinitionType(definitionType: string): Promise<ISnippetMeta[]> {
		const ids = this._byDefinitionType.get(definitionType) ?? new Set<number>()
		return Array.from(ids).map((id) => this._idMap.get(id)!)
	}

	/**
	 * 按签名查找片段
	 */
	async findBySignature(signature: string): Promise<ISnippetMeta[]> {
		const ids = this._bySignature.get(signature) ?? new Set<number>()
		return Array.from(ids).map((id) => this._idMap.get(id)!)
	}

	/**
	 * 查找具有特定参数类型的函数
	 */
	async findFunctionsWithParameterTypes(paramTypes: string[]): Promise<ISnippetMeta[]> {
		const results: ISnippetMeta[] = []

		for (const snippet of this.snippets) {
			if (snippet.parameters && snippet.parameters.length >= paramTypes.length) {
				let match = true
				for (let i = 0; i < paramTypes.length; i++) {
					if (snippet.parameters[i]?.type !== paramTypes[i]) {
						match = false
						break
					}
				}
				if (match) {
					results.push(snippet)
				}
			}
		}

		return results
	}

	async merge(codeSnippets: ICodeSnippets): Promise<void> {
		if (codeSnippets && codeSnippets.snippets) {
			await this.insert(codeSnippets.snippets)
		}
	}

	/**
	 * 获取统计信息
	 */
	getStats(): {
		totalSnippets: number
		fileCount: number
		typeDistribution: Record<string, number>
		definitionTypeDistribution: Record<string, number>
		avgLinesPerSnippet: number
	} {
		const typeDistribution: Record<string, number> = {}
		const definitionTypeDistribution: Record<string, number> = {}
		let totalLines = 0

		for (const snippet of this.snippets) {
			// 统计类型分布
			typeDistribution[snippet.type] = (typeDistribution[snippet.type] || 0) + 1

			// 统计定义类型分布
			if (snippet.definition?.type) {
				definitionTypeDistribution[snippet.definition.type] =
					(definitionTypeDistribution[snippet.definition.type] || 0) + 1
			}

			// 统计总行数
			totalLines += snippet.endLine - snippet.startLine + 1
		}

		const files = new Set(this.snippets.map((s) => s.filePath))

		return {
			totalSnippets: this.snippets.length,
			fileCount: files.size,
			typeDistribution,
			definitionTypeDistribution,
			avgLinesPerSnippet: this.snippets.length > 0 ? totalLines / this.snippets.length : 0,
		}
	}

	/**
	 * Find snippets by partial information
	 */
	async find(query: Partial<ISnippetMeta>): Promise<ISnippetMeta[]> {
		// filePath + name
		if (query.filePath && query.name) {
			const bucket = this._fileIndex.get(query.filePath)
			const ids = bucket?.byName.get(query.name) ?? new Set<number>()
			let res = Array.from(ids).map((id) => this._idMap.get(id)!)
			if (query.type) res = res.filter((s) => s.type === query.type)
			if (query.field) {
				res = res.filter((s) => s.field === query.field)
			} else {
				res = res.filter((s) => s.field === undefined || s.field === null || s.field === "")
			}
			return res
		}

		// name
		if (query.name) {
			const ids = this._byName.get(query.name) ?? new Set<number>()
			return Array.from(ids).map((id) => this._idMap.get(id)!)
		}

		// filePath
		if (query.filePath) {
			const bucket = this._fileIndex.get(query.filePath)
			const ids = bucket?.allIds ?? new Set<number>()
			return Array.from(ids).map((id) => this._idMap.get(id)!)
		}

		return Array.from(this._idMap.values())
	}

	/**
	 * Delete all snippets from a specific file
	 */
	async deleteByFile(filePath: string): Promise<ISnippetMeta[]> {
		const bucket = this._fileIndex.get(filePath)
		if (!bucket) return []

		const deleted: ISnippetMeta[] = []
		for (const id of Array.from(bucket.allIds)) {
			const snippet = this._idMap.get(id)!
			this._removeFromIndices(id, snippet)
			this._idMap.delete(id)
			deleted.push(snippet)
		}

		if (deleted.length) {
			this.snippets = this.snippets.filter((s) => s.filePath !== filePath)
		}
		return deleted
	}

	/**
	 * Insert snippets in batch
	 */
	async insert(snippets: ISnippetMeta[]): Promise<number> {
		for (const snippet of snippets) {
			const id = ++this._nextId
			this._idMap.set(id, snippet)
			this._addToIndices(id, snippet)
			this.snippets.push(snippet)

			// 更新文件hash映射
			if (snippet.fileHash && snippet.filePath) {
				this.filesHash.set(snippet.filePath, snippet.fileHash)
			}
		}
		return snippets.length
	}

	/**
	 * Update a single snippet
	 */
	async update(snippet: ISnippetMeta): Promise<boolean> {
		// 通过 filePath + name + type 精确定位
		if (snippet.filePath && snippet.name && snippet.type) {
			const bucket = this._fileIndex.get(snippet.filePath)
			if (!bucket) return false

			const candidateIds = bucket.byName.get(snippet.name) ?? new Set<number>()
			for (const id of candidateIds) {
				const old = this._idMap.get(id)!
				if (old.type === snippet.type) {
					// 找到匹配的片段，进行更新
					this._removeFromIndices(id, old)
					this._idMap.set(id, snippet)
					this._addToIndices(id, snippet)

					// 更新公开数组
					const idx = this.snippets.indexOf(old)
					if (idx > -1) this.snippets[idx] = snippet
					return true
				}
			}
		}

		// 如果 type 不匹配或信息不完整，尝试只通过 filePath + name 查找
		if (snippet.filePath && snippet.name) {
			const bucket = this._fileIndex.get(snippet.filePath)
			if (!bucket) return false

			const candidateIds = bucket.byName.get(snippet.name) ?? new Set<number>()
			if (candidateIds.size > 0) {
				// 取第一个匹配的片段进行更新
				const id = Array.from(candidateIds)[0]
				const old = this._idMap.get(id)!

				this._removeFromIndices(id, old)
				this._idMap.set(id, snippet)
				this._addToIndices(id, snippet)

				// 更新公开数组
				const idx = this.snippets.indexOf(old)
				if (idx > -1) this.snippets[idx] = snippet
				return true
			}
		}

		return false
	}

	async updateByFile(file: IFileMeta, snippets: CodeSnippets): Promise<boolean> {
		await this.deleteByFile(file.filePath)
		await this.merge(snippets)

		// 更新文件hash
		const fileHash = file.fileHash || (file as any).hash
		if (fileHash) {
			this.filesHash.set(file.filePath, fileHash)
		}

		return true
	}

	/**
	 * Clear all snippets
	 */
	clear(): void {
		this._nextId = 0
		this._idMap.clear()
		this._byName.clear()
		this._fileIndex.clear()
		this._byDefinitionType.clear()
		this._bySignature.clear()

		this.snippets = []
	}

	/**
	 * Filter snippets by condition
	 */
	async filter(predicate: (snippet: ISnippetMeta) => boolean): Promise<ICodeSnippets> {
		const result = new CodeSnippets()
		for (const snippet of this._idMap.values()) {
			if (predicate(snippet)) await result.insert([snippet])
		}
		return result as ICodeSnippets
	}

	/**
	 * Check if a code file's index is up to date
	 * @param file The file to check
	 * @returns true if the file needs to be updated (index is NOT latest), false if index is current
	 */
	isCodeFileLatestIndex(file: IFileMeta): boolean {
		const storedHash = this.filesHash.get(file.filePath)
		if (!storedHash) {
			// No stored hash means file is not indexed yet, needs update
			return true
		}

		// Compare current file hash with stored hash
		// If hashes don't match, file needs update
		const currentHash = file.fileHash || (file as any).hash
		return currentHash !== storedHash
	}

	getSnippets(): ISnippetMeta[] {
		return this.snippets
	}
}
