import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

export async function extractSnippetsFromCapturesForCPP(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForCPP]")

	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 收集所有 class/struct/union/enum captures 用于确定 field 和 method 的归属
	const containerCaptures = captures
		.filter(
			(c) =>
				c.name === "definition.class" ||
				c.name === "definition.struct" ||
				c.name === "definition.union" ||
				c.name === "definition.enum" ||
				c.name === "definition.template",
		)
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			name: c.name,
			nameCap: findNameCaptureForContainer(c, captures, sourceCode),
		}))
		.filter((c) => c.nameCap) // 只保留有名称的containers

	// 处理 include declarations (imports)
	if (snippetTypes.includes(SnippetType.ImportOrInclude)) {
		captures.forEach((capture) => {
			if (capture.name === "definition.include") {
				const node = capture.node
				const startLine = node.startPosition.row + 1
				const endLine = node.endPosition.row + 1
				const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
				const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

				if (!seenHashes.has(snippetHash)) {
					seenHashes.add(snippetHash)
					const scope = buildScopeChain(node)

					const includeNameCapture = captures.find(
						(c) =>
							c.name === "name.definition.include" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)
					const includeName = includeNameCapture
						? sourceCode.substring(includeNameCapture.node.startIndex, includeNameCapture.node.endIndex)
						: ""

					snippets.push({
						type: SnippetType.ImportOrInclude,
						name: includeName,
						rangeText,
						startLine,
						endLine,
						filePath,
						fileHash,
						scope,
						language: CodeLanguageType.CPP,
					})
				}
			}
		})
	}

	// 处理 function definitions and declarations
	if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
		const functionCaptures = captures.filter((c) => c.name === "definition.function")

		functionCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const functionNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.function" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const functionName = functionNameCapture
					? sourceCode.substring(functionNameCapture.node.startIndex, functionNameCapture.node.endIndex)
					: ""

				const parametersCapture = captures.find(
					(c) =>
						c.name === "parameters.definition.function" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const parameters = parametersCapture
					? extractParametersFromNode(parametersCapture.node, sourceCode)
					: []

				const bodyCapture = captures.find(
					(c) =>
						c.name === "body.definition.function" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const hasBody = !!bodyCapture

				snippets.push({
					type: SnippetType.FunctionOrMethod,
					name: functionName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					parameters,
					definition: {
						name: functionName,
						type: "function",
						parameters,
					},
				})
			}
		})
	}

	// 处理 method definitions and declarations
	if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
		const methodCaptures = captures.filter((c) => c.name === "definition.method")

		methodCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const methodNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.method" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const methodName = methodNameCapture
					? sourceCode.substring(methodNameCapture.node.startIndex, methodNameCapture.node.endIndex)
					: ""

				const parametersCapture = captures.find(
					(c) =>
						c.name === "parameters.definition.method" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const parameters = parametersCapture
					? extractParametersFromNode(parametersCapture.node, sourceCode)
					: []

				const bodyCapture = captures.find(
					(c) =>
						c.name === "body.definition.method" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const hasBody = !!bodyCapture

				// 查找归属的类或结构体
				const parentContainer = findParentContainer(node, containerCaptures)
				const field = parentContainer?.nameCap || undefined

				snippets.push({
					type: SnippetType.FunctionOrMethod,
					name: methodName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					parameters,
					field,
					definition: {
						name: methodName,
						type: "method",
						parameters,
					},
				})
			}
		})
	}

	// 处理 constructor definitions
	if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
		const constructorCaptures = captures.filter((c) => c.name === "definition.constructor")

		constructorCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const constructorNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.constructor" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const constructorName = constructorNameCapture
					? sourceCode.substring(constructorNameCapture.node.startIndex, constructorNameCapture.node.endIndex)
					: ""

				const parametersCapture = captures.find(
					(c) =>
						c.name === "parameters.definition.constructor" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const parameters = parametersCapture
					? extractParametersFromNode(parametersCapture.node, sourceCode)
					: []

				// 查找归属的类
				const parentContainer = findParentContainer(node, containerCaptures)
				const field = parentContainer?.nameCap || undefined

				snippets.push({
					type: SnippetType.FunctionOrMethod,
					name: constructorName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					parameters,
					field,
					definition: {
						name: constructorName,
						type: "constructor",
						parameters,
					},
				})
			}
		})
	}

	// 处理 destructor definitions
	if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
		const destructorCaptures = captures.filter((c) => c.name === "definition.destructor")

		destructorCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const destructorNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.destructor" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const destructorName = destructorNameCapture
					? sourceCode.substring(destructorNameCapture.node.startIndex, destructorNameCapture.node.endIndex)
					: ""

				// 查找归属的类
				const parentContainer = findParentContainer(node, containerCaptures)
				const field = parentContainer?.nameCap || undefined

				snippets.push({
					type: SnippetType.FunctionOrMethod,
					name: destructorName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					field,
					definition: {
						name: destructorName,
						type: "destructor",
						parameters: [],
					},
				})
			}
		})
	}

	// 处理 operator overloads
	if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
		const operatorCaptures = captures.filter((c) => c.name === "definition.operator")

		operatorCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const operatorNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.operator" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const operatorName = operatorNameCapture
					? sourceCode.substring(operatorNameCapture.node.startIndex, operatorNameCapture.node.endIndex)
					: ""

				const parametersCapture = captures.find(
					(c) =>
						c.name === "parameters.definition.operator" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const parameters = parametersCapture
					? extractParametersFromNode(parametersCapture.node, sourceCode)
					: []

				// 查找归属的类
				const parentContainer = findParentContainer(node, containerCaptures)
				const field = parentContainer?.nameCap || undefined

				snippets.push({
					type: SnippetType.FunctionOrMethod,
					name: operatorName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					parameters,
					field,
					definition: {
						name: operatorName,
						type: "operator",
						parameters,
					},
				})
			}
		})
	}

	// 处理 class definitions (排除模板内的class)
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const classCaptures = captures.filter((c) => c.name === "definition.class")
		const templateCaptures = captures.filter((c) => c.name === "definition.template")

		classCaptures.forEach((capture) => {
			const node = capture.node

			// 检查是否被template包含
			const isWithinTemplate = templateCaptures.some((templateCapture) => {
				return (
					templateCapture.node.startIndex <= node.startIndex && node.endIndex <= templateCapture.node.endIndex
				)
			})

			// 如果在template内，跳过（由template处理）
			if (isWithinTemplate) {
				return
			}

			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const classNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.class" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const className = classNameCapture
					? sourceCode.substring(classNameCapture.node.startIndex, classNameCapture.node.endIndex)
					: ""

				const fields = extractFieldsFromContainerNode(node, sourceCode, captures)

				snippets.push({
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					name: className,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: className,
						type: "class",
					},
				})
			}
		})
	}

	// 处理 struct definitions
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const structCaptures = captures.filter((c) => c.name === "definition.struct")

		structCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const structNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.struct" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const structName = structNameCapture
					? sourceCode.substring(structNameCapture.node.startIndex, structNameCapture.node.endIndex)
					: ""

				const fields = extractFieldsFromContainerNode(node, sourceCode, captures)

				snippets.push({
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					name: structName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: structName,
						type: "struct",
					},
				})
			}
		})
	}

	// 处理 union definitions
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const unionCaptures = captures.filter((c) => c.name === "definition.union")

		unionCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const unionNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.union" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const unionName = unionNameCapture
					? sourceCode.substring(unionNameCapture.node.startIndex, unionNameCapture.node.endIndex)
					: ""

				const fields = extractFieldsFromContainerNode(node, sourceCode, captures)

				snippets.push({
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					name: unionName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: unionName,
						type: "union",
					},
				})
			}
		})
	}

	// 处理 enum definitions
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const enumCaptures = captures.filter((c) => c.name === "definition.enum")

		enumCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const enumNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.enum" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const enumName = enumNameCapture
					? sourceCode.substring(enumNameCapture.node.startIndex, enumNameCapture.node.endIndex)
					: ""

				const enumerators = extractEnumeratorsFromEnumNode(node, sourceCode, captures)

				snippets.push({
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					name: enumName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: enumName,
						type: "enum",
					},
				})
			}
		})
	}

	// 处理 template definitions
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const templateCaptures = captures.filter((c) => c.name === "definition.template")

		templateCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				// 查找模板的名称（可能是类、结构体或函数）
				const templateClassNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.template.class" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const templateStructNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.template.struct" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const templateFunctionNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.template.function" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)

				let templateName = ""
				let templateType = "template"
				if (templateClassNameCapture) {
					templateName = sourceCode.substring(
						templateClassNameCapture.node.startIndex,
						templateClassNameCapture.node.endIndex,
					)
					templateType = "template_class"
				} else if (templateStructNameCapture) {
					templateName = sourceCode.substring(
						templateStructNameCapture.node.startIndex,
						templateStructNameCapture.node.endIndex,
					)
					templateType = "template_struct"
				} else if (templateFunctionNameCapture) {
					templateName = sourceCode.substring(
						templateFunctionNameCapture.node.startIndex,
						templateFunctionNameCapture.node.endIndex,
					)
					templateType = "template_function"
				}

				const parametersCapture = captures.find(
					(c) =>
						c.name === "parameters.definition.template" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const templateParameters = parametersCapture
					? extractTemplateParametersFromNode(parametersCapture.node, sourceCode)
					: []

				if (templateType === "template_function") {
					// 模板函数归类为函数
					if (snippetTypes.includes(SnippetType.FunctionOrMethod)) {
						const functionParametersCapture = captures.find(
							(c) =>
								c.name === "parameters.definition.template.function" &&
								c.node.startIndex >= node.startIndex &&
								c.node.endIndex <= node.endIndex,
						)
						const functionParameters = functionParametersCapture
							? extractParametersFromNode(functionParametersCapture.node, sourceCode)
							: []

						snippets.push({
							type: SnippetType.FunctionOrMethod,
							name: templateName,
							rangeText,
							startLine,
							endLine,
							filePath,
							fileHash,
							scope,
							language: CodeLanguageType.CPP,
							parameters: functionParameters,
							definition: {
								name: templateName,
								type: templateType,
								parameters: functionParameters,
							},
						})
					}
				} else {
					// 模板类/结构体归类为类
					snippets.push({
						type: SnippetType.ClassOrInterfaceOrStructOrEnum,
						name: templateName,
						rangeText,
						startLine,
						endLine,
						filePath,
						fileHash,
						scope,
						language: CodeLanguageType.CPP,
						definition: {
							name: templateName,
							type: templateType,
						},
					})
				}
			}
		})
	}

	// 处理 namespace definitions
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const namespaceCaptures = captures.filter((c) => c.name === "definition.namespace")

		namespaceCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const namespaceNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.namespace" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const namespaceName = namespaceNameCapture
					? sourceCode.substring(namespaceNameCapture.node.startIndex, namespaceNameCapture.node.endIndex)
					: "<anonymous>"

				snippets.push({
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					name: namespaceName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: namespaceName,
						type: "namespace",
					},
				})
			}
		})
	}

	// 处理 typedef definitions
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const typedefCaptures = captures.filter((c) => c.name === "definition.typedef")

		typedefCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const typedefNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.typedef" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const typedefName = typedefNameCapture
					? sourceCode.substring(typedefNameCapture.node.startIndex, typedefNameCapture.node.endIndex)
					: ""

				snippets.push({
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					name: typedefName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: typedefName,
						type: "typedef",
					},
				})
			}
		})
	}

	// 处理 using declarations
	if (snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
		const usingCaptures = captures.filter((c) => c.name === "definition.using")

		usingCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const usingNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.using" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const usingName = usingNameCapture
					? sourceCode.substring(usingNameCapture.node.startIndex, usingNameCapture.node.endIndex)
					: ""

				snippets.push({
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					name: usingName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: usingName,
						type: "using",
					},
				})
			}
		})
	}

	// 处理 global variables
	if (snippetTypes.includes(SnippetType.VariableOrConstant)) {
		const variableCaptures = captures.filter((c) => c.name === "definition.variable")

		variableCaptures.forEach((capture) => {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)

			if (!seenHashes.has(snippetHash)) {
				seenHashes.add(snippetHash)
				const scope = buildScopeChain(node)

				const variableNameCapture = captures.find(
					(c) =>
						c.name === "name.definition.variable" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)
				const variableName = variableNameCapture
					? sourceCode.substring(variableNameCapture.node.startIndex, variableNameCapture.node.endIndex)
					: ""

				snippets.push({
					type: SnippetType.VariableOrConstant,
					name: variableName,
					rangeText,
					startLine,
					endLine,
					filePath,
					fileHash,
					scope,
					language: CodeLanguageType.CPP,
					definition: {
						name: variableName,
						type: "variable",
					},
				})
			}
		})
	}

	logger.info(`Extracted ${snippets.length} C++ snippets`)
	return snippets
}

// 辅助函数：提取函数参数
function extractParametersFromNode(node: Parser.SyntaxNode, sourceCode: string): ParameterInfo[] {
	const parameters: ParameterInfo[] = []

	// 遍历参数列表中的每个参数
	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child && child.type === "parameter_declaration") {
			const parameterText = sourceCode.substring(child.startIndex, child.endIndex)
			const parameterName = extractParameterName(child, sourceCode)

			parameters.push({
				name: parameterName,
				type: parameterText,
			})
		}
	}

	return parameters
}

// 辅助函数：提取参数名称
function extractParameterName(node: Parser.SyntaxNode, sourceCode: string): string {
	// 查找参数声明中的标识符
	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child && child.type === "identifier") {
			return sourceCode.substring(child.startIndex, child.endIndex)
		}
	}
	return ""
}

// 辅助函数：提取模板参数
function extractTemplateParametersFromNode(node: Parser.SyntaxNode, sourceCode: string): ParameterInfo[] {
	const parameters: ParameterInfo[] = []

	// 遍历模板参数列表中的每个参数
	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child && (child.type === "type_parameter_declaration" || child.type === "parameter_declaration")) {
			const parameterText = sourceCode.substring(child.startIndex, child.endIndex)
			const parameterName = extractTemplateParameterName(child, sourceCode)

			parameters.push({
				name: parameterName,
				type: parameterText,
			})
		}
	}

	return parameters
}

// 辅助函数：提取模板参数名称
function extractTemplateParameterName(node: Parser.SyntaxNode, sourceCode: string): string {
	// 查找模板参数声明中的标识符
	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child && (child.type === "type_identifier" || child.type === "identifier")) {
			return sourceCode.substring(child.startIndex, child.endIndex)
		}
	}
	return ""
}

// 辅助函数：提取结构体/类/联合体字段
function extractFieldsFromContainerNode(
	node: Parser.SyntaxNode,
	sourceCode: string,
	captures: Parser.QueryCapture[],
): string[] {
	const fields: string[] = []

	// 查找该容器内的所有字段
	const fieldCaptures = captures.filter(
		(c) =>
			c.name === "name.definition.field" &&
			c.node.startIndex >= node.startIndex &&
			c.node.endIndex <= node.endIndex,
	)

	fieldCaptures.forEach((capture) => {
		const fieldName = sourceCode.substring(capture.node.startIndex, capture.node.endIndex)
		fields.push(fieldName)
	})

	return fields
}

// 辅助函数：提取枚举值
function extractEnumeratorsFromEnumNode(
	node: Parser.SyntaxNode,
	sourceCode: string,
	captures: Parser.QueryCapture[],
): string[] {
	const enumerators: string[] = []

	// 查找该枚举内的所有枚举值
	const enumeratorCaptures = captures.filter(
		(c) =>
			c.name === "name.definition.enumerator" &&
			c.node.startIndex >= node.startIndex &&
			c.node.endIndex <= node.endIndex,
	)

	enumeratorCaptures.forEach((capture) => {
		const enumeratorName = sourceCode.substring(capture.node.startIndex, capture.node.endIndex)
		enumerators.push(enumeratorName)
	})

	return enumerators
}

// 辅助函数：查找父容器
function findParentContainer(
	node: Parser.SyntaxNode,
	containerCaptures: Array<{
		node: Parser.SyntaxNode
		start: number
		end: number
		name: string
		nameCap: string | null
	}>,
): { node: Parser.SyntaxNode; start: number; end: number; name: string; nameCap: string | null } | null {
	for (const container of containerCaptures) {
		if (container.start <= node.startIndex && node.endIndex <= container.end) {
			return container
		}
	}
	return null
}

// 辅助函数：根据容器类型生成对应的名称capture名称
function getNameCaptureNameForContainer(containerCaptureName: string): string {
	switch (containerCaptureName) {
		case "definition.class":
			return "name.definition.class"
		case "definition.struct":
			return "name.definition.struct"
		case "definition.union":
			return "name.definition.union"
		case "definition.enum":
			return "name.definition.enum"
		case "definition.template":
			return "name.definition.template.class" // 默认查找类模板
		default:
			return containerCaptureName
	}
}

// 辅助函数：查找容器的名称capture
function findNameCaptureForContainer(
	containerCapture: Parser.QueryCapture,
	captures: Parser.QueryCapture[],
	sourceCode: string,
): string | null {
	const node = containerCapture.node
	const nameCaptureName = getNameCaptureNameForContainer(containerCapture.name)

	// 对于模板，可能需要查找不同类型的名称
	if (containerCapture.name === "definition.template") {
		const templateClassNameCapture = captures.find(
			(c) =>
				c.name === "name.definition.template.class" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (templateClassNameCapture) {
			return sourceCode.substring(
				templateClassNameCapture.node.startIndex,
				templateClassNameCapture.node.endIndex,
			)
		}

		const templateStructNameCapture = captures.find(
			(c) =>
				c.name === "name.definition.template.struct" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (templateStructNameCapture) {
			return sourceCode.substring(
				templateStructNameCapture.node.startIndex,
				templateStructNameCapture.node.endIndex,
			)
		}

		const templateFunctionNameCapture = captures.find(
			(c) =>
				c.name === "name.definition.template.function" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (templateFunctionNameCapture) {
			return sourceCode.substring(
				templateFunctionNameCapture.node.startIndex,
				templateFunctionNameCapture.node.endIndex,
			)
		}
	}

	const nameCapture = captures.find(
		(c) => c.name === nameCaptureName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
	)

	return nameCapture ? sourceCode.substring(nameCapture.node.startIndex, nameCapture.node.endIndex) : null
}

// 用于构建概览的函数
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): any {
	const summary = {
		includes: [] as string[],
		namespaces: [] as string[],
		classes: [] as string[],
		structs: [] as string[],
		unions: [] as string[],
		enums: [] as string[],
		templates: [] as string[],
		functions: [] as string[],
		methods: [] as string[],
		constructors: [] as string[],
		destructors: [] as string[],
		operators: [] as string[],
		variables: [] as string[],
		typedefs: [] as string[],
		usings: [] as string[],
	}

	snippets.forEach((snippet) => {
		switch (snippet.type) {
			case SnippetType.ImportOrInclude:
				summary.includes.push(snippet.name)
				break
			case SnippetType.FunctionOrMethod:
				if (snippet.definition?.type === "function") {
					summary.functions.push(snippet.name)
				} else if (snippet.definition?.type === "method") {
					summary.methods.push(snippet.name)
				} else if (snippet.definition?.type === "constructor") {
					summary.constructors.push(snippet.name)
				} else if (snippet.definition?.type === "destructor") {
					summary.destructors.push(snippet.name)
				} else if (snippet.definition?.type === "operator") {
					summary.operators.push(snippet.name)
				} else if (snippet.definition?.type === "template_function") {
					summary.functions.push(snippet.name)
				}
				break
			case SnippetType.ClassOrInterfaceOrStructOrEnum:
				if (snippet.definition?.type === "class") {
					summary.classes.push(snippet.name)
				} else if (snippet.definition?.type === "struct") {
					summary.structs.push(snippet.name)
				} else if (snippet.definition?.type === "union") {
					summary.unions.push(snippet.name)
				} else if (snippet.definition?.type === "enum") {
					summary.enums.push(snippet.name)
				} else if (
					snippet.definition?.type === "template_class" ||
					snippet.definition?.type === "template_struct"
				) {
					summary.templates.push(snippet.name)
				} else if (snippet.definition?.type === "namespace") {
					summary.namespaces.push(snippet.name)
				} else if (snippet.definition?.type === "typedef") {
					summary.typedefs.push(snippet.name)
				} else if (snippet.definition?.type === "using") {
					summary.usings.push(snippet.name)
				}
				break
			case SnippetType.VariableOrConstant:
				summary.variables.push(snippet.name)
				break
		}
	})

	return summary
}

// 用于格式化概览文本的函数
export function formatOutlineText(summary: any): string {
	let outline = ""

	if (summary.includes.length > 0) {
		outline += `Includes: ${summary.includes.join(", ")}\n`
	}

	if (summary.namespaces.length > 0) {
		outline += `Namespaces: ${summary.namespaces.join(", ")}\n`
	}

	if (summary.classes.length > 0) {
		outline += `Classes: ${summary.classes.join(", ")}\n`
	}

	if (summary.structs.length > 0) {
		outline += `Structs: ${summary.structs.join(", ")}\n`
	}

	if (summary.unions.length > 0) {
		outline += `Unions: ${summary.unions.join(", ")}\n`
	}

	if (summary.enums.length > 0) {
		outline += `Enums: ${summary.enums.join(", ")}\n`
	}

	if (summary.templates.length > 0) {
		outline += `Templates: ${summary.templates.join(", ")}\n`
	}

	if (summary.functions.length > 0) {
		outline += `Functions: ${summary.functions.join(", ")}\n`
	}

	if (summary.methods.length > 0) {
		outline += `Methods: ${summary.methods.join(", ")}\n`
	}

	if (summary.constructors.length > 0) {
		outline += `Constructors: ${summary.constructors.join(", ")}\n`
	}

	if (summary.destructors.length > 0) {
		outline += `Destructors: ${summary.destructors.join(", ")}\n`
	}

	if (summary.operators.length > 0) {
		outline += `Operators: ${summary.operators.join(", ")}\n`
	}

	if (summary.variables.length > 0) {
		outline += `Variables: ${summary.variables.join(", ")}\n`
	}

	if (summary.typedefs.length > 0) {
		outline += `Typedefs: ${summary.typedefs.join(", ")}\n`
	}

	if (summary.usings.length > 0) {
		outline += `Usings: ${summary.usings.join(", ")}\n`
	}

	return outline
}
