import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

export async function extractSnippetsFromCapturesForSwift(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForSwift]")

	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 收集所有类/结构体/协议/扩展captures用于确定field
	const containerCaptures = captures
		.filter(
			(c) =>
				c.name === "definition.class" ||
				c.name === "definition.struct" ||
				c.name === "definition.protocol" ||
				c.name === "definition.enum",
		)
		.map((c) => {
			const node = c.node
			const type = c.name.split(".")[1] // class, struct, protocol, enum

			// 查找名称capture
			let nameCap = captures.find(
				(nc) =>
					nc.name === `name.${c.name}` &&
					nc.node.startIndex >= node.startIndex &&
					nc.node.endIndex <= node.endIndex,
			)

			// 特殊处理extension：获取被扩展的类型名
			let actualName = nameCap ? sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex) : ""
			if (type === "class") {
				const actualType = determineClassDeclarationType(node, sourceCode)
				if (actualType === "extension") {
					// 对于extension，获取被扩展的类型名（第二个子节点的type_identifier）
					if (node.childCount >= 2) {
						const userTypeNode = node.child(1) // user_type节点
						if (userTypeNode && userTypeNode.type === "user_type" && userTypeNode.childCount > 0) {
							const typeIdNode = userTypeNode.child(0)
							if (typeIdNode && typeIdNode.type === "type_identifier") {
								actualName = sourceCode.substring(typeIdNode.startIndex, typeIdNode.endIndex)
							}
						}
					}
				}
			}

			return {
				node,
				start: node.startIndex,
				end: node.endIndex,
				type,
				nameCap,
				actualName, // 实际的类型名（对于extension是被扩展的类型）
			}
		})

	// 处理import语句
	captures.forEach((capture) => {
		if (capture.name === "name.definition.import") {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) return
			seenHashes.add(snippetHash)

			const name = sourceCode.substring(node.startIndex, node.endIndex)
			const scope = buildScopeChain(node)

			snippets.push({
				name,
				type: SnippetType.ImportOrInclude,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				scope,
				fileHash,
			})
		}
	})

	// 处理函数/方法定义
	for (const capture of captures) {
		if (
			capture.name !== "definition.method" &&
			capture.name !== "definition.initializer" &&
			capture.name !== "definition.deinitializer" &&
			capture.name !== "definition.protocol_method"
		)
			continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 查找函数/方法名
		let nameCap: Parser.QueryCapture | undefined
		if (capture.name === "definition.method") {
			nameCap = captures.find(
				(c) =>
					c.name === "name.definition.method" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
		} else if (capture.name === "definition.protocol_method") {
			nameCap = captures.find(
				(c) =>
					c.name === "name.definition.protocol_method" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
		} else if (capture.name === "definition.initializer") {
			nameCap = captures.find(
				(c) =>
					c.name === "name.definition.initializer" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
		} else if (capture.name === "definition.deinitializer") {
			nameCap = captures.find(
				(c) =>
					c.name === "name.definition.deinitializer" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
		}

		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// 查找参数 - 为Swift实现基本的参数解析
		const parameters: ParameterInfo[] = []

		// 查找函数声明中的参数列表
		// Swift函数参数在 parameter_list 节点中
		let parameterNodes: Parser.SyntaxNode[] = []
		function findParameterNodes(node: Parser.SyntaxNode) {
			if (node.type === "parameter") {
				parameterNodes.push(node)
			}
			for (let i = 0; i < node.childCount; i++) {
				const child = node.child(i)
				if (child) {
					findParameterNodes(child)
				}
			}
		}
		findParameterNodes(node)

		// 解析每个参数
		for (const paramNode of parameterNodes) {
			// 查找参数名 - Swift参数可能有external name和internal name
			let paramName = ""
			for (let i = 0; i < paramNode.childCount; i++) {
				const child = paramNode.child(i)
				if (child && child.type === "simple_identifier") {
					paramName = sourceCode.substring(child.startIndex, child.endIndex)
					break
				}
			}

			if (paramName) {
				parameters.push({
					name: paramName,
					type: "Any", // 暂时使用占位符类型
				})
			}
		}

		// 查找方法体
		const bodyCap = captures.find(
			(c) =>
				c.name === "body.definition.method" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)

		let rangeText: string
		let implementText: string | undefined = undefined

		if (bodyCap) {
			// 获取方法签名（不包括方法体）
			rangeText = sourceCode.substring(node.startIndex, bodyCap.node.startIndex).trimEnd()
			implementText = sourceCode.substring(node.startIndex, node.endIndex)
		} else {
			rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		}

		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		// 查找返回类型 - 暂时跳过
		let returnType = ""

		const paramStr = parameters.map((p) => p.name).join(", ")
		const signature = `${name}(${paramStr})`
		const scope = buildScopeChain(node)

		// 确定是否属于某个类/结构体/协议（设置field）
		let field: string | undefined = undefined
		let closestContainer: (typeof containerCaptures)[0] | undefined = undefined

		// 找到最小的包含当前节点的容器
		for (const container of containerCaptures) {
			if (node.startIndex > container.start && node.endIndex <= container.end) {
				if (
					!closestContainer ||
					container.end - container.start < closestContainer.end - closestContainer.start
				) {
					closestContainer = container
				}
			}
		}

		if (closestContainer) {
			// 对于extension，使用被扩展的类型名；否则使用容器的名称
			field =
				closestContainer.actualName ||
				(closestContainer.nameCap
					? sourceCode.substring(
							closestContainer.nameCap.node.startIndex,
							closestContainer.nameCap.node.endIndex,
						)
					: undefined)
		}

		snippets.push({
			name,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			definition: {
				name,
				type: node.type,
				parameters,
				returnType,
			},
			parameters,
			signature,
			language: CodeLanguageType.Swift,
			...(implementText ? { implementText } : {}),
		})
	}

	// 处理属性定义（包含计算属性）
	for (const capture of captures) {
		if (capture.name !== "definition.property" && capture.name !== "definition.computed_property") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 检查是否已经处理过这个节点（防止重复处理同一个属性）
		const nodeHash = `${node.startIndex}-${node.endIndex}`
		const isAlreadyProcessed = snippets.some(
			(s) => s.rangeText === sourceCode.substring(node.startIndex, node.endIndex) && s.startLine === startLine,
		)
		if (isAlreadyProcessed) continue

		// 查找属性名，优先使用computed_property的名称
		let nameCap: Parser.QueryCapture | undefined
		let isComputedProperty = false

		// 首先检查是否有computed_property名称capture
		nameCap = captures.find(
			(c) =>
				c.name === "name.definition.computed_property" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)

		if (nameCap) {
			isComputedProperty = true
		} else {
			// 如果没有，使用普通property名称capture
			nameCap = captures.find(
				(c) =>
					c.name === "name.definition.property" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)
		}

		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// 判断是否在容器内部
		const isInContainer = containerCaptures.some(
			(container) => node.startIndex > container.start && node.endIndex <= container.end,
		)

		if (!isInContainer) continue // 只处理类/结构体内的属性

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		// 确定属于哪个容器
		let field: string | undefined = undefined
		let closestContainer: (typeof containerCaptures)[0] | undefined = undefined

		// 找到最小的包含当前节点的容器
		for (const container of containerCaptures) {
			if (node.startIndex > container.start && node.endIndex <= container.end) {
				if (
					!closestContainer ||
					container.end - container.start < closestContainer.end - closestContainer.start
				) {
					closestContainer = container
				}
			}
		}

		if (closestContainer) {
			// 对于extension，使用被扩展的类型名；否则使用容器的名称
			field =
				closestContainer.actualName ||
				(closestContainer.nameCap
					? sourceCode.substring(
							closestContainer.nameCap.node.startIndex,
							closestContainer.nameCap.node.endIndex,
						)
					: undefined)
		}

		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			implementText: rangeText,
			scope,
			fileHash,
			field,
			definition: {
				name,
				type: isComputedProperty ? "computed_property" : "property",
			},
			language: CodeLanguageType.Swift,
		})
	}

	// 处理全局变量和常量
	for (const capture of captures) {
		if (capture.name !== "definition.global_var") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 查找变量名
		const nameCap = captures.find(
			(c) =>
				c.name === "name.definition.global_var" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)

		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		// 判断是let还是var
		let varType = "var"
		if (rangeText.trim().startsWith("let")) {
			varType = "let"
		}

		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			implementText: rangeText,
			scope,
			fileHash,
			field: "",
			definition: { name, type: varType },
			language: CodeLanguageType.Swift,
		})
	}

	// 处理类/结构体/协议/枚举定义
	const typeDefs = [
		{ capture: "name.definition.class", outlineType: "class" },
		{ capture: "name.definition.protocol", outlineType: "protocol" },
		{ capture: "name.definition.type_alias", outlineType: "typealias" },
	]

	for (const { capture, outlineType } of typeDefs) {
		const defCaptureName = `definition.${outlineType === "typealias" ? "type_alias" : outlineType}`

		for (const cap of captures.filter((c) => c.name === capture)) {
			const node = cap.node

			// 判断是否在函数/方法内
			const isInFunc = captures.some(
				(fc) =>
					(fc.name === "definition.method" ||
						fc.name === "definition.initializer" ||
						fc.name === "definition.deinitializer") &&
					fc.node.startIndex < node.startIndex &&
					node.endIndex < fc.node.endIndex,
			)
			if (isInFunc) continue

			// 优先使用@definition.xxx区间 - 找到最具体（最小）的包含此名称的定义
			let defCap = captures
				.filter(
					(c) =>
						c.name === defCaptureName &&
						c.node.startIndex <= node.startIndex &&
						node.endIndex <= c.node.endIndex,
				)
				.sort((a, b) => a.node.endIndex - a.node.startIndex - (b.node.endIndex - b.node.startIndex))[0]

			const rangeText = defCap
				? sourceCode.substring(defCap.node.startIndex, defCap.node.endIndex)
				: sourceCode.substring(node.startIndex, node.endIndex)

			// 对于class_declaration，使用AST检查确定真实类型
			let actualType = outlineType
			if (outlineType === "class" && defCap) {
				actualType = determineClassDeclarationType(defCap.node, sourceCode)
			}

			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)

			const name = sourceCode.substring(node.startIndex, node.endIndex)

			snippets.push({
				name,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				implementText: rangeText,
				scope: [],
				fileHash,
				field: "",
				definition: { name, type: actualType },
				language: CodeLanguageType.Swift,
			})
		}
	}

	// 根据snippetTypes过滤并排序
	return snippets.filter((snippet) => snippetTypes.includes(snippet.type)).sort((a, b) => a.startLine - b.startLine)
}

/**
 * 将文本中的换行符替换为空格，提高可读性
 */
function normalizeTextForSummary(text: string): string {
	return text.split("\n").join(" ")
}

/**
 * 确定class_declaration的实际类型（class、struct、enum、extension）
 */
function determineClassDeclarationType(node: Parser.SyntaxNode, sourceCode: string): string {
	// 检查第一个子节点来确定类型
	if (node.childCount > 0) {
		const firstChild = node.child(0)
		if (firstChild) {
			const firstChildType = firstChild.type
			if (firstChildType === "struct") return "struct"
			if (firstChildType === "enum") return "enum"
			if (firstChildType === "class") return "class"
			if (firstChildType === "extension") return "extension"
		}
	}
	// 默认返回class
	return "class"
}
function parseSwiftParameters(paramText: string): string[] {
	if (!paramText.trim()) return []

	const params: string[] = []
	let current = ""
	let parenDepth = 0
	let bracketDepth = 0
	let inString = false
	let stringChar = ""

	for (let i = 0; i < paramText.length; i++) {
		const char = paramText[i]

		if (!inString) {
			if (char === '"' || char === "'") {
				inString = true
				stringChar = char
			} else if (char === "(") {
				parenDepth++
			} else if (char === ")") {
				parenDepth--
			} else if (char === "[") {
				bracketDepth++
			} else if (char === "]") {
				bracketDepth--
			} else if (char === "," && parenDepth === 0 && bracketDepth === 0) {
				if (current.trim()) {
					params.push(current.trim())
				}
				current = ""
				continue
			}
		} else {
			if (char === stringChar && paramText[i - 1] !== "\\") {
				inString = false
				stringChar = ""
			}
		}

		current += char
	}

	if (current.trim()) {
		params.push(current.trim())
	}

	return params
}

// VSCode Outline Entry 类型
export interface OutlineEntry {
	type: string
	name: string
	file: string // 表示当前文件
	field?: string // 空字符串代表当前文件的global
	description?: string
}

/**
 * 根据Swift文件的所有snippet生成大纲（Outline）
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): OutlineEntry[] {
	const outline: OutlineEntry[] = []

	// 只保留全局定义（scope为空或全为""）
	const isGlobal = (s: ISnippetMeta) => !s.scope || s.scope.length === 0 || s.scope.every((x) => !x)

	for (const s of snippets) {
		if (!isGlobal(s)) continue

		if (s.type === SnippetType.ImportOrInclude) {
			outline.push({
				type: "import",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.FunctionOrMethod) {
			if (s.field) {
				outline.push({
					type: "method",
					name: s.name,
					file: s.filePath,
					field: s.field,
					description: s.definitionText,
				})
			} else {
				outline.push({
					type: "function",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.definitionText,
				})
			}
		} else if (s.type === SnippetType.VariableOrConstant) {
			const t = s.definition?.type || "var"
			outline.push({
				type: t,
				name: s.name,
				file: s.filePath,
				field: s.field || "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.ClassOrInterfaceOrStructOrEnum) {
			const t = s.definition?.type || "type"
			outline.push({
				type: t,
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.name,
			})
		}
	}

	return outline
}

/**
 * 将OutlineEntry[]格式化为文本，同一个field的放在一起。
 */
export function formatOutlineText(outline: OutlineEntry[]): string {
	// 按field分组
	const groups: Record<string, OutlineEntry[]> = {}
	for (const entry of outline) {
		const key = entry.field ?? ""
		if (!groups[key]) groups[key] = []
		groups[key].push(entry)
	}

	// 合成文本
	let result = ""
	for (const field of Object.keys(groups).sort()) {
		const group = groups[field]
		const title = field === "" ? "[global]" : `[${field}]`
		result += title + "\n"
		for (const entry of group) {
			let line = `  ${entry.type}`
			if (entry.description) line += `: ${normalizeTextForSummary(entry.description)}`
			result += line + "\n"
		}
		result += "\n"
	}
	return result.trimEnd()
}
