import { CodeLanguageType, SnippetType, ISnippetMeta } from "../types"
import * as go from "./go"
import * as python from "./python"
import * as java from "./java"
import * as javascript from "./javascript"
import * as jsx from "./jsx"
import * as typescript from "./typescript"
import * as tsx from "./tsx"
import * as swift from "./swift"
import * as css from "./css"
import * as html from "./html"
import * as kotlin from "./kotlin"
import * as php from "./php"
import * as rust from "./rust"
import * as c from "./c"
import * as cpp from "./cpp"
import * as general from "./general"

import Parser from "web-tree-sitter"

export const extractSnippetsFromCaptures: Record<
	CodeLanguageType,
	(
		captures: Parser.QueryCapture[],
		sourceCode: string,
		filePath: string,
		fileHash: string,
		snippetTypes: SnippetType[],
		options: { maxSnippetLines: number; minSnippetLines: number },
	) => Promise<ISnippetMeta[]>
> = {
	[CodeLanguageType.Go]: go.extractSnippetsFromCapturesForGo,
	[CodeLanguageType.Python]: python.extractSnippetsFromCapturesForPython,
	[CodeLanguageType.Java]: java.extractSnippetsFromCapturesForJava,
	[CodeLanguageType.JavaScript]: javascript.extractSnippetsFromCapturesForJavaScript,
	[CodeLanguageType.JSX]: jsx.extractSnippetsFromCapturesForJSX,
	[CodeLanguageType.TypeScript]: typescript.extractSnippetsFromCapturesForTypeScript,
	[CodeLanguageType.TSX]: tsx.extractSnippetsFromCapturesForTSX,
	[CodeLanguageType.Swift]: swift.extractSnippetsFromCapturesForSwift,
	[CodeLanguageType.CSS]: css.extractSnippetsFromCapturesForCSS,
	[CodeLanguageType.HTML]: html.extractSnippetsFromCapturesForHTML,
	[CodeLanguageType.Kotlin]: kotlin.extractSnippetsFromCapturesForKotlin,
	[CodeLanguageType.PHP]: php.extractSnippetsFromCapturesForPHP,
	[CodeLanguageType.Rust]: rust.extractSnippetsFromCapturesForRust,
	[CodeLanguageType.C]: c.extractSnippetsFromCapturesForC,
	[CodeLanguageType.CPP]: cpp.extractSnippetsFromCapturesForCPP,
	[CodeLanguageType.Unknown]: general.extractSnippetsFromCapturesForGeneral,
}

export const generateOverViewFromSnippets: Record<CodeLanguageType, (snippets: ISnippetMeta[]) => string> = {
	[CodeLanguageType.Go]: (snippets: ISnippetMeta[]) => {
		return go.formatOutlineText(go.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.Python]: (snippets: ISnippetMeta[]) => {
		return python.formatOutlineText(python.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.Java]: (snippets: ISnippetMeta[]) => {
		return java.formatOutlineText(java.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.JavaScript]: (snippets: ISnippetMeta[]) => {
		return ""
	},
	[CodeLanguageType.JSX]: (snippets: ISnippetMeta[]) => {
		return jsx.formatOutlineText(jsx.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.TypeScript]: (snippets: ISnippetMeta[]) => {
		return typescript.formatOutlineText(typescript.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.TSX]: (snippets: ISnippetMeta[]) => {
		return tsx.formatOutlineText(tsx.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.Swift]: (snippets: ISnippetMeta[]) => {
		return swift.formatOutlineText(swift.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.CSS]: (snippets: ISnippetMeta[]) => {
		return css.formatOutlineText(css.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.HTML]: (snippets: ISnippetMeta[]) => {
		return html.formatOutlineText(html.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.Kotlin]: (snippets: ISnippetMeta[]) => {
		return kotlin.formatOutlineText(kotlin.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.PHP]: (snippets: ISnippetMeta[]) => {
		return php.formatOutlineText(php.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.Rust]: (snippets: ISnippetMeta[]) => {
		return rust.formatOutlineText(rust.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.C]: (snippets: ISnippetMeta[]) => {
		return c.formatOutlineText(c.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.CPP]: (snippets: ISnippetMeta[]) => {
		return cpp.formatOutlineText(cpp.buildSummaryFromSnippets(snippets))
	},
	[CodeLanguageType.Unknown]: (snippets: ISnippetMeta[]) => {
		return ""
	},
}
