// CTCode/src/autocompletion/v2/types.ts
// 所有核心类型和接口定义，供多人协作实现

export enum CodeLanguageType {
	Python = "python",
	Java = "java",
	JavaScript = "javascript",
	TypeScript = "typescript",
	JSX = "jsx",
	TSX = "tsx",
	Go = "go",
	Swift = "swift",
	CSS = "css",
	HTML = "html",
	Kotlin = "kotlin",
	PHP = "php",
	Rust = "rust",
	C = "c",
	CPP = "cpp",
	Unknown = "unknown",
}

export const CodeLanguageTypeSupport = [
	CodeLanguageType.Python,
	CodeLanguageType.Java,
	CodeLanguageType.JavaScript,
	CodeLanguageType.TypeScript,
	CodeLanguageType.JSX,
	CodeLanguageType.TSX,
	CodeLanguageType.Go,
	CodeLanguageType.Swift,
	CodeLanguageType.CSS,
	CodeLanguageType.HTML,
	CodeLanguageType.Kotlin,
	CodeLanguageType.PHP,
	CodeLanguageType.Rust,
	CodeLanguageType.C,
	CodeLanguageType.CPP,
]

export const FileExtensionLanguageMap = {
	".py": CodeLanguageType.Python,
	".java": CodeLanguageType.Java,
	".js": CodeLanguageType.JavaScript,
	".mjs": CodeLanguageType.JavaScript,
	".ts": CodeLanguageType.TypeScript,
	".tsx": CodeLanguageType.TSX,
	".jsx": CodeLanguageType.JSX,
	".go": CodeLanguageType.Go,
	".swift": CodeLanguageType.Swift,
	".css": CodeLanguageType.CSS,
	".html": CodeLanguageType.HTML,
	".kt": CodeLanguageType.Kotlin,
	".kts": CodeLanguageType.Kotlin,
	".php": CodeLanguageType.PHP,
	".rs": CodeLanguageType.Rust,
	".c": CodeLanguageType.C,
	".h": CodeLanguageType.C,
	".cpp": CodeLanguageType.CPP,
	".cxx": CodeLanguageType.CPP,
	".cc": CodeLanguageType.CPP,
	".hpp": CodeLanguageType.CPP,
	".hxx": CodeLanguageType.CPP,
}

export const LanguageFileExtensionMap = Object.entries(FileExtensionLanguageMap).reduce(
	(acc, [extension, language]) => {
		if (!acc[language]) acc[language] = []
		acc[language].push(extension)
		return acc
	},
	{} as Record<CodeLanguageType, string[]>,
)

// 片段类型/来源枚举
export enum SnippetType {
	ImportOrInclude = "import_or_include",
	FunctionOrMethod = "function_or_method",
	ClassOrInterfaceOrStructOrEnum = "class_or_interface_or_struct",
	VariableOrConstant = "variable_or_constant",
}

export type Point = {
	row: number
	column: number
}

// 代码片段元信息结构
export interface ISnippetMeta {
	name: string // 符号名称
	type: SnippetType // 片段类型/来源
	filePath: string // 所在文件路径
	startLine: number // 起始行号
	endLine: number // 结束行号
	startPosition?: Point // 起始位置
	endPosition?: Point // 结束位置
	rangeText: string // 片段原始捕获代码内容
	fileHash: string // 所属文件内容 hash
	startColumn?: number // 添加起始列
	endColumn?: number // 添加结束列

	language?: CodeLanguageType // 语言类型
	field?: string // 所处的class/struct名称
	namespace?: string // 所处的namespace名称

	// 结构化信息
	definition?: {
		name: string // 定义名称（函数名、类名、变量名等）
		type: string // 定义类型（function, class, interface, variable, method等）
		parameters?: ParameterInfo[] // 参数信息（仅对函数/方法有效）
		returnType?: string // 返回类型（仅对函数/方法有效）
		visibility?: string // 可见性（public, private, protected等）
		isStatic?: boolean // 是否为静态成员
		isAsync?: boolean // 是否为异步函数
	}

	// 参数信息结构
	parameters?: ParameterInfo[]

	// 签名信息（用于快速匹配和prompt生成）
	signature?: string // 函数/方法签名，如 "add(a: number, b: number): number"
	definitionText?: string // 定义文本，用于提供定义参考
	implementText?: string // 实现文本，用于提供实现参考

	// 可选: score, source, extraMeta 等
	scope?: string[] // 作用域链
	id?: string // 唯一标识
	dependencies?: string[]
}

// 参数信息接口
export interface ParameterInfo {
	name: string // 参数名称
	type?: string // 参数类型
	defaultValue?: string // 默认值
	isOptional?: boolean // 是否为可选参数
	isRest?: boolean // 是否为剩余参数（...args）
	description?: string // 参数描述
}

// 单个源文件的元信息结构
export interface IFileMeta {
	filePath: string
	fileExtension?: string
	language?: CodeLanguageType // 语言类型（可选）
	snippetMetas?: ISnippetMeta[] // 代码片段（可选）
	projectRoot?: string // 项目根目录（可选）
	fileHash?: string // 文件哈希（可选）
	content?: string // 文件内容（可选）
	read?(): Promise<string | null> // 异步读取文件内容（可选）
}

// 多文件元信息集合
export interface ICodeFiles {
	files: IFileMeta[]
}

// 代码片段集合及相关操作接口
export interface ICodeSnippets {
	snippets: ISnippetMeta[]
	merge(globalContext: ICodeSnippets): Promise<void>
	find(query: Partial<ISnippetMeta>): Promise<ISnippetMeta[]>
	deleteByFile(filePath: string): Promise<ISnippetMeta[]>
	updateByFile(file: IFileMeta, snippets: ICodeSnippets): Promise<boolean>
	insert(snippets: ISnippetMeta[]): Promise<number>
	update(snippet: ISnippetMeta): Promise<boolean>
	merge(codeSnippets: ICodeSnippets): Promise<void>
	clear(): void
	filter(predicate: (snippet: ISnippetMeta) => boolean): Promise<ICodeSnippets>
	isCodeFileLatestIndex(file: IFileMeta): boolean
}

// 类型定义
export interface ChunkOptions {
	file: IFileMeta
	snippetTypes?: SnippetType[]
	maxSnippetLines?: number
	minSnippetLines?: number
}

// 代码上下文管理主接口
export interface ICodeContext {
	chunk(codeText: string, options: ChunkOptions): Promise<ICodeSnippets>
	index(codeFiles: ICodeFiles, options: any): Promise<ICodeSnippets>
	retriveRelates(
		codeText: string,
		line: number,
		column: number,
		file: IFileMeta,
		globalContext?: ICodeSnippets,
	): Promise<ICodeSnippets>
}

// 结构化补全请求体
export interface ICompletionContext {
	snippets: ISnippetMeta[]
	overview?: string // 文件概览
	prefix: string
	suffix: string
	filePath?: string
	fileHash?: string
	gitUrl?: string
	// 其他全局信息
}

// 共享的文档和位置信息接口
export interface DocumentInfo {
	file: IFileMeta
	content: string
	lineCount: number
	language: string
	getLineText(line: number): string
}

export interface PositionInfo {
	line: number
	character: number
}

export enum ModelType {
	DeepSeek = "deepseek",
	Qwen = "qwen",
}

export enum IContextSectionType {
	RepoTreeView = "RepoTreeView",
	FileInfo = "FileInfo",
	FileCodeOverview = "FileCodeOverview",
	RelatedFunctionDefinition = "RelatedFunctionDefinition",
	RelatedFuntionImplement = "RelatedFuntionImplement",
	EndContextPrompt = "EndContextPrompt",
}

export interface IContextSection {
	type: IContextSectionType
	content: string
}

export interface CompletionResponse {
	completion: string
	id: string
}

export interface ICodeModel {
	modelType: ModelType
	modelId: string
	baseUrl: string
	apiKey: string
	commonStops?: string[]
	generateCompletion(prompt: string, languageType: CodeLanguageType | undefined): Promise<CompletionResponse>
	buildCompletionPrompt(prefix: string, suffix: string, extraContexts: IContextSection[], file: IFileMeta): string
}

export interface IPreprocessOptions {
	maxContextLength: number
	codeFile: IFileMeta
	editCursor: PositionInfo
	recentOpenFiles: IFileMeta[]
}

export interface IPreprocessResult {
	prefix: string
	suffix: string
	extraContexts: IContextSection[]
	shouldSkipCompletion?: boolean
}

export interface PostProcessOptions {
	prompt: string
	prefix: string
	suffix: string
	modelType: ModelType
}

export interface ICodeContextOptions {
	numLinesAsContext: number
	numLinesAsContextSuffix: number
	numImplementContexts: number
}

export enum LogLevel {
	Debug = "DEBUG",
	Info = "INFO",
	Warn = "WARN",
	Error = "ERROR",
}

export interface ILogger {
	prefix: string
	level: LogLevel
	with(prefix: string): ILogger
	debug(message?: any, ...optionalParams: any[]): void
	info(message?: any, ...optionalParams: any[]): void
	warn(message?: any, ...optionalParams: any[]): void
	error(message?: any, ...optionalParams: any[]): void
}
