import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { extractSnippetsFromCapturesForTypeScript } from "./typescript"
import { Logger } from "../Logger"

// Helper function to check if a name starts with uppercase letter
function startsWithUppercase(name: string): boolean {
	return name.length > 0 && name[0] >= "A" && name[0] <= "Z"
}

// Helper function to check if a function is a generic component
function isGenericComponent(rangeText: string): boolean {
	return rangeText.includes("function") && rangeText.includes("<") && rangeText.includes(">(")
}

// Helper function to clean parameter name by removing type annotations, defaults, and optional markers
function cleanParameterName(paramText: string): string {
	let cleaned = paramText.trim()

	// Remove type annotation (: type)
	const colonIndex = cleaned.indexOf(":")
	if (colonIndex !== -1) {
		cleaned = cleaned.substring(0, colonIndex).trim()
	}

	// Remove default value (= value)
	const equalsIndex = cleaned.indexOf("=")
	if (equalsIndex !== -1) {
		cleaned = cleaned.substring(0, equalsIndex).trim()
	}

	// Remove optional marker (?)
	if (cleaned.endsWith("?")) {
		cleaned = cleaned.slice(0, -1).trim()
	}

	return cleaned
}

// Helper function to extract parameters from destructured props using string parsing
function extractDestructuredParamsFromText(paramText: string): string[] {
	const params: string[] = []

	// Find destructuring pattern { ... }
	const openBrace = paramText.indexOf("{")
	const closeBrace = paramText.lastIndexOf("}")

	if (openBrace !== -1 && closeBrace !== -1 && closeBrace > openBrace) {
		const destructuredContent = paramText.substring(openBrace + 1, closeBrace)
		const paramNames = destructuredContent.split(",")

		for (const param of paramNames) {
			const cleanName = cleanParameterName(param)
			if (cleanName && cleanName !== "..." && !cleanName.includes("...")) {
				params.push(cleanName)
			}
		}
	}

	return params
}

export async function extractSnippetsFromCapturesForTSX(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[TSX extract]")

	// 首先让TypeScript处理器处理基础的函数、类、接口、变量等
	const tsSnippets = await extractSnippetsFromCapturesForTypeScript(
		captures,
		sourceCode,
		filePath,
		fileHash,
		snippetTypes,
		options,
	)

	// 更新language字段为TSX，并修改definition类型以符合React惯例
	for (const snippet of tsSnippets) {
		snippet.language = CodeLanguageType.TSX

		// 修改函数的definition类型为react_component
		if (snippet.type === SnippetType.FunctionOrMethod && snippet.definition?.type === "function") {
			// 修改自定义hook（以use开头的函数）的definition类型
			if (snippet.name.startsWith("use")) {
				snippet.definition.type = "react_custom_hook"
			} else {
				// 检查是否是React组件（函数名以大写字母开头或返回JSX）
				const isReactComponent =
					startsWithUppercase(snippet.name) ||
					(snippet.rangeText.includes("return") &&
						snippet.rangeText.includes("<") &&
						snippet.rangeText.includes(">"))

				if (isReactComponent) {
					// 检查是否是泛型组件
					if (
						snippet.rangeText.includes("<") &&
						snippet.rangeText.includes(">(") &&
						isGenericComponent(snippet.rangeText)
					) {
						snippet.definition.type = "react_generic_component"
					} else {
						snippet.definition.type = "react_component"
					}
				}
			}

			// 改进参数解析 - 处理props解构参数
			if (snippet.parameters && snippet.parameters.length > 0) {
				// 首先重新组合所有参数，以检测被分割的解构参数
				const paramTexts = snippet.parameters.map((p) => p.name.trim())
				const fullParamText = paramTexts.join(", ")

				const improvedParams: ParameterInfo[] = []

				// 检查重新组合的文本是否包含解构参数
				if (fullParamText.includes("{") && fullParamText.includes("}")) {
					// 重新解析整个参数字符串使用字符串方法
					const destructuredParams = extractDestructuredParamsFromText(fullParamText)
					improvedParams.push(...destructuredParams.map((p) => ({ name: p })))
				} else {
					// 处理普通参数
					for (const param of snippet.parameters) {
						const paramText = param.name.trim()
						const cleanName = cleanParameterName(paramText)
						if (cleanName && !cleanName.includes("{") && !cleanName.includes("}")) {
							improvedParams.push({ name: cleanName })
						}
					}
				}

				snippet.parameters = improvedParams

				// 更新签名
				const paramStr = improvedParams.map((p) => p.name).join(", ")
				snippet.signature = `${snippet.name}(${paramStr})`
			} else if (!snippet.parameters || snippet.parameters.length === 0) {
				// 如果 snippet.parameters 是空的或不存在，但 definition.parameters 有内容，使用 definition.parameters
				if (snippet.definition?.parameters && snippet.definition.parameters.length > 0) {
					// 需要重新组合被错误分割的解构参数
					const paramTexts = snippet.definition.parameters.map((p) => p.name || "")
					const fullParamText = paramTexts.join(", ")

					const improvedParams: ParameterInfo[] = []

					// 检查是否包含解构参数
					if (fullParamText.includes("{") && fullParamText.includes("}")) {
						// 重新解析整个参数字符串使用字符串方法
						const destructuredParams = extractDestructuredParamsFromText(fullParamText)
						improvedParams.push(...destructuredParams.map((p) => ({ name: p })))
					} else {
						// 处理普通参数
						for (const param of snippet.definition.parameters) {
							const paramText = param.name?.trim() || ""
							const cleanName = cleanParameterName(paramText)
							if (cleanName && !cleanName.includes("{") && !cleanName.includes("}")) {
								improvedParams.push({ name: cleanName })
							}
						}
					}

					snippet.parameters = improvedParams

					// 更新签名
					const paramStr = improvedParams.map((p) => p.name).join(", ")
					snippet.signature = `${snippet.name}(${paramStr})`
				}
			}
		} else if (snippet.type === SnippetType.FunctionOrMethod && snippet.definition?.type === "method") {
			// 类方法保持原样，但更新语言类型
		} else if (
			snippet.type === SnippetType.ClassOrInterfaceOrStructOrEnum &&
			snippet.definition?.type === "class"
		) {
			// 检查是否是React类组件（继承自Component或PureComponent）
			if (
				snippet.rangeText.includes("extends") &&
				(snippet.rangeText.includes("Component") || snippet.rangeText.includes("PureComponent"))
			) {
				snippet.definition.type = "react_class_component"
			}
		} else if (snippet.type === SnippetType.ImportOrInclude && snippet.definition?.type === "import") {
			snippet.definition.type = "react_import"
		} else if (snippet.type === SnippetType.VariableOrConstant && snippet.definition?.type === "variable") {
			// 检查变量是否是React组件（以大写字母开头且可能包含JSX）
			if (startsWithUppercase(snippet.name)) {
				// 由于 rangeText 可能只包含变量名，我们需要检查代码上下文
				// 检查是否在同一文件中有对应的函数组件定义
				const isLikelyComponent =
					tsSnippets.some(
						(s) =>
							s.name === snippet.name &&
							s.type === SnippetType.FunctionOrMethod &&
							s.definition?.type === "react_component",
					) ||
					// 或者检查常见的 React 组件模式
					snippet.rangeText.includes("withError") ||
					snippet.rangeText.includes("memo(") ||
					snippet.rangeText.includes("forwardRef(") ||
					snippet.rangeText.includes("lazy(") ||
					snippet.rangeText.includes("React.") ||
					snippet.rangeText.includes("FC") ||
					snippet.rangeText.includes("FunctionComponent") ||
					(snippet.rangeText.includes("=>") &&
						snippet.rangeText.includes("<") &&
						snippet.rangeText.includes(">"))

				if (isLikelyComponent) {
					snippet.definition.type = "react_component"
					snippet.type = SnippetType.FunctionOrMethod // 改变类型为函数组件
				}
			}
		}
	}

	const snippets: ISnippetMeta[] = [...tsSnippets]
	const seenHashes = new Set<string>()

	// 填充已见过的哈希值
	for (const snippet of snippets) {
		const hash = generateSnippetHash(snippet.filePath, snippet.startLine, snippet.endLine, snippet.rangeText)
		seenHashes.add(hash)
	}

	// === JSX Components ===
	for (const capture of captures) {
		if (
			capture.name !== "definition.component" &&
			capture.name !== "definition.jsx_element" &&
			capture.name !== "definition.jsx_self_closing_element"
		)
			continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Find JSX component name
		let nameCap: Parser.QueryCapture | undefined
		if (capture.name === "definition.component") {
			nameCap = captures.find(
				(c) => c.name === "name" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
			)
		} else {
			nameCap = captures.find(
				(c) =>
					c.name === "component" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
			)
		}

		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// Only capture custom components (starting with uppercase) or known React components
		if (!startsWithUppercase(name) && !isKnownReactComponent(name)) continue

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name,
			signature: `<${name}>`,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: `<${name}>`,
			implementText: rangeText,
			scope,
			fileHash,
			language: CodeLanguageType.TSX,
			definition: {
				name,
				type: "jsx_component",
				parameters: [],
				returnType: "JSX.Element",
			},
		})
	}

	// === JSX Member Components (e.g., Button.Primary) ===
	for (const capture of captures) {
		if (capture.name !== "definition.member_component") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Find object and property
		const objectCap = captures.find(
			(c) => c.name === "object" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)
		const propertyCap = captures.find(
			(c) => c.name === "property" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)

		if (!objectCap || !propertyCap) continue

		const objectName = sourceCode.substring(objectCap.node.startIndex, objectCap.node.endIndex)
		const propertyName = sourceCode.substring(propertyCap.node.startIndex, propertyCap.node.endIndex)
		const fullName = `${objectName}.${propertyName}`

		// Only capture if object starts with uppercase (component namespace)
		if (!startsWithUppercase(objectName)) continue

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name: fullName,
			signature: `<${fullName}>`,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: `<${fullName}>`,
			implementText: rangeText,
			scope,
			fileHash,
			field: objectName,
			language: CodeLanguageType.TSX,
			definition: {
				name: fullName,
				type: "jsx_member_component",
				parameters: [],
				returnType: "JSX.Element",
			},
		})
	}

	// === JSX Conditional Components ===
	for (const capture of captures) {
		if (capture.name !== "definition.conditional_component") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Find component name in conditional expression
		const componentCap = captures.find(
			(c) => c.name === "component" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)

		if (!componentCap) continue
		const name = sourceCode.substring(componentCap.node.startIndex, componentCap.node.endIndex)

		// Only capture custom components (starting with uppercase)
		if (!startsWithUppercase(name)) continue

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name,
			signature: `{condition ? <${name}> : null}`,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: `{condition ? <${name}> : null}`,
			implementText: rangeText,
			scope,
			fileHash,
			language: CodeLanguageType.TSX,
			definition: {
				name,
				type: "jsx_conditional_component",
				parameters: [],
				returnType: "JSX.Element | null",
			},
		})
	}

	// === Generic Components ===
	for (const capture of captures) {
		if (capture.name !== "definition.generic_component") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Find component name
		const nameCap = captures.find(
			(c) => c.name === "name" && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)

		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// Check if it's a React component (starts with uppercase or returns JSX)
		const isReactComponent =
			startsWithUppercase(name) || sourceCode.substring(node.startIndex, node.endIndex).includes("JSX")

		if (!isReactComponent) continue

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name,
			signature: `${name}<T>`,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: `${name}<T>`,
			implementText: rangeText,
			scope,
			fileHash,
			language: CodeLanguageType.TSX,
			definition: {
				name,
				type: "react_generic_component",
				parameters: [],
				returnType: "JSX.Element",
			},
		})
	}

	return snippets
}

// Helper function to check if a component name is a known React component
function isKnownReactComponent(name: string): boolean {
	const knownComponents = [
		"Fragment",
		"Suspense",
		"StrictMode",
		"Profiler",
		"ErrorBoundary",
		"Provider",
		"Consumer",
		"Portal",
	]
	return knownComponents.includes(name)
}

export interface OutlineEntry {
	type: string
	name: string
	file: string
	field?: string
	description?: string
}

export function buildSummaryFromSnippets(snips: ISnippetMeta[]): OutlineEntry[] {
	const outline: OutlineEntry[] = []
	for (const s of snips) {
		if (s.type === SnippetType.ImportOrInclude) {
			outline.push({
				type: "import",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.FunctionOrMethod) {
			let type = "function"
			if (s.definition?.type === "react_component") type = "component"
			else if (s.definition?.type === "react_custom_hook") type = "hook"
			else if (s.definition?.type === "jsx_component") type = "jsx"
			else if (s.definition?.type === "jsx_member_component") type = "jsx.member"
			else if (s.definition?.type === "jsx_conditional_component") type = "jsx.conditional"
			else if (s.definition?.type === "react_generic_component") type = "component.generic"
			else if (s.field) type = "method"

			outline.push({
				type,
				name: s.name,
				file: s.filePath,
				field: s.field || "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.ClassOrInterfaceOrStructOrEnum) {
			let type = s.definition?.type || "type"
			if (s.definition?.type === "react_class_component") type = "class.component"

			outline.push({
				type,
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.name,
			})
		} else if (s.type === SnippetType.VariableOrConstant) {
			outline.push({
				type: "variable",
				name: s.name,
				file: s.filePath,
				field: s.field || "",
				description: s.definitionText,
			})
		}
	}
	return outline
}

export function formatOutlineText(outline: OutlineEntry[]): string {
	const groups: Record<string, OutlineEntry[]> = {}
	for (const e of outline) {
		const key = e.field || ""
		;(groups[key] ||= []).push(e)
	}
	let out = ""
	for (const field of Object.keys(groups).sort()) {
		out += (field ? `[${field}]` : "[global]") + "\n"
		for (const e of groups[field]) {
			out += `  ${e.type}: ${e.description}\n`
		}
		out += "\n"
	}
	return out.trimEnd()
}
