import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { extractSnippetsFromCapturesForJavaScript } from "./javascript"

export async function extractSnippetsFromCapturesForJSX(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	// 首先让JavaScript处理器处理基础的函数和变量
	const jsSnippets = await extractSnippetsFromCapturesForJavaScript(
		captures,
		sourceCode,
		filePath,
		fileHash,
		snippetTypes,
		options,
	)

	// 更新language字段为JSX，并修改definition类型以符合React惯例
	for (const snippet of jsSnippets) {
		snippet.language = CodeLanguageType.JSX

		// 修改函数的definition类型为react_component
		if (snippet.type === SnippetType.FunctionOrMethod && snippet.definition?.type === "function") {
			snippet.definition.type = "react_component"

			// 改进参数解析 - 解构参数
			if (snippet.parameters && snippet.parameters.length > 0) {
				const improvedParams: ParameterInfo[] = []
				for (const param of snippet.parameters) {
					// 处理解构参数 {name, age, email}
					if (param.name.includes("{") && param.name.includes("}")) {
						const openBrace = param.name.indexOf("{")
						const closeBrace = param.name.indexOf("}")
						if (openBrace !== -1 && closeBrace !== -1 && closeBrace > openBrace) {
							const destructuredContent = param.name.substring(openBrace + 1, closeBrace)
							const destructuredParams = destructuredContent.split(",").map((p) => {
								// 移除默认值 - 使用indexOf而不是正则表达式
								const equalIndex = p.indexOf("=")
								const cleanParam = equalIndex !== -1 ? p.substring(0, equalIndex).trim() : p.trim()
								return cleanParam
							})
							improvedParams.push(...destructuredParams.map((p) => ({ name: p })))
						}
					} else {
						improvedParams.push(param)
					}
				}
				snippet.parameters = improvedParams

				// 更新签名
				const paramStr = improvedParams.map((p) => p.name).join(", ")
				snippet.signature = `${snippet.name}(${paramStr})`
			}
		} else if (snippet.type === SnippetType.FunctionOrMethod && snippet.definition?.type === "arrow function") {
			snippet.definition.type = "react_component"

			// 改进参数解析 - 解构参数
			if (snippet.parameters && snippet.parameters.length > 0) {
				const improvedParams: ParameterInfo[] = []
				for (const param of snippet.parameters) {
					// 处理解构参数 {name, age, email}
					if (param.name.includes("{") && param.name.includes("}")) {
						// 使用indexOf和substring替代正则表达式
						const openBrace = param.name.indexOf("{")
						const closeBrace = param.name.indexOf("}")
						if (openBrace !== -1 && closeBrace !== -1 && closeBrace > openBrace) {
							const destructuredContent = param.name.substring(openBrace + 1, closeBrace)
							const destructuredParams = destructuredContent.split(",").map((p) => {
								// 移除默认值 - 使用indexOf而不是正则表达式
								const equalIndex = p.indexOf("=")
								const cleanParam = equalIndex !== -1 ? p.substring(0, equalIndex).trim() : p.trim()
								return cleanParam
							})
							improvedParams.push(...destructuredParams.map((p) => ({ name: p })))
						}
					} else {
						improvedParams.push(param)
					}
				}
				snippet.parameters = improvedParams

				// 更新签名
				const paramStr = improvedParams.map((p) => p.name).join(", ")
				snippet.signature = `${snippet.name}(${paramStr})`
			}
		} else if (
			snippet.type === SnippetType.ClassOrInterfaceOrStructOrEnum &&
			snippet.definition?.type === "class"
		) {
			snippet.definition.type = "react_class_component"
		} else if (snippet.type === SnippetType.ImportOrInclude && snippet.definition?.type === "import") {
			snippet.definition.type = "react_import"
		}

		// 修改自定义hook（以use开头的函数）的definition类型
		if (
			snippet.type === SnippetType.FunctionOrMethod &&
			snippet.name.startsWith("use") &&
			snippet.definition?.type === "react_component"
		) {
			snippet.definition.type = "react_custom_hook"

			// 改进自定义hook的参数解析 - 使用tree-sitter而非正则表达式
			if (snippet.parameters && snippet.parameters.length > 0) {
				const improvedParams: ParameterInfo[] = []
				for (const param of snippet.parameters) {
					// 移除默认值
					const equalIndex = param.name.indexOf("=")
					const cleanName = equalIndex !== -1 ? param.name.substring(0, equalIndex).trim() : param.name
					improvedParams.push({ name: cleanName })
				}
				snippet.parameters = improvedParams

				// 更新签名
				const paramStr = improvedParams.map((p) => p.name).join(", ")
				snippet.signature = `${snippet.name}(${paramStr})`
			}
		}
	}

	const snippets: ISnippetMeta[] = [...jsSnippets]
	const seenHashes = new Set<string>()

	// 填充已见过的哈希值
	for (const snippet of snippets) {
		const hash = generateSnippetHash(snippet.filePath, snippet.startLine, snippet.endLine, snippet.rangeText)
		seenHashes.add(hash)
	}

	// === JSX Elements ===
	for (const capture of captures) {
		if (capture.name !== "definition.jsx_element") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// Find JSX component name
		const nameCap = captures.find(
			(c) =>
				c.name === "name.jsx_element" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// Only capture custom components (starting with uppercase) - using character comparison instead of regex
		const firstChar = name.charAt(0)
		if (firstChar < "A" || firstChar > "Z") continue

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name,
			signature: `<${name}>`,
			parameters: [],
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			implementText: rangeText,
			scope,
			fileHash,
			language: CodeLanguageType.JSX,
			definition: {
				name,
				type: "jsx_element",
				parameters: [],
			},
		})
	}

	// === Process array patterns for useState hooks ===
	for (const capture of captures) {
		if (capture.name !== "array_pattern") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 检查是否是useState的解构
		const parentNode = node.parent
		if (!parentNode || parentNode.type !== "variable_declarator") continue

		const valueNode = parentNode.namedChild(1) // value节点
		if (!valueNode || valueNode.type !== "call_expression") continue

		const functionNode = valueNode.namedChild(0) // function节点
		if (!functionNode || functionNode.type !== "identifier") continue

		const functionName = sourceCode.substring(functionNode.startIndex, functionNode.endIndex)
		if (functionName !== "useState") continue

		// 获取解构的变量名
		const firstIdentifier = node.namedChild(0)
		const secondIdentifier = node.namedChild(1)

		if (
			!firstIdentifier ||
			!secondIdentifier ||
			firstIdentifier.type !== "identifier" ||
			secondIdentifier.type !== "identifier"
		) {
			continue
		}

		const stateName = sourceCode.substring(firstIdentifier.startIndex, firstIdentifier.endIndex)
		const setterName = sourceCode.substring(secondIdentifier.startIndex, secondIdentifier.endIndex)
		const rangeText = sourceCode.substring(parentNode.startIndex, parentNode.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name: stateName,
			signature: `useState()`,
			parameters: [],
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			implementText: rangeText,
			scope,
			fileHash,
			language: CodeLanguageType.JSX,
			definition: {
				name: stateName,
				type: "react_hook",
				parameters: [],
			},
		})
	}

	// === Process useEffect and other hooks ===
	for (const capture of captures) {
		if (capture.name !== "name.definition.function") continue

		const node = capture.node
		const functionName = sourceCode.substring(node.startIndex, node.endIndex)

		// 只处理React hooks
		if (!functionName.startsWith("use")) continue

		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 找到调用表达式的父节点
		let callExpressionNode = node.parent
		while (callExpressionNode && callExpressionNode.type !== "call_expression") {
			callExpressionNode = callExpressionNode.parent
		}

		if (!callExpressionNode) continue

		// 跳过已经处理过的useState hooks（避免重复）
		if (functionName === "useState") {
			// 检查是否是解构赋值
			let parentNode = callExpressionNode.parent
			while (parentNode && parentNode.type !== "variable_declarator") {
				parentNode = parentNode.parent
			}
			if (parentNode) {
				const nameNode = parentNode.namedChild(0)
				if (nameNode && nameNode.type === "array_pattern") {
					continue // 跳过，因为已经在useState处理中处理了
				}
			}
		}

		const rangeText = sourceCode.substring(callExpressionNode.startIndex, callExpressionNode.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name: functionName,
			signature: `${functionName}()`,
			parameters: [],
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			implementText: rangeText,
			scope,
			fileHash,
			language: CodeLanguageType.JSX,
			definition: {
				name: functionName,
				type: "react_hook",
				parameters: [],
			},
		})
	}

	// === Process HOCs ===
	for (const capture of captures) {
		if (capture.name !== "name.definition.variable") continue

		const node = capture.node
		const variableName = sourceCode.substring(node.startIndex, node.endIndex)

		// 找到变量声明的父节点
		let declaratorNode = node.parent
		while (declaratorNode && declaratorNode.type !== "variable_declarator") {
			declaratorNode = declaratorNode.parent
		}

		if (!declaratorNode) continue

		// 检查是否是函数调用赋值
		const valueNode = declaratorNode.namedChild(1)
		if (!valueNode || valueNode.type !== "call_expression") continue

		const functionNode = valueNode.namedChild(0)
		if (!functionNode || functionNode.type !== "identifier") continue

		const functionName = sourceCode.substring(functionNode.startIndex, functionNode.endIndex)

		// Simple HOC detection: function name starts with uppercase or with "with" - using character comparison instead of regex
		const firstChar = functionName.charAt(0)
		const isUppercase = firstChar >= "A" && firstChar <= "Z"
		const startsWithWith = functionName.startsWith("with")

		if (!isUppercase && !startsWithWith) continue

		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		const rangeText = sourceCode.substring(declaratorNode.startIndex, declaratorNode.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		snippets.push({
			name: variableName,
			signature: `${functionName}(${variableName})`,
			parameters: [],
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			implementText: rangeText,
			scope,
			fileHash,
			language: CodeLanguageType.JSX,
			definition: {
				name: variableName,
				type: "react_hoc",
				parameters: [],
			},
		})
	}

	return snippets
}

export interface OutlineEntry {
	type: string
	name: string
	file: string // 表示当前文件
	field?: string // 空字符串代表当前文件的global
	description?: string
}

/**
 * 根据 JSX 文件的所有 snippet 生成大纲（Outline），支持 import、component、class、hook、jsx_element、variable
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): OutlineEntry[] {
	const outline: OutlineEntry[] = []

	// 在JSX中，我们应该包含所有顶级定义（不在方法内部的）
	// 包括组件、类、hooks、JSX元素、变量等
	const isTopLevel = (s: ISnippetMeta) => {
		// 如果scope为空或者只有一层（比如只有组件名），则认为是顶级
		return !s.scope || s.scope.length <= 1
	}

	for (const s of snippets) {
		if (!isTopLevel(s)) continue
		// Skip imports - focus on file-level definitions only
		if (s.type === SnippetType.ImportOrInclude) {
			continue
		} else if (s.type === SnippetType.FunctionOrMethod) {
			const definitionType = s.definition?.type
			if (definitionType === "react_component") {
				outline.push({
					type: "component",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.signature,
				})
			} else if (definitionType === "react_custom_hook") {
				outline.push({
					type: "custom_hook",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.signature,
				})
			} else {
				outline.push({
					type: "function",
					name: s.name,
					file: s.filePath,
					field: s.field || "",
					description: s.signature,
				})
			}
		} else if (s.type === SnippetType.ClassOrInterfaceOrStructOrEnum) {
			outline.push({
				type: "class",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.signature || s.name,
			})
		} else if (s.type === SnippetType.VariableOrConstant) {
			const definitionType = s.definition?.type
			if (definitionType === "react_hook") {
				outline.push({
					type: "hook",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.signature,
				})
			} else if (definitionType === "jsx_element") {
				outline.push({
					type: "jsx_element",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.signature,
				})
			} else {
				outline.push({
					type: "variable",
					name: s.name,
					file: s.filePath,
					field: s.field || "",
					description: s.signature || s.name,
				})
			}
		}
	}

	return outline
}

export function formatOutlineText(outline: OutlineEntry[]): string {
	// 按类型分组
	const groups: Record<string, OutlineEntry[]> = {}
	for (const entry of outline) {
		const key = entry.type
		if (!groups[key]) groups[key] = []
		groups[key].push(entry)
	}

	// 合成文本
	let result = ""
	const typeOrder = ["component", "class", "custom_hook", "hook", "jsx_element", "function", "variable"]
	const typeLabels: Record<string, string> = {
		component: "## React Components",
		class: "## Classes",
		custom_hook: "## Custom Hooks",
		hook: "## React Hooks",
		jsx_element: "## JSX Elements",
		function: "## Functions",
		variable: "## Variables",
	}

	for (const type of typeOrder) {
		const group = groups[type]
		if (!group || group.length === 0) continue

		result += typeLabels[type] + "\n"
		for (const entry of group) {
			result += `- ${entry.description || entry.name}\n`
		}
		result += "\n"
	}

	return result
}
