import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

/**
 * HTML特定的辅助函数
 */

/**
 * 替换字符串中的换行符为可视化字符（用于日志输出）
 */
function escapeNewlinesForLogging(text: string): string {
	return text.split("\n").join("\\n")
}

/**
 * 格式化类型名称：将下划线分隔的类型名转换为标题格式
 * 例如: "semantic_element" -> "Semantic Element"
 */
function formatTypeLabel(type: string): string {
	return type
		.split("_")
		.map((word) => word.charAt(0).toUpperCase() + word.slice(1).toLowerCase())
		.join(" ")
}

/**
 * 从节点中提取文本内容
 */
function getNodeText(node: Parser.SyntaxNode, sourceCode: string): string {
	return sourceCode.substring(node.startIndex, node.endIndex)
}

/**
 * 查找子节点按类型
 */
function findChildByType(node: Parser.SyntaxNode, type: string): Parser.SyntaxNode | null {
	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child && child.type === type) {
			return child
		}
	}
	return null
}

/**
 * 递归查找指定类型的子节点
 */
function findChildByTypeRecursive(node: Parser.SyntaxNode, type: string): Parser.SyntaxNode | null {
	if (node.type === type) {
		return node
	}

	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child) {
			const found = findChildByTypeRecursive(child, type)
			if (found) return found
		}
	}
	return null
}

/**
 * 提取HTML元素的标签名
 */
function extractTagName(node: Parser.SyntaxNode, sourceCode: string): string {
	// 查找tag_name节点
	const tagNameNode = findChildByTypeRecursive(node, "tag_name")
	if (tagNameNode) {
		return getNodeText(tagNameNode, sourceCode)
	}

	// 回退到默认名称
	return "element"
}

/**
 * 提取HTML属性信息
 */
function extractAttributeInfo(node: Parser.SyntaxNode, sourceCode: string): { name: string; value?: string } {
	const attributeNameNode = findChildByType(node, "attribute_name")
	const attributeValueNode =
		findChildByType(node, "attribute_value") || findChildByType(node, "quoted_attribute_value")

	const name = attributeNameNode ? getNodeText(attributeNameNode, sourceCode) : "attribute"
	let value: string | undefined

	if (attributeValueNode) {
		let rawValue = getNodeText(attributeValueNode, sourceCode)
		// 移除引号
		if (
			(rawValue.startsWith('"') && rawValue.endsWith('"')) ||
			(rawValue.startsWith("'") && rawValue.endsWith("'"))
		) {
			rawValue = rawValue.slice(1, -1)
		}
		value = rawValue
	}

	return { name, value }
}

/**
 * 判断是否为语义化HTML元素
 */
function isSemanticElement(tagName: string): boolean {
	const semanticTags = new Set([
		"header",
		"nav",
		"main",
		"section",
		"article",
		"aside",
		"footer",
		"figure",
		"figcaption",
		"details",
		"summary",
		"mark",
		"time",
	])
	return semanticTags.has(tagName.toLowerCase())
}

/**
 * 判断是否为交互式HTML元素
 */
function isInteractiveElement(tagName: string): boolean {
	const interactiveTags = new Set(["button", "input", "select", "textarea", "a", "form"])
	return interactiveTags.has(tagName.toLowerCase())
}

/**
 * 主要的HTML捕获处理函数
 * 从tree-sitter的captures中提取HTML代码片段信息
 */
export async function extractSnippetsFromCapturesForHTML(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForHTML]")
	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 处理捕获的每个节点
	for (const capture of captures) {
		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1
		const rangeText = getNodeText(node, sourceCode)

		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText) + "_" + capture.name
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		// DEBUG: Log each capture being processed
		logger.info(`Processing capture: ${capture.name} -> "${escapeNewlinesForLogging(rangeText)}"`)

		// 处理DOCTYPE声明
		if (capture.name === "definition.doctype" && snippetTypes.includes(SnippetType.ImportOrInclude)) {
			snippets.push({
				name: "DOCTYPE",
				type: SnippetType.ImportOrInclude,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: "DOCTYPE",
					type: "doctype",
				},
				definitionText: rangeText,
				signature: "DOCTYPE html",
				scope: buildScopeChain(node),
			})
		}

		// 处理文档根元素
		else if (
			capture.name === "definition.document" &&
			snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)
		) {
			snippets.push({
				name: "document",
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: "document",
					type: "document",
				},
				definitionText: rangeText,
				signature: "HTML Document",
				scope: buildScopeChain(node),
			})
		}

		// 处理常规HTML元素
		else if (
			capture.name === "definition.element" &&
			snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)
		) {
			// 查找对应的name capture - 它应该在相同范围内
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			const elementName = nameCapture
				? getNodeText(nameCapture.node, sourceCode)
				: extractTagName(node, sourceCode)

			// 确定元素类型
			let elementType = "element"
			if (isSemanticElement(elementName)) {
				elementType = "semantic_element"
			} else if (isInteractiveElement(elementName)) {
				elementType = "interactive_element"
			}

			snippets.push({
				name: elementName,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: elementName,
					type: elementType,
				},
				definitionText: rangeText,
				signature: `<${elementName}>`,
				scope: buildScopeChain(node),
			})
		}

		// 处理脚本元素
		else if (capture.name === "definition.script" && snippetTypes.includes(SnippetType.FunctionOrMethod)) {
			// 查找对应的name capture - 应该在相同范围内
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			const scriptName = nameCapture ? getNodeText(nameCapture.node, sourceCode) : "script"

			snippets.push({
				name: scriptName,
				type: SnippetType.FunctionOrMethod,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: scriptName,
					type: "script",
				},
				definitionText: rangeText,
				signature: `<${scriptName}>`,
				scope: buildScopeChain(node),
			})
		}

		// 处理样式元素
		else if (capture.name === "definition.style" && snippetTypes.includes(SnippetType.FunctionOrMethod)) {
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			const styleName = nameCapture ? getNodeText(nameCapture.node, sourceCode) : "style"

			snippets.push({
				name: styleName,
				type: SnippetType.FunctionOrMethod,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: styleName,
					type: "style",
				},
				definitionText: rangeText,
				signature: `<${styleName}>`,
				scope: buildScopeChain(node),
			})
		}

		// 处理void元素（自闭合元素）
		else if (
			capture.name === "definition.void_element" &&
			snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)
		) {
			// 查找对应的void元素的name capture
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition.void" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			const elementName = nameCapture
				? getNodeText(nameCapture.node, sourceCode)
				: extractTagName(node, sourceCode)

			// 确定元素类型 - 优先检查是否为交互元素
			let elementType = "void_element"
			if (isInteractiveElement(elementName)) {
				elementType = "interactive_element"
			} else if (isSemanticElement(elementName)) {
				elementType = "semantic_element"
			}

			snippets.push({
				name: elementName,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: elementName,
					type: elementType,
				},
				definitionText: rangeText,
				signature: elementType === "void_element" ? `<${elementName} />` : `<${elementName}>`,
				scope: buildScopeChain(node),
			})
		}

		// 处理自闭合标签
		else if (
			capture.name === "definition.self_closing_tag" &&
			snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)
		) {
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			const tagName = nameCapture ? getNodeText(nameCapture.node, sourceCode) : "self_closing_tag"

			snippets.push({
				name: tagName,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: tagName,
					type: "self_closing_tag",
				},
				definitionText: rangeText,
				signature: `<${tagName} />`,
				scope: buildScopeChain(node),
			})
		}

		// 处理HTML属性
		else if (capture.name === "definition.attribute" && snippetTypes.includes(SnippetType.VariableOrConstant)) {
			// 查找对应的name capture - 应该在相同范围内
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			if (nameCapture) {
				const attributeInfo = extractAttributeInfo(node, sourceCode)
				const attributeName = attributeInfo.name

				// 确定属性类型
				let attributeType = "attribute"
				if (attributeName.startsWith("data-")) {
					attributeType = "data_attribute"
				} else if (attributeName.startsWith("aria-")) {
					attributeType = "aria_attribute"
				} else if (["id", "class"].includes(attributeName)) {
					attributeType = "core_attribute"
				}

				const signature = attributeInfo.value ? `${attributeName}="${attributeInfo.value}"` : attributeName

				snippets.push({
					name: attributeName,
					type: SnippetType.VariableOrConstant,
					filePath,
					startLine,
					endLine,
					rangeText,
					fileHash,
					language: CodeLanguageType.HTML,
					definition: {
						name: attributeName,
						type: attributeType,
					},
					definitionText: rangeText,
					signature,
					scope: buildScopeChain(node),
				})
			}
		}

		// 处理HTML注释
		else if (capture.name === "definition.comment" && snippetTypes.includes(SnippetType.ImportOrInclude)) {
			// 提取注释内容，移除注释标记
			let commentText = rangeText.trim()
			if (commentText.startsWith("<!--") && commentText.endsWith("-->")) {
				commentText = commentText.slice(4, -3).trim()
			}

			// 取注释的第一行作为名称
			const firstLine = commentText.split("\n")[0].trim()
			const commentName = firstLine.length > 0 ? firstLine : "comment"

			snippets.push({
				name: commentName,
				type: SnippetType.ImportOrInclude,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: commentName,
					type: "comment",
				},
				definitionText: rangeText,
				signature: `<!-- ${commentName} -->`,
				scope: buildScopeChain(node),
			})
		}

		// 处理文本内容（满足最小行数要求）
		else if (capture.name === "definition.text" && snippetTypes.includes(SnippetType.VariableOrConstant)) {
			const lineCount = endLine - startLine + 1
			if (lineCount >= options.minSnippetLines) {
				// 提取文本内容的第一行作为名称
				const textContent = rangeText.trim()
				const firstLine = textContent.split("\n")[0].trim()
				const textName = firstLine.length > 0 ? firstLine : "text"

				snippets.push({
					name: textName,
					type: SnippetType.VariableOrConstant,
					filePath,
					startLine,
					endLine,
					rangeText,
					fileHash,
					language: CodeLanguageType.HTML,
					definition: {
						name: textName,
						type: "text",
					},
					definitionText: rangeText,
					signature: textName,
					scope: buildScopeChain(node),
				})
			}
		}

		// 处理原始文本内容
		else if (capture.name === "definition.raw_text" && snippetTypes.includes(SnippetType.VariableOrConstant)) {
			const lineCount = endLine - startLine + 1
			if (lineCount >= options.minSnippetLines) {
				const textContent = rangeText.trim()
				const firstLine = textContent.split("\n")[0].trim()
				const rawTextName = firstLine.length > 0 ? firstLine : "raw_text"

				snippets.push({
					name: rawTextName,
					type: SnippetType.VariableOrConstant,
					filePath,
					startLine,
					endLine,
					rangeText,
					fileHash,
					language: CodeLanguageType.HTML,
					definition: {
						name: rawTextName,
						type: "raw_text",
					},
					definitionText: rangeText,
					signature: rawTextName,
					scope: buildScopeChain(node),
				})
			}
		}

		// 处理嵌套元素结构
		else if (
			capture.name === "definition.nested_elements" &&
			snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)
		) {
			const tagName = extractTagName(node, sourceCode)

			snippets.push({
				name: `${tagName}_container`,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.HTML,
				definition: {
					name: `${tagName}_container`,
					type: "nested_elements",
				},
				definitionText: rangeText,
				signature: `<${tagName}> (with children)`,
				scope: buildScopeChain(node),
			})
		}
	}

	logger.info(`[CodeContext] Extracted ${snippets.length} HTML snippets`)
	return snippets
}

/**
 * 构建HTML代码的概览文本
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): {
	doctypes: ISnippetMeta[]
	elements: ISnippetMeta[]
	attributes: ISnippetMeta[]
	scripts: ISnippetMeta[]
	styles: ISnippetMeta[]
	comments: ISnippetMeta[]
	textContent: ISnippetMeta[]
} {
	const summary = {
		doctypes: [] as ISnippetMeta[],
		elements: [] as ISnippetMeta[],
		attributes: [] as ISnippetMeta[],
		scripts: [] as ISnippetMeta[],
		styles: [] as ISnippetMeta[],
		comments: [] as ISnippetMeta[],
		textContent: [] as ISnippetMeta[],
	}

	for (const snippet of snippets) {
		const defType = snippet.definition?.type || ""

		switch (defType) {
			case "doctype":
				summary.doctypes.push(snippet)
				break
			case "element":
			case "semantic_element":
			case "interactive_element":
			case "void_element":
			case "self_closing_tag":
			case "nested_elements":
				summary.elements.push(snippet)
				break
			case "attribute":
			case "data_attribute":
			case "aria_attribute":
			case "core_attribute":
				summary.attributes.push(snippet)
				break
			case "script":
				summary.scripts.push(snippet)
				break
			case "style":
				summary.styles.push(snippet)
				break
			case "comment":
				summary.comments.push(snippet)
				break
			case "text":
			case "raw_text":
				summary.textContent.push(snippet)
				break
		}
	}

	return summary
}

/**
 * 格式化HTML概览文本输出
 */
export function formatOutlineText(summary: ReturnType<typeof buildSummaryFromSnippets>): string {
	const lines: string[] = []

	// DOCTYPE声明
	if (summary.doctypes.length > 0) {
		lines.push("## Document Type")
		summary.doctypes.forEach((snippet) => {
			lines.push(`- ${snippet.signature || snippet.name}`)
		})
		lines.push("")
	}

	// HTML元素
	if (summary.elements.length > 0) {
		lines.push("## HTML Elements")
		summary.elements.forEach((snippet) => {
			const typeSuffix =
				snippet.definition?.type === "semantic_element"
					? " (semantic)"
					: snippet.definition?.type === "interactive_element"
						? " (interactive)"
						: ""
			lines.push(`- ${snippet.signature || snippet.name}${typeSuffix}`)
		})
		lines.push("")
	}

	// 脚本和样式
	if (summary.scripts.length > 0 || summary.styles.length > 0) {
		lines.push("## Scripts & Styles")
		summary.scripts.forEach((snippet) => {
			lines.push(`- ${snippet.signature || snippet.name} (script)`)
		})
		summary.styles.forEach((snippet) => {
			lines.push(`- ${snippet.signature || snippet.name} (style)`)
		})
		lines.push("")
	}

	// 属性
	if (summary.attributes.length > 0) {
		lines.push("## Attributes")
		const attributesByType: Record<string, ISnippetMeta[]> = {}
		summary.attributes.forEach((snippet) => {
			const type = snippet.definition?.type || "attribute"
			if (!attributesByType[type]) attributesByType[type] = []
			attributesByType[type].push(snippet)
		})

		Object.keys(attributesByType)
			.sort()
			.forEach((type) => {
				const typeLabel = formatTypeLabel(type)
				lines.push(`### ${typeLabel}`)
				attributesByType[type].forEach((snippet) => {
					lines.push(`- ${snippet.signature || snippet.name}`)
				})
			})
		lines.push("")
	}

	// 注释
	if (summary.comments.length > 0) {
		lines.push("## Comments")
		summary.comments.forEach((snippet) => {
			lines.push(`- ${snippet.name}`)
		})
		lines.push("")
	}

	// 文本内容
	if (summary.textContent.length > 0) {
		lines.push("## Text Content")
		summary.textContent.forEach((snippet) => {
			const preview = snippet.name.length > 50 ? snippet.name.substring(0, 47) + "..." : snippet.name
			lines.push(`- ${preview}`)
		})
		lines.push("")
	}

	return lines.join("\n").trim()
}
