import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

export async function extractSnippetsFromCapturesForPHP(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForPHP]")

	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 收集所有类相关的captures用于确定field归属
	const classCaptures = captures
		.filter(
			(c) =>
				c.name === "definition.class" ||
				c.name === "definition.abstract_class" ||
				c.name === "definition.final_class" ||
				c.name === "definition.readonly_class",
		)
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			nameCap: captures.find(
				(nc) =>
					(nc.name === "name.definition.class" ||
						nc.name === "name.definition.abstract_class" ||
						nc.name === "name.definition.final_class" ||
						nc.name === "name.definition.readonly_class") &&
					nc.node.startIndex >= c.node.startIndex &&
					nc.node.endIndex <= c.node.endIndex,
			),
		}))

	// 收集接口、trait、枚举的captures
	const interfaceCaptures = captures
		.filter((c) => c.name === "definition.interface")
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			nameCap: captures.find(
				(nc) =>
					nc.name === "name.definition.interface" &&
					nc.node.startIndex >= c.node.startIndex &&
					nc.node.endIndex <= c.node.endIndex,
			),
		}))

	const traitCaptures = captures
		.filter((c) => c.name === "definition.trait")
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			nameCap: captures.find(
				(nc) =>
					nc.name === "name.definition.trait" &&
					nc.node.startIndex >= c.node.startIndex &&
					nc.node.endIndex <= c.node.endIndex,
			),
		}))

	const enumCaptures = captures
		.filter((c) => c.name === "definition.enum")
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			nameCap: captures.find(
				(nc) =>
					nc.name === "name.definition.enum" &&
					nc.node.startIndex >= c.node.startIndex &&
					nc.node.endIndex <= c.node.endIndex,
			),
		}))

	// 收集namespace captures用于确定namespace归属
	const namespaceCaptures = captures
		.filter((c) => c.name === "definition.namespace")
		.map((c) => ({
			node: c.node,
			start: c.node.startIndex,
			end: c.node.endIndex,
			nameCap: captures.find(
				(nc) =>
					nc.name === "name.definition.namespace" &&
					nc.node.startIndex >= c.node.startIndex &&
					nc.node.endIndex <= c.node.endIndex,
			),
		}))

	// 辅助函数：确定节点所属的namespace
	function findNamespaceForNode(node: Parser.SyntaxNode): string | undefined {
		// 在PHP中，namespace通常是文件级别的，找到最近的前面的namespace声明
		let closestNamespace: string | undefined = undefined
		let closestDistance = Infinity

		for (const ns of namespaceCaptures) {
			// namespace应该在当前节点之前定义
			if (ns.start < node.startIndex && ns.nameCap) {
				const distance = node.startIndex - ns.start
				if (distance < closestDistance) {
					closestDistance = distance
					closestNamespace = sourceCode.substring(ns.nameCap.node.startIndex, ns.nameCap.node.endIndex)
				}
			}
		}

		return closestNamespace
	}

	// 处理 use 语句 (imports)
	captures.forEach((capture) => {
		if (capture.name === "name.definition.use") {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) return
			seenHashes.add(snippetHash)
			const name = sourceCode.substring(node.startIndex, node.endIndex)
			const scope = buildScopeChain(node)
			const namespace = findNamespaceForNode(node)
			snippets.push({
				name,
				type: SnippetType.ImportOrInclude,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				scope,
				fileHash,
				namespace,
			})
		}
	})

	// 处理函数和方法
	for (const capture of captures) {
		if (
			capture.name !== "definition.function" &&
			capture.name !== "definition.method" &&
			capture.name !== "definition.static_method" &&
			capture.name !== "definition.abstract_method" &&
			capture.name !== "definition.final_method"
		)
			continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 确定名称capture的类型
		let nameCapName: string
		let paramCapName: string
		let bodyCapName: string

		if (capture.name === "definition.function") {
			nameCapName = "name.definition.function"
			paramCapName = "parameters.definition.function"
			bodyCapName = "body.definition.function"
		} else if (capture.name === "definition.method") {
			nameCapName = "name.definition.method"
			paramCapName = "parameters.definition.method"
			bodyCapName = "body.definition.method"
		} else if (capture.name === "definition.static_method") {
			nameCapName = "name.definition.static_method"
			paramCapName = "parameters.definition.static_method"
			bodyCapName = "body.definition.static_method"
		} else if (capture.name === "definition.abstract_method") {
			nameCapName = "name.definition.abstract_method"
			paramCapName = "parameters.definition.abstract_method"
			bodyCapName = "body.definition.abstract_method"
		} else if (capture.name === "definition.final_method") {
			nameCapName = "name.definition.final_method"
			paramCapName = "parameters.definition.final_method"
			bodyCapName = "body.definition.final_method"
		} else {
			continue
		}

		// 查找函数/方法名
		const nameCap = captures.find(
			(c) => c.name === nameCapName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// 解析参数
		const paramCap = captures.find(
			(c) => c.name === paramCapName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)
		const parameters: ParameterInfo[] = []
		if (paramCap) {
			const paramText = sourceCode.substring(paramCap.node.startIndex, paramCap.node.endIndex)
			const cleanText = removeParentheses(paramText).trim()
			if (cleanText) {
				const paramList = parsePHPParameters(cleanText)
				parameters.push(...paramList.map((param: string) => ({ name: param })))
			}
		}

		// 查找函数/方法体
		const bodyCap = captures.find(
			(c) => c.name === bodyCapName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)

		let rangeText: string
		let implementText: string | undefined = undefined
		if (bodyCap) {
			rangeText = sourceCode.substring(node.startIndex, bodyCap.node.startIndex).trimEnd()
			implementText = sourceCode.substring(node.startIndex, node.endIndex)
		} else {
			rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		}

		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const paramStr = parameters.map((p) => p.name).join(", ")
		const signature = `${name}(${paramStr})`
		const scope = buildScopeChain(node)

		// 确定归属的类/接口/trait/枚举
		let field: string | undefined = undefined
		const allParentCaptures = [...classCaptures, ...interfaceCaptures, ...traitCaptures, ...enumCaptures]
		for (const parent of allParentCaptures) {
			if (node.startIndex > parent.start && node.endIndex <= parent.end && parent.nameCap) {
				field = sourceCode.substring(parent.nameCap.node.startIndex, parent.nameCap.node.endIndex)
				break
			}
		}

		const namespace = findNamespaceForNode(node)

		snippets.push({
			name,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			namespace,
			definition: {
				name,
				type: capture.name === "definition.function" ? "function" : "method",
				parameters,
				returnType: "mixed", // PHP默认返回类型
			},
			parameters,
			signature,
			language: CodeLanguageType.PHP,
			...(implementText ? { implementText } : {}),
		})
	}

	// 处理属性（包括静态属性、只读属性、构造函数提升属性）
	for (const capture of captures) {
		if (
			capture.name !== "definition.property" &&
			capture.name !== "definition.static_property" &&
			capture.name !== "definition.readonly_property" &&
			capture.name !== "definition.promoted_property"
		)
			continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 确定名称capture的类型
		let nameCapName: string
		if (capture.name === "definition.property") {
			nameCapName = "name.definition.property"
		} else if (capture.name === "definition.static_property") {
			nameCapName = "name.definition.static_property"
		} else if (capture.name === "definition.readonly_property") {
			nameCapName = "name.definition.readonly_property"
		} else if (capture.name === "definition.promoted_property") {
			nameCapName = "name.definition.promoted_property"
		} else {
			continue
		}

		// 查找属性名
		const nameCap = captures.find(
			(c) => c.name === nameCapName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		// 确定归属的类/接口/trait/枚举
		let field: string | undefined = undefined
		const allParentCaptures = [...classCaptures, ...interfaceCaptures, ...traitCaptures, ...enumCaptures]
		for (const parent of allParentCaptures) {
			if (node.startIndex > parent.start && node.endIndex <= parent.end && parent.nameCap) {
				field = sourceCode.substring(parent.nameCap.node.startIndex, parent.nameCap.node.endIndex)
				break
			}
		}

		const namespace = findNamespaceForNode(node)

		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			namespace,
			language: CodeLanguageType.PHP,
		})
	}

	// 处理常量
	for (const capture of captures) {
		if (capture.name !== "definition.constant") continue

		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		const nameCap = captures.find(
			(c) =>
				c.name === "name.definition.constant" &&
				c.node.startIndex >= node.startIndex &&
				c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const scope = buildScopeChain(node)

		// 确定归属的类/接口/trait/枚举
		let field: string | undefined = undefined
		const allParentCaptures = [...classCaptures, ...interfaceCaptures, ...traitCaptures, ...enumCaptures]
		for (const parent of allParentCaptures) {
			if (node.startIndex > parent.start && node.endIndex <= parent.end && parent.nameCap) {
				field = sourceCode.substring(parent.nameCap.node.startIndex, parent.nameCap.node.endIndex)
				break
			}
		}

		const namespace = findNamespaceForNode(node)

		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			namespace,
			language: CodeLanguageType.PHP,
		})
	}

	// 处理类、接口、trait、枚举定义
	const typeDefs = [
		{ capture: "name.definition.class", outlineType: "class" },
		{ capture: "name.definition.abstract_class", outlineType: "abstract_class" },
		{ capture: "name.definition.final_class", outlineType: "final_class" },
		{ capture: "name.definition.readonly_class", outlineType: "readonly_class" },
		{ capture: "name.definition.interface", outlineType: "interface" },
		{ capture: "name.definition.trait", outlineType: "trait" },
		{ capture: "name.definition.enum", outlineType: "enum" },
	]

	for (const { capture, outlineType } of typeDefs) {
		const defCaptureName = `definition.${outlineType}`
		for (const cap of captures.filter((c) => c.name === capture)) {
			const node = cap.node
			// 判断是否在函数/方法内（局部类）
			const isInMethod = captures.some(
				(fc) =>
					(fc.name === "definition.function" ||
						fc.name === "definition.method" ||
						fc.name === "definition.static_method" ||
						fc.name === "definition.abstract_method" ||
						fc.name === "definition.final_method") &&
					fc.node.startIndex < node.startIndex &&
					node.endIndex < fc.node.endIndex,
			)
			if (isInMethod) continue

			// 优先使用 @definition.xxx 区间
			const defCap = captures.find(
				(c) =>
					c.name === defCaptureName &&
					c.node.startIndex <= node.startIndex &&
					node.endIndex <= c.node.endIndex,
			)
			const rangeText = defCap
				? sourceCode.substring(defCap.node.startIndex, defCap.node.endIndex)
				: sourceCode.substring(node.startIndex, node.endIndex)
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)
			const name = sourceCode.substring(node.startIndex, node.endIndex)
			const scope = buildScopeChain(node)
			const namespace = findNamespaceForNode(node)

			snippets.push({
				name,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				implementText: rangeText,
				scope,
				fileHash,
				namespace,
				language: CodeLanguageType.PHP,
			})
		}
	}

	return snippets
}

// 解析PHP参数的辅助函数
function parsePHPParameters(paramText: string): string[] {
	const params: string[] = []
	let current = ""
	let depth = 0
	let inString = false
	let stringChar = ""

	for (let i = 0; i < paramText.length; i++) {
		const char = paramText[i]
		const prev = i > 0 ? paramText[i - 1] : ""

		if (!inString) {
			if (char === '"' || char === "'") {
				inString = true
				stringChar = char
			} else if (char === "(" || char === "[" || char === "{") {
				depth++
			} else if (char === ")" || char === "]" || char === "}") {
				depth--
			} else if (char === "," && depth === 0) {
				if (current.trim()) {
					params.push(extractPHPParamName(current.trim()))
				}
				current = ""
				continue
			}
		} else {
			if (char === stringChar && prev !== "\\") {
				inString = false
				stringChar = ""
			}
		}

		current += char
	}

	if (current.trim()) {
		params.push(extractPHPParamName(current.trim()))
	}

	return params
}

// 从PHP参数字符串中提取参数名
function extractPHPParamName(param: string): string {
	// 移除类型提示、默认值等，只保留参数名
	// 例如: "string $name = 'default'" -> "$name"
	// 例如: "array &$items" -> "$items"
	// 例如: "...$args" -> "$args"

	// 首先处理引用符号和展开运算符
	let cleaned = removeReferenceAndSplatOperators(param)

	// 查找变量名（以$开头）
	const variableName = findPHPVariableName(cleaned)
	if (variableName) {
		return variableName
	}

	// 如果没有找到变量名，返回原始参数
	return param
}

// 移除括号的辅助函数
function removeParentheses(text: string): string {
	if (text.startsWith("(") && text.endsWith(")")) {
		return text.slice(1, -1)
	}
	return text
}

// 移除引用符号和展开运算符的辅助函数
function removeReferenceAndSplatOperators(param: string): string {
	let result = param.trim()

	// 移除开头的空白
	while (result.startsWith(" ") || result.startsWith("\t")) {
		result = result.slice(1)
	}

	// 移除引用符号 &
	if (result.startsWith("&")) {
		result = result.slice(1).trim()
	}

	// 移除展开运算符 ...
	if (result.startsWith("...")) {
		result = result.slice(3).trim()
	}

	return result
}

// 查找PHP变量名的辅助函数
function findPHPVariableName(text: string): string | null {
	// 查找以$开头的变量名
	for (let i = 0; i < text.length; i++) {
		if (text[i] === "$") {
			let variableName = "$"
			let j = i + 1

			// 提取变量名部分（字母、数字、下划线）
			while (j < text.length) {
				const char = text[j]
				if (isValidPHPVariableChar(char)) {
					variableName += char
					j++
				} else {
					break
				}
			}

			if (variableName.length > 1) {
				// 确保不只是$符号
				return variableName
			}
		}
	}

	return null
}

// 检查字符是否是有效的PHP变量字符
function isValidPHPVariableChar(char: string): boolean {
	return (char >= "a" && char <= "z") || (char >= "A" && char <= "Z") || (char >= "0" && char <= "9") || char === "_"
}

// 构建概要信息
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): OutlineData {
	const outline: OutlineData = {
		imports: [],
		classes: [],
		interfaces: [],
		traits: [],
		enums: [],
		functions: [],
		constants: [],
		namespaces: [],
	}

	for (const snippet of snippets) {
		// 收集namespace信息
		if (snippet.namespace && !outline.namespaces.includes(snippet.namespace)) {
			outline.namespaces.push(snippet.namespace)
		}

		switch (snippet.type) {
			case SnippetType.ImportOrInclude:
				outline.imports.push(snippet.name)
				break
			case SnippetType.ClassOrInterfaceOrStructOrEnum:
				if (snippet.name.includes("interface")) {
					outline.interfaces.push(snippet.name)
				} else if (snippet.name.includes("trait")) {
					outline.traits.push(snippet.name)
				} else if (snippet.name.includes("enum")) {
					outline.enums.push(snippet.name)
				} else {
					outline.classes.push(snippet.name)
				}
				break
			case SnippetType.FunctionOrMethod:
				outline.functions.push(snippet.name)
				break
			case SnippetType.VariableOrConstant:
				if (snippet.name.includes("const")) {
					outline.constants.push(snippet.name)
				}
				break
		}
	}

	return outline
}

// 格式化概要文本
export function formatOutlineText(outline: OutlineData): string {
	const sections: string[] = []

	if (outline.namespaces.length > 0) {
		sections.push(`Namespaces: ${outline.namespaces.join(", ")}`)
	}

	if (outline.imports.length > 0) {
		sections.push(`Imports: ${outline.imports.join(", ")}`)
	}

	if (outline.classes.length > 0) {
		sections.push(`Classes: ${outline.classes.join(", ")}`)
	}

	if (outline.interfaces.length > 0) {
		sections.push(`Interfaces: ${outline.interfaces.join(", ")}`)
	}

	if (outline.traits.length > 0) {
		sections.push(`Traits: ${outline.traits.join(", ")}`)
	}

	if (outline.enums.length > 0) {
		sections.push(`Enums: ${outline.enums.join(", ")}`)
	}

	if (outline.functions.length > 0) {
		sections.push(`Functions: ${outline.functions.join(", ")}`)
	}

	if (outline.constants.length > 0) {
		sections.push(`Constants: ${outline.constants.join(", ")}`)
	}

	return sections.join("\n")
}

// 定义概要数据结构
interface OutlineData {
	imports: string[]
	classes: string[]
	interfaces: string[]
	traits: string[]
	enums: string[]
	functions: string[]
	constants: string[]
	namespaces: string[]
}
