import Parser from "web-tree-sitter"
import { CodeLanguageType, ISnippetMeta, SnippetType } from "../types"

export interface JavaFunctionCallInfo {
	calledFunctionName: string
	callType:
		| "direct"
		| "method"
		| "static"
		| "field"
		| "constructor"
		| "this"
		| "super"
		| "chain"
		| "lambda"
		| "stream"
		| "array"
		| "collection"
		| "inner"
		| "package"
		| "generic"
		| "exception"
	receiver?: string // 对于方法调用，接收者的名称
	className?: string // 对于静态方法调用，类的名称
	packageName?: string // 对于包限定调用，包的名称
	fieldName?: string // 对于字段访问后的方法调用，字段的名称
	line: number
	column: number
	isGeneric?: boolean // 是否是泛型调用
	isException?: boolean // 是否在异常处理中
}

export interface JavaFunctionCallAnalysisResult {
	functionCalls: JavaFunctionCallInfo[]
	scopeInfo: {
		packageName?: string
		className?: string // 如果函数是某个类的方法
		functionName: string
	}
}

export function dependenciesCapturesProcessForJava(captures: Parser.QueryCapture[]): ISnippetMeta[] {
	const result = analyzeJavaFunctionCalls(captures)
	return result.functionCalls.map((call) => ({
		name: call.calledFunctionName,
		type: SnippetType.FunctionOrMethod,
		filePath: "",
		startLine: call.line,
		endLine: call.line,
		startPosition: {
			row: call.line,
			column: call.column,
		},
		endPosition: {
			row: call.line,
			column: call.column,
		},
		rangeText: "",
		fileHash: "",
		definition: {
			name: call.calledFunctionName,
			type: call.callType,
		},
		field: call.className || call.packageName,
		signature: `${call.calledFunctionName}()`,
		language: CodeLanguageType.Java,
	}))
}

/**
 * 分析 Java 语言的函数调用
 */
function analyzeJavaFunctionCalls(captures: Parser.QueryCapture[]): JavaFunctionCallAnalysisResult {
	const functionCalls: JavaFunctionCallInfo[] = []
	const scopeInfo = {
		packageName: undefined as string | undefined,
		className: undefined as string | undefined,
		functionName: "unknown",
	}

	// 用于临时存储同一行的方法调用信息
	const callsByLine = new Map<
		number,
		{
			functionName?: string
			receiver?: string
			className?: string
			packageName?: string
			fieldName?: string
			callType?: JavaFunctionCallInfo["callType"]
			isGeneric?: boolean
			isException?: boolean
		}
	>()

	// 处理各种类型的捕获
	for (const capture of captures) {
		const node = capture.node
		const captureText = node.text.trim()
		const line = node.startPosition.row

		switch (capture.name) {
			case "method.call.direct":
				// 直接方法调用
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "direct",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "method.call.name":
				// 方法调用的方法名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const methodCall = callsByLine.get(line)!
				methodCall.functionName = captureText
				methodCall.callType = "method"
				break

			case "method.call.receiver":
				// 方法调用的接收者
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.receiver = captureText
				break

			case "static.call.class":
				// 静态方法调用的类名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const staticCall = callsByLine.get(line)!
				staticCall.className = captureText
				staticCall.callType = "static"
				break

			case "static.call.method":
				// 静态方法调用的方法名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.functionName = captureText
				break

			case "this.call.method":
				// this方法调用
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "this",
					receiver: "this",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "super.call.method":
				// super方法调用
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "super",
					receiver: "super",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "constructor.call.name":
				// 构造函数调用
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "constructor",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "method.call.chain":
				// 链式方法调用
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "chain",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			default:
				break
		}
	}

	// 处理复合调用信息
	for (const [line, callInfo] of callsByLine) {
		if (callInfo.functionName) {
			const callData: JavaFunctionCallInfo = {
				calledFunctionName: callInfo.functionName,
				callType: callInfo.callType || "method",
				receiver: callInfo.receiver,
				className: callInfo.className,
				packageName: callInfo.packageName,
				fieldName: callInfo.fieldName,
				line: line + 1,
				column: 0,
				isGeneric: callInfo.isGeneric,
				isException: callInfo.isException,
			}

			functionCalls.push(callData)
		}
	}

	return { functionCalls, scopeInfo }
}

export function filterRelevantJavaFunctionCalls(
	callAnalysis: JavaFunctionCallAnalysisResult,
	allFunctions: ISnippetMeta[],
	currentFunction: ISnippetMeta,
	language: CodeLanguageType,
): ISnippetMeta[] {
	const relevantFunctions: ISnippetMeta[] = []

	for (const call of callAnalysis.functionCalls) {
		// 查找对应的函数定义
		const targetFunction = findJavaTargetFunction(call, allFunctions, callAnalysis.scopeInfo)

		if (targetFunction && targetFunction.name !== currentFunction.name) {
			// 检查是否已经添加过
			if (!relevantFunctions.find((f) => f.name === targetFunction.name)) {
				relevantFunctions.push(targetFunction)
			}
		}
	}

	return relevantFunctions
}

/**
 * 查找 Java 语言的目标函数
 */
function findJavaTargetFunction(
	call: JavaFunctionCallInfo,
	allFunctions: ISnippetMeta[],
	scopeInfo: JavaFunctionCallAnalysisResult["scopeInfo"],
): ISnippetMeta | null {
	switch (call.callType) {
		case "direct":
			// 直接方法调用：查找同类的方法
			return (
				allFunctions.find(
					(func) => func.name === call.calledFunctionName && func.field === scopeInfo.className,
				) || null
			)

		case "method":
			// 方法调用：查找指定对象的方法
			if (call.receiver === scopeInfo.className) {
				// 同类实例方法调用
				return (
					allFunctions.find(
						(func) => func.name === call.calledFunctionName && func.field === scopeInfo.className,
					) || null
				)
			}
			break

		case "this":
			// this方法调用：查找同类的方法
			return (
				allFunctions.find(
					(func) => func.name === call.calledFunctionName && func.field === scopeInfo.className,
				) || null
			)

		case "static":
			// 静态方法调用：查找指定类的静态方法
			return (
				allFunctions.find((func) => func.name === call.calledFunctionName && func.field === call.className) ||
				null
			)

		case "field":
			// 字段访问后的方法调用：通常是其他类的方法，可能不在同文件中
			return null

		case "constructor":
			// 构造函数调用：查找类定义
			return allFunctions.find((func) => func.name === call.calledFunctionName) || null

		case "super":
			// super调用：通常是父类方法，可能不在同文件中
			return null

		case "chain":
		case "lambda":
		case "stream":
		case "array":
		case "collection":
		case "inner":
		case "package":
		case "generic":
		case "exception":
			// 这些调用类型通常涉及内置方法、外部类或复杂调用链，不需要包含在上下文中
			return null
	}

	return null
}
