import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

/**
 * CSS特定的辅助函数
 */

/**
 * 解析CSS选择器节点，基于tree-sitter AST提取选择器名称
 */
function extractSelectorName(selectorsNode: Parser.SyntaxNode, sourceCode: string): string {
	// 递归查找第一个有意义的选择器名称
	function findSelectorName(node: Parser.SyntaxNode): string | null {
		// 处理类选择器
		if (node.type === "class_selector") {
			const classNameNode = findChildByType(node, "class_name")
			if (classNameNode) {
				return getNodeText(classNameNode, sourceCode)
			}
		}

		// 处理ID选择器
		if (node.type === "id_selector") {
			const idNameNode = findChildByType(node, "id_name")
			if (idNameNode) {
				return getNodeText(idNameNode, sourceCode)
			}
		}

		// 处理标签选择器
		if (node.type === "tag_name") {
			return getNodeText(node, sourceCode)
		}

		// 处理伪类/伪元素选择器
		if (node.type === "pseudo_class_selector" || node.type === "pseudo_element_selector") {
			const nameNode = findChildByType(node, "class_name") || findChildByType(node, "tag_name")
			if (nameNode) {
				return getNodeText(nameNode, sourceCode)
			}
		}

		// 递归查找子节点
		for (let i = 0; i < node.childCount; i++) {
			const child = node.child(i)
			if (child) {
				const name = findSelectorName(child)
				if (name) return name
			}
		}

		return null
	}

	const name = findSelectorName(selectorsNode)
	if (name) return name

	// 如果无法从AST中提取名称，回退到简单的文本处理
	const selectorText = getNodeText(selectorsNode, sourceCode).trim()

	let firstSelector = selectorText
	const separators = [" ", ",", ">", "+", "~"]
	for (const sep of separators) {
		const index = firstSelector.indexOf(sep)
		if (index >= 0) {
			firstSelector = firstSelector.substring(0, index)
		}
	}

	// 移除特殊字符
	if (firstSelector.startsWith(".")) return firstSelector.substring(1)
	if (firstSelector.startsWith("#")) return firstSelector.substring(1)

	return firstSelector || "selector"
}

/**
 * 判断是否为CSS变量（自定义属性）
 */
function isCSSVariable(propertyName: string): boolean {
	return propertyName.startsWith("--")
}

/**
 * 从节点中提取文本内容
 */
function getNodeText(node: Parser.SyntaxNode, sourceCode: string): string {
	return sourceCode.substring(node.startIndex, node.endIndex)
}

/**
 * 查找子节点按类型
 */
function findChildByType(node: Parser.SyntaxNode, type: string): Parser.SyntaxNode | null {
	for (let i = 0; i < node.childCount; i++) {
		const child = node.child(i)
		if (child && child.type === type) {
			return child
		}
	}
	return null
}

/**
 * 主要的CSS捕获处理函数
 * 从tree-sitter的captures中提取CSS代码片段信息
 */
export async function extractSnippetsFromCapturesForCSS(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForCSS]")
	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 处理捕获的每个节点
	for (const capture of captures) {
		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1
		const rangeText = getNodeText(node, sourceCode)

		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		// 处理CSS规则集（选择器）
		if (capture.name === "definition.rule" && snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)) {
			// 查找选择器capture
			const selectorsCapture = captures.find(
				(c) =>
					c.name === "selectors.definition.rule" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			if (selectorsCapture) {
				const selectorText = getNodeText(selectorsCapture.node, sourceCode)
				const selectorName = extractSelectorName(selectorsCapture.node, sourceCode)

				// 基于AST节点确定选择器类型
				let definitionType = "selector"

				// 递归查找选择器类型
				function findSelectorType(node: Parser.SyntaxNode): string {
					if (node.type === "class_selector") return "class_selector"
					if (node.type === "id_selector") return "id_selector"
					if (node.type === "tag_name") return "tag_selector"
					if (node.type === "pseudo_class_selector" || node.type === "pseudo_element_selector")
						return "pseudo_selector"

					for (let i = 0; i < node.childCount; i++) {
						const child = node.child(i)
						if (child) {
							const type = findSelectorType(child)
							if (type !== "selector") return type
						}
					}
					return "selector"
				}

				definitionType = findSelectorType(selectorsCapture.node)

				snippets.push({
					name: selectorName,
					type: SnippetType.ClassOrInterfaceOrStructOrEnum,
					filePath,
					startLine,
					endLine,
					rangeText,
					fileHash,
					language: CodeLanguageType.CSS,
					definition: {
						name: selectorName,
						type: definitionType,
					},
					definitionText: rangeText,
					signature: selectorName,
					scope: buildScopeChain(node),
				})
			}
		}

		// 处理@import语句
		else if (capture.name === "definition.import" && snippetTypes.includes(SnippetType.ImportOrInclude)) {
			// 查找导入名称capture
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition.import" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			let importName = "import"
			if (nameCapture) {
				// 使用字符串方法移除引号，代替正则表达式
				let rawName = getNodeText(nameCapture.node, sourceCode)
				if (rawName.startsWith('"') && rawName.endsWith('"')) {
					rawName = rawName.slice(1, -1)
				} else if (rawName.startsWith("'") && rawName.endsWith("'")) {
					rawName = rawName.slice(1, -1)
				}
				importName = rawName
			}

			snippets.push({
				name: importName,
				type: SnippetType.ImportOrInclude,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.CSS,
				definitionText: rangeText,
				signature: `@import "${importName}"`,
				scope: buildScopeChain(node),
			})
		}

		// 处理@keyframes动画
		else if (
			capture.name === "definition.keyframes" &&
			snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)
		) {
			// 查找keyframes名称capture
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition.keyframes" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			let keyframesName = "animation"
			if (nameCapture) {
				keyframesName = getNodeText(nameCapture.node, sourceCode)
			}

			snippets.push({
				name: keyframesName,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.CSS,
				definition: {
					name: keyframesName,
					type: "keyframes",
				},
				definitionText: rangeText,
				signature: keyframesName,
				scope: buildScopeChain(node),
			})
		}

		// 处理@media查询
		else if (
			capture.name === "definition.media" &&
			snippetTypes.includes(SnippetType.ClassOrInterfaceOrStructOrEnum)
		) {
			// 查找媒体查询capture
			const queryCapture = captures.find(
				(c) =>
					c.name === "query.definition.media" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			let mediaName = "media"
			if (queryCapture) {
				mediaName = getNodeText(queryCapture.node, sourceCode).trim()
			}

			snippets.push({
				name: mediaName,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				fileHash,
				language: CodeLanguageType.CSS,
				definition: {
					name: mediaName,
					type: "media",
				},
				definitionText: rangeText,
				signature: `@media ${mediaName}`,
				scope: buildScopeChain(node),
			})
		}

		// 处理CSS函数
		else if (capture.name === "definition.function" && snippetTypes.includes(SnippetType.FunctionOrMethod)) {
			// 查找函数名capture
			const nameCapture = captures.find(
				(c) =>
					c.name === "name.definition.function" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			if (nameCapture) {
				const functionName = getNodeText(nameCapture.node, sourceCode)

				// 查找参数capture
				const argsCapture = captures.find(
					(c) =>
						c.name === "args.definition.function" &&
						c.node.startIndex >= node.startIndex &&
						c.node.endIndex <= node.endIndex,
				)

				const parameters: ParameterInfo[] = []
				if (argsCapture) {
					const argsText = getNodeText(argsCapture.node, sourceCode)
					// 简单的参数分割，处理逗号分隔的参数
					const args = argsText
						.split(",")
						.map((arg) => arg.trim())
						.filter((arg) => arg.length > 0)
					parameters.push(...args.map((arg) => ({ name: arg })))
				}

				const signature =
					parameters.length > 0
						? `${functionName}(${parameters.map((p) => p.name).join(", ")})`
						: `${functionName}()`

				snippets.push({
					name: functionName,
					type: SnippetType.FunctionOrMethod,
					filePath,
					startLine,
					endLine,
					rangeText,
					fileHash,
					language: CodeLanguageType.CSS,
					parameters,
					definition: {
						name: functionName,
						type: "function",
						parameters,
					},
					definitionText: rangeText,
					signature,
					scope: buildScopeChain(node),
				})
			}
		}

		// 处理CSS声明（变量和属性）
		else if (capture.name === "definition.declaration" && snippetTypes.includes(SnippetType.VariableOrConstant)) {
			// 查找属性名capture
			const propertyCapture = captures.find(
				(c) =>
					c.name === "property.definition.declaration" &&
					c.node.startIndex >= node.startIndex &&
					c.node.endIndex <= node.endIndex,
			)

			if (propertyCapture) {
				const propertyName = getNodeText(propertyCapture.node, sourceCode)

				// 只处理CSS变量
				if (isCSSVariable(propertyName)) {
					// 查找值capture
					const valueCapture = captures.find(
						(c) =>
							c.name === "value.definition.declaration" &&
							c.node.startIndex >= node.startIndex &&
							c.node.endIndex <= node.endIndex,
					)

					let propertyValue = ""
					if (valueCapture) {
						propertyValue = getNodeText(valueCapture.node, sourceCode)
					}

					snippets.push({
						name: propertyName,
						type: SnippetType.VariableOrConstant,
						filePath,
						startLine,
						endLine,
						rangeText,
						fileHash,
						language: CodeLanguageType.CSS,
						definition: {
							name: propertyName,
							type: "css_variable",
						},
						definitionText: `${propertyName}: ${propertyValue};`,
						signature: `${propertyName}: ${propertyValue}`,
						scope: buildScopeChain(node),
					})
				}
			}
		}
	}

	logger.info(`Extracted ${snippets.length} CSS snippets`)
	return snippets
}

/**
 * 构建CSS文件的摘要信息
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): {
	selectors: ISnippetMeta[]
	variables: ISnippetMeta[]
	functions: ISnippetMeta[]
	atRules: ISnippetMeta[]
	imports: ISnippetMeta[]
} {
	const selectors: ISnippetMeta[] = []
	const variables: ISnippetMeta[] = []
	const functions: ISnippetMeta[] = []
	const atRules: ISnippetMeta[] = []
	const imports: ISnippetMeta[] = []

	for (const snippet of snippets) {
		switch (snippet.type) {
			case SnippetType.ImportOrInclude:
				imports.push(snippet)
				break
			case SnippetType.FunctionOrMethod:
				functions.push(snippet)
				break
			case SnippetType.VariableOrConstant:
				variables.push(snippet)
				break
			case SnippetType.ClassOrInterfaceOrStructOrEnum:
				if (snippet.definition?.type?.includes("selector")) {
					selectors.push(snippet)
				} else {
					atRules.push(snippet)
				}
				break
		}
	}

	return { selectors, variables, functions, atRules, imports }
}

/**
 * 格式化CSS文件的大纲文本
 */
export function formatOutlineText(summary: {
	selectors: ISnippetMeta[]
	variables: ISnippetMeta[]
	functions: ISnippetMeta[]
	atRules: ISnippetMeta[]
	imports: ISnippetMeta[]
}): string {
	let outline = "CSS File Overview:\n\n"

	if (summary.imports.length > 0) {
		outline += "Imports:\n"
		for (const imp of summary.imports) {
			outline += `  - ${imp.signature}\n`
		}
		outline += "\n"
	}

	if (summary.variables.length > 0) {
		outline += "CSS Variables & Properties:\n"
		for (const variable of summary.variables) {
			outline += `  - ${variable.signature}\n`
		}
		outline += "\n"
	}

	if (summary.selectors.length > 0) {
		outline += "Selectors:\n"
		for (const selector of summary.selectors) {
			const type = selector.definition?.type || "selector"
			outline += `  - ${selector.name} (${type})\n`
		}
		outline += "\n"
	}

	if (summary.functions.length > 0) {
		outline += "CSS Functions:\n"
		for (const func of summary.functions) {
			outline += `  - ${func.signature}\n`
		}
		outline += "\n"
	}

	if (summary.atRules.length > 0) {
		outline += "At-Rules:\n"
		for (const rule of summary.atRules) {
			outline += `  - @${rule.definition?.type}: ${rule.name}\n`
		}
		outline += "\n"
	}

	return outline.trim()
}
