import Parser from "web-tree-sitter"
import { CodeLanguageType, ISnippetMeta, SnippetType } from "../types"

export interface TypeScriptFunctionCallInfo {
	calledFunctionName: string
	callType:
		| "direct"
		| "method"
		| "static"
		| "namespace"
		| "constructor"
		| "chain"
		| "this"
		| "super"
		| "arrow"
		| "async"
		| "array"
		| "promise"
		| "optional"
		| "generic"
		| "template"
		| "nested"
	receiver?: string // 对于方法调用，接收者的名称
	className?: string // 对于静态方法调用，类的名称
	namespace?: string // 对于命名空间调用，命名空间的名称
	line: number
	column: number
	isAsync?: boolean // 是否是异步调用
	isOptional?: boolean // 是否是可选链调用
	isGeneric?: boolean // 是否是泛型调用
}

export interface TypeScriptFunctionCallAnalysisResult {
	functionCalls: TypeScriptFunctionCallInfo[]
	scopeInfo: {
		moduleName?: string
		className?: string // 如果函数是某个类的方法
		functionName: string
		isAsync?: boolean
	}
}

export function dependenciesCapturesProcessForTypeScript(captures: Parser.QueryCapture[]): ISnippetMeta[] {
	const result = analyzeTypeScriptFunctionCalls(captures)
	return result.functionCalls.map((call) => ({
		name: call.calledFunctionName,
		type: SnippetType.FunctionOrMethod,
		filePath: "",
		startLine: call.line,
		endLine: call.line,
		startPosition: {
			row: call.line,
			column: call.column,
		},
		endPosition: {
			row: call.line,
			column: call.column,
		},
		rangeText: "",
		fileHash: "",
		definition: {
			name: call.calledFunctionName,
			type: call.callType,
		},
		field: call.className || call.namespace,
		signature: `${call.calledFunctionName}()`,
		language: CodeLanguageType.TypeScript,
	}))
}

/**
 * 分析 TypeScript/JavaScript 的函数调用
 */
function analyzeTypeScriptFunctionCalls(captures: Parser.QueryCapture[]): TypeScriptFunctionCallAnalysisResult {
	const functionCalls: TypeScriptFunctionCallInfo[] = []
	const scopeInfo = {
		moduleName: undefined as string | undefined,
		className: undefined as string | undefined,
		functionName: "unknown",
		isAsync: false,
	}

	// 用于临时存储调用信息，按位置分组
	const callGroups = new Map<string, { [key: string]: string }>()

	// 用于临时存储同一行的调用信息
	const callsByLine = new Map<
		number,
		{
			functionName?: string
			receiver?: string
			className?: string
			namespace?: string
			callType?: TypeScriptFunctionCallInfo["callType"]
			isAsync?: boolean
			isOptional?: boolean
			isGeneric?: boolean
		}
	>()

	// 处理各种类型的捕获
	for (const capture of captures) {
		const node = capture.node
		const captureText = node.text.trim()
		const line = node.startPosition.row

		switch (capture.name) {
			case "function.call.direct":
				// 直接函数调用

				functionCalls.push({
					calledFunctionName: captureText,
					callType: "direct",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "method.call.name":
				// 方法调用的方法名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const methodCall = callsByLine.get(line)!
				methodCall.functionName = captureText
				methodCall.callType = "method"
				break

			case "method.call.receiver":
				// 方法调用的接收者
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.receiver = captureText
				break

			case "static.call.class":
				// 静态方法调用的类名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const staticCall = callsByLine.get(line)!
				staticCall.className = captureText
				staticCall.callType = "static"
				break

			case "static.call.method":
				// 静态方法调用的方法名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.functionName = captureText
				break

			case "namespace.call.name":
				// 命名空间调用的命名空间名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const namespaceCall = callsByLine.get(line)!
				namespaceCall.namespace = captureText
				namespaceCall.callType = "namespace"
				break

			case "namespace.call.function":
				// 命名空间调用的函数名
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.functionName = captureText
				break

			case "this.call.method":
				// this方法调用

				functionCalls.push({
					calledFunctionName: captureText,
					callType: "this",
					receiver: "this",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "super.call.method":
				// super方法调用

				functionCalls.push({
					calledFunctionName: captureText,
					callType: "super",
					receiver: "super",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "constructor.call.name":
				// 构造函数调用

				functionCalls.push({
					calledFunctionName: captureText,
					callType: "constructor",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "method.call.chain":
				// 链式方法调用

				functionCalls.push({
					calledFunctionName: captureText,
					callType: "chain",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "array.call.method":
				// 数组方法调用
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const arrayCall = callsByLine.get(line)!
				arrayCall.functionName = captureText
				arrayCall.callType = "array"
				break

			case "array.call.object":
				// 数组对象
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.receiver = captureText
				break

			case "promise.call.method":
				// Promise方法调用 - 使用简单的按行分组
				const promiseGroupKey = `promise-${line}`
				if (!callGroups.has(promiseGroupKey)) {
					callGroups.set(promiseGroupKey, {})
				}
				callGroups.get(promiseGroupKey)!["method"] = captureText
				callGroups.get(promiseGroupKey)!["line"] = line.toString()
				callGroups.get(promiseGroupKey)!["column"] = node.startPosition.column.toString()
				break

			case "promise.call.object":
				// Promise对象
				const promiseObjGroupKey = `promise-${line}`
				if (!callGroups.has(promiseObjGroupKey)) {
					callGroups.set(promiseObjGroupKey, {})
				}
				callGroups.get(promiseObjGroupKey)!["receiver"] = captureText
				break

			case "await.call.function":
				// await函数调用

				functionCalls.push({
					calledFunctionName: captureText,
					callType: "async",
					isAsync: true,
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "async.call.method":
				// 异步方法调用
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const asyncCall = callsByLine.get(line)!
				asyncCall.functionName = captureText
				asyncCall.callType = "async"
				asyncCall.isAsync = true
				break

			case "async.call.receiver":
				// 异步方法接收者
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.receiver = captureText
				break

			case "optional.call.method":
				// 可选链方法调用
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const optionalCall = callsByLine.get(line)!
				optionalCall.functionName = captureText
				optionalCall.callType = "optional"
				optionalCall.isOptional = true
				break

			case "optional.call.receiver":
				// 可选链接收者
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.receiver = captureText
				break

			case "generic.call.function":
				// 泛型函数调用
				console.log(`[analyzeTypeScriptFunctionCalls] Adding generic function call: ${captureText}`)
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "generic",
					isGeneric: true,
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "template.call.function":
				// 模板字符串中的函数调用
				console.log(`[analyzeTypeScriptFunctionCalls] Adding template function call: ${captureText}`)
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "template",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "nested.call.root":
				// 嵌套调用的根对象
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				const nestedCall = callsByLine.get(line)!
				nestedCall.receiver = captureText
				nestedCall.callType = "nested"
				break

			case "nested.call.method":
				// 嵌套调用的方法
				if (!callsByLine.has(line)) {
					callsByLine.set(line, {})
				}
				callsByLine.get(line)!.functionName = captureText
				break

			default:
				break
		}
	}

	// 处理独立的调用组
	for (const [groupKey, group] of callGroups) {
		if (groupKey.startsWith("promise-") && group["method"] && group["receiver"]) {
			functionCalls.push({
				calledFunctionName: group["method"],
				callType: "promise",
				receiver: group["receiver"],
				line: parseInt(group["line"]) + 1,
				column: parseInt(group["column"]),
			})
		}
	}

	// 处理复合调用信息 - 支持多个独立的函数调用
	const processedCalls = new Set<string>() // 防止重复处理

	for (const [line, callInfo] of callsByLine) {
		if (callInfo.functionName) {
			// 检查是否有明确的特殊类型标识
			const callTypes = []

			// Promise调用优先级最高
			if (callInfo.callType === "promise") {
				callTypes.push({
					type: "promise" as TypeScriptFunctionCallInfo["callType"],
					receiver: callInfo.receiver,
					functionName: callInfo.functionName,
				})
			}

			// 数组调用
			if (callInfo.callType === "array") {
				callTypes.push({
					type: "array" as TypeScriptFunctionCallInfo["callType"],
					receiver: callInfo.receiver,
					functionName: callInfo.functionName,
				})
			}

			// 静态调用 (有className)
			if (callInfo.className) {
				callTypes.push({
					type: "static" as TypeScriptFunctionCallInfo["callType"],
					receiver: callInfo.className,
					functionName: callInfo.functionName,
					className: callInfo.className,
				})
			}

			// 命名空间调用
			if (callInfo.namespace) {
				callTypes.push({
					type: "namespace" as TypeScriptFunctionCallInfo["callType"],
					receiver: callInfo.namespace,
					functionName: callInfo.functionName,
					namespace: callInfo.namespace,
				})
			}

			// 异步调用
			if (callInfo.isAsync) {
				callTypes.push({
					type: "async" as TypeScriptFunctionCallInfo["callType"],
					receiver: callInfo.receiver,
					functionName: callInfo.functionName,
					isAsync: true,
				})
			}

			// 嵌套调用
			if (callInfo.callType === "nested") {
				callTypes.push({
					type: "nested" as TypeScriptFunctionCallInfo["callType"],
					receiver: callInfo.receiver,
					functionName: callInfo.functionName,
				})
			}

			// 如果没有特殊类型，默认为普通方法调用
			if (callTypes.length === 0 && callInfo.receiver) {
				callTypes.push({
					type: "method" as TypeScriptFunctionCallInfo["callType"],
					receiver: callInfo.receiver,
					functionName: callInfo.functionName,
				})
			}

			// 为每个识别的调用类型创建记录
			for (const callType of callTypes) {
				const callKey = `${line}-${callType.type}-${callType.functionName}-${callType.receiver || ""}`
				if (!processedCalls.has(callKey)) {
					processedCalls.add(callKey)

					functionCalls.push({
						calledFunctionName: callType.functionName,
						callType: callType.type,
						receiver: callType.receiver,
						className: callType.className,
						namespace: callType.namespace,
						line: line + 1,
						column: 0,
						isAsync: callType.isAsync,
						isOptional: callInfo.isOptional,
						isGeneric: callInfo.isGeneric,
					})
				}
			}
		}
	}

	return { functionCalls, scopeInfo }
}

export function filterRelevantTypeScriptFunctionCalls(
	callAnalysis: TypeScriptFunctionCallAnalysisResult,
	allFunctions: ISnippetMeta[],
	currentFunction: ISnippetMeta,
	language: CodeLanguageType,
): ISnippetMeta[] {
	const relevantFunctions: ISnippetMeta[] = []

	for (const call of callAnalysis.functionCalls) {
		// 查找对应的函数定义
		const targetFunction = findTypeScriptTargetFunction(call, allFunctions, callAnalysis.scopeInfo)

		if (targetFunction && targetFunction.name !== currentFunction.name) {
			// 检查是否已经添加过
			if (!relevantFunctions.find((f) => f.name === targetFunction.name)) {
				relevantFunctions.push(targetFunction)
				console.log(
					`[TypeScriptFunctionCallAnalyzer] Found relevant function call: ${call.calledFunctionName} (${call.callType})`,
				)
			}
		}
	}

	return relevantFunctions
}

/**
 * 查找 TypeScript 的目标函数
 */
function findTypeScriptTargetFunction(
	call: TypeScriptFunctionCallInfo,
	allFunctions: ISnippetMeta[],
	scopeInfo: TypeScriptFunctionCallAnalysisResult["scopeInfo"],
): ISnippetMeta | null {
	switch (call.callType) {
		case "direct":
			// 直接函数调用：查找同模块的函数
			return (
				allFunctions.find(
					(func) => func.name === call.calledFunctionName && !func.field, // 模块级函数通常没有field
				) || null
			)

		case "method":
		case "this":
			// 方法调用：查找同类的方法
			if (call.receiver === "this" || call.receiver === scopeInfo.className) {
				return (
					allFunctions.find(
						(func) => func.name === call.calledFunctionName && func.field === scopeInfo.className,
					) || null
				)
			}
			break

		case "static":
			// 静态方法调用：查找指定类的静态方法
			return (
				allFunctions.find((func) => func.name === call.calledFunctionName && func.field === call.className) ||
				null
			)

		case "namespace":
			// 命名空间调用：查找指定命名空间的函数
			return (
				allFunctions.find((func) => func.name === call.calledFunctionName && func.field === call.namespace) ||
				null
			)

		case "constructor":
			// 构造函数调用：查找类定义
			return allFunctions.find((func) => func.name === call.calledFunctionName) || null

		case "super":
			// super调用：通常是父类方法，可能不在同文件中
			return null

		case "chain":
		case "array":
		case "promise":
		case "optional":
		case "async":
		case "generic":
		case "template":
		case "nested":
			// 这些调用类型通常是内置方法或链式调用，不需要包含在上下文中
			return null
	}

	return null
}
