import { CodeLanguageType } from "../types"

const lineCommentSymbols = {
	[CodeLanguageType.Go]: "//",
	[CodeLanguageType.Python]: "#",
	[CodeLanguageType.TypeScript]: "//",
	[CodeLanguageType.JavaScript]: "//",
	[CodeLanguageType.Java]: "//",
	[CodeLanguageType.Unknown]: "",
}

export function formatCodeWithLanguageWithLineComment(text: string, language: CodeLanguageType): string {
	if (language === CodeLanguageType.Unknown) {
		return text
	}
	const lineCommentSymbol = lineCommentSymbols[language]
	if (!lineCommentSymbol) {
		return text
	}

	const lines = text.split("\n")
	const formattedLines = lines.map((line) => {
		return `${lineCommentSymbol} ${line}`
	})
	return formattedLines.join("\n")
}
