// CTCode/src/autocompletion/v2/capturesProcess/functionQuery/analyzer.ts

import Parser from "web-tree-sitter"
import { CodeLanguageType, ISnippetMeta, SnippetType } from "../types"

export interface FunctionCallInfo {
	calledFunctionName: string
	callType: "direct" | "method" | "package" | "literal"
	receiver?: string // 对于方法调用，接收者的名称
	packageQualifier?: string // 对于包函数调用，包的名称
	line: number
	column: number
}

export interface FunctionCallAnalysisResult {
	functionCalls: FunctionCallInfo[]
	scopeInfo: {
		packageName?: string
		typeName?: string // 如果函数是某个类型的方法
		functionName: string
	}
}

export function dependenciesCapturesProcessForGo(captures: Parser.QueryCapture[]): ISnippetMeta[] {
	const result = analyzeGoFunctionCalls(captures)
	return result.functionCalls.map((call) => ({
		name: call.calledFunctionName,
		type: SnippetType.FunctionOrMethod,
		filePath: "",
		startLine: call.line,
		endLine: call.line,
		startPosition: {
			row: call.line,
			column: call.column,
		},
		endPosition: {
			row: call.line,
			column: call.column,
		},
		rangeText: "",
		fileHash: "",
	}))
}

/**
 * 分析 Go 语言的函数调用
 */
function analyzeGoFunctionCalls(captures: Parser.QueryCapture[]): FunctionCallAnalysisResult {
	const functionCalls: FunctionCallInfo[] = []
	const scopeInfo = {
		packageName: undefined as string | undefined,
		typeName: undefined as string | undefined,
		functionName: "unknown",
	}

	console.log(`[analyzeGoFunctionCalls] Processing ${captures.length} captures`)

	// 用于临时存储同一行的方法调用信息
	const methodCallsByLine = new Map<
		number,
		{
			functionName?: string
			receiver?: string
		}
	>()

	// 处理各种类型的捕获
	for (const capture of captures) {
		const node = capture.node
		const captureText = node.text.trim()
		const line = node.startPosition.row

		console.log(`[analyzeGoFunctionCalls] Processing capture: ${capture.name} = "${captureText}"`)

		switch (capture.name) {
			case "function.call.direct":
				// 直接函数调用
				console.log(`[analyzeGoFunctionCalls] Adding direct function call: ${captureText}`)
				functionCalls.push({
					calledFunctionName: captureText,
					callType: "direct",
					line: line + 1,
					column: node.startPosition.column,
				})
				break

			case "method.call.name":
				// 方法调用的方法名
				if (!methodCallsByLine.has(line)) {
					methodCallsByLine.set(line, {})
				}
				methodCallsByLine.get(line)!.functionName = captureText
				break

			case "method.call.receiver":
				// 方法调用的接收者
				if (!methodCallsByLine.has(line)) {
					methodCallsByLine.set(line, {})
				}
				methodCallsByLine.get(line)!.receiver = captureText
				break

			default:
				console.log(`[analyzeGoFunctionCalls] Unhandled capture type: ${capture.name}`)
				break
		}
	}

	// 处理方法调用信息
	for (const [line, callInfo] of methodCallsByLine) {
		if (callInfo.functionName && callInfo.receiver) {
			console.log(`[analyzeGoFunctionCalls] Adding method call: ${callInfo.receiver}.${callInfo.functionName}`)
			functionCalls.push({
				calledFunctionName: callInfo.functionName,
				callType: "method",
				receiver: callInfo.receiver,
				line: line + 1,
				column: 0,
			})
		}
	}

	console.log(
		`[analyzeGoFunctionCalls] Final result: ${functionCalls.length} function calls, scope: ${JSON.stringify(scopeInfo)}`,
	)

	return { functionCalls, scopeInfo }
}

export function filterRelevantFunctionCalls(
	callAnalysis: FunctionCallAnalysisResult,
	allFunctions: ISnippetMeta[],
	currentFunction: ISnippetMeta,
	language: CodeLanguageType,
): ISnippetMeta[] {
	const relevantFunctions: ISnippetMeta[] = []

	for (const call of callAnalysis.functionCalls) {
		// 查找对应的函数定义
		const targetFunction = findTargetFunction(call, allFunctions, callAnalysis.scopeInfo, language)

		if (targetFunction && targetFunction.name !== currentFunction.name) {
			// 检查是否已经添加过
			if (!relevantFunctions.find((f) => f.name === targetFunction.name)) {
				relevantFunctions.push(targetFunction)
				console.log(
					`[FunctionCallAnalyzer] Found relevant function call: ${call.calledFunctionName} (${call.callType})`,
				)
			}
		}
	}

	return relevantFunctions
}

/**
 * 查找目标函数定义
 */
function findTargetFunction(
	call: FunctionCallInfo,
	allFunctions: ISnippetMeta[],
	scopeInfo: FunctionCallAnalysisResult["scopeInfo"],
	language: CodeLanguageType,
): ISnippetMeta | null {
	switch (language) {
		case CodeLanguageType.Go:
			return findGoTargetFunction(call, allFunctions, scopeInfo)
		default:
			// 通用查找逻辑
			return allFunctions.find((func) => func.name === call.calledFunctionName) || null
	}
}

/**
 * 查找 Go 语言的目标函数
 */
function findGoTargetFunction(
	call: FunctionCallInfo,
	allFunctions: ISnippetMeta[],
	scopeInfo: FunctionCallAnalysisResult["scopeInfo"],
): ISnippetMeta | null {
	switch (call.callType) {
		case "direct":
			// 直接函数调用：查找同包的函数
			return (
				allFunctions.find(
					(func) => func.name === call.calledFunctionName && !func.scope?.length, // 包级函数通常没有额外的作用域
				) || null
			)

		case "method":
			// 方法调用：查找同类型的方法
			if (call.receiver === "this" || call.receiver === scopeInfo.typeName) {
				// 同类型的方法调用
				return (
					allFunctions.find(
						(func) =>
							func.name === call.calledFunctionName && func.scope?.includes(scopeInfo.typeName || ""),
					) || null
				)
			}
			break

		case "package":
			// 包函数调用：通常是外部包，不包含在同文件上下文中
			return null

		case "literal":
			// 函数字面量调用：通常不需要包含在上下文中
			return null
	}

	return null
}
