import { SnippetType, ISnippetMeta, ParameterInfo, CodeLanguageType } from "../types"
import Parser from "web-tree-sitter"
import { generateSnippetHash, buildScopeChain } from "./general"
import { Logger } from "../Logger"

export async function extractSnippetsFromCapturesForJava(
	captures: Parser.QueryCapture[],
	sourceCode: string,
	filePath: string,
	fileHash: string,
	snippetTypes: SnippetType[],
	options: { maxSnippetLines: number; minSnippetLines: number },
): Promise<ISnippetMeta[]> {
	const logger = Logger.getDefaultLogger().with("[extractSnippetsFromCapturesForJava]")

	const snippets: ISnippetMeta[] = []
	const seenHashes = new Set<string>()

	if (snippetTypes.length === 0) {
		logger.warn(`No snippet types provided, skipping`)
		return snippets
	}

	// 处理 import 和 package
	captures.forEach((capture) => {
		if (capture.name === "name.definition.import") {
			const node = capture.node
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) return
			seenHashes.add(snippetHash)
			const name = sourceCode.substring(node.startIndex, node.endIndex)
			const scope = buildScopeChain(node)
			snippets.push({
				name,
				type: SnippetType.ImportOrInclude,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				scope,
				fileHash,
			})
		}
	})

	// 处理 method 和 constructor
	for (const capture of captures) {
		if (capture.name !== "definition.method" && capture.name !== "definition.constructor") continue
		const node = capture.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		const isConstructor = capture.name === "definition.constructor"
		const nameCapName = isConstructor ? "name.definition.constructor" : "name.definition.method"
		const paramCapName = isConstructor ? "parameters.definition.constructor" : "parameters.definition.method"
		const bodyCapName = isConstructor ? "body.definition.constructor" : "body.definition.method"

		const nameCap = captures.find(
			(c) => c.name === nameCapName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)
		if (!nameCap) continue
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// 解析参数
		const paramCap = captures.find(
			(c) => c.name === paramCapName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)
		const parameters: ParameterInfo[] = []
		if (paramCap) {
			const paramText = sourceCode.substring(paramCap.node.startIndex, paramCap.node.endIndex)
			// 解析 Java 参数: (Type param1, Type param2, ...)
			const cleanText = paramText.replace(/^\(|\)$/g, "").trim()
			if (cleanText) {
				// 更好的Java参数解析，保持完整的类型信息
				const paramList = parseJavaParameters(cleanText)
				parameters.push(...paramList.map((param: string) => ({ name: param })))
			}
		}

		// 查找方法体
		const bodyCap = captures.find(
			(c) => c.name === bodyCapName && c.node.startIndex >= node.startIndex && c.node.endIndex <= node.endIndex,
		)

		let rangeText: string
		let implementText: string | undefined = undefined
		if (bodyCap) {
			rangeText = sourceCode.substring(node.startIndex, bodyCap.node.startIndex).trimEnd()
			implementText = sourceCode.substring(node.startIndex, node.endIndex)
		} else {
			rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		}

		const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		const paramStr = parameters.map((p) => p.name).join(", ")
		const signature = `${name}(${paramStr})`
		const scope = buildScopeChain(node)

		// 查找归属的类/接口/枚举/record
		let field: string | undefined = undefined
		const parentTypes = ["class", "interface", "enum", "record", "annotation"]
		for (const parentType of parentTypes) {
			const parentCap = captures.find(
				(c) =>
					c.name === `definition.${parentType}` &&
					c.node.startIndex < node.startIndex &&
					node.endIndex < c.node.endIndex,
			)
			if (parentCap) {
				const parentNameCap = captures.find(
					(c) =>
						c.name === `name.definition.${parentType}` &&
						c.node.startIndex >= parentCap.node.startIndex &&
						c.node.endIndex <= parentCap.node.endIndex,
				)
				if (parentNameCap) {
					field = sourceCode.substring(parentNameCap.node.startIndex, parentNameCap.node.endIndex)
					break
				}
			}
		}

		snippets.push({
			name,
			type: SnippetType.FunctionOrMethod,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			scope,
			fileHash,
			field,
			definition: {
				name,
				type: isConstructor ? "constructor" : "method",
				parameters,
				returnType: isConstructor ? "" : "void", // Java 方法默认返回类型
			},
			parameters,
			signature,
			language: CodeLanguageType.Java,
			...(implementText ? { implementText } : {}),
		})
	}

	// 处理 class/interface/enum/record/annotation
	const typeDefs = [
		{ capture: "name.definition.class", outlineType: "class" },
		{ capture: "name.definition.interface", outlineType: "interface" },
		{ capture: "name.definition.enum", outlineType: "enum" },
		{ capture: "name.definition.record", outlineType: "record" },
		{ capture: "name.definition.annotation", outlineType: "annotation" },
	]

	for (const { capture, outlineType } of typeDefs) {
		const defCaptureName = `definition.${outlineType}`
		for (const cap of captures.filter((c) => c.name === capture)) {
			const node = cap.node
			// 判断是否在方法/构造函数内（局部类）
			const isInMethod = captures.some(
				(fc) =>
					(fc.name === "definition.method" || fc.name === "definition.constructor") &&
					fc.node.startIndex < node.startIndex &&
					node.endIndex < fc.node.endIndex,
			)
			if (isInMethod) continue

			// 优先用 @definition.xxx 区间
			const defCap = captures.find(
				(c) =>
					c.name === defCaptureName &&
					c.node.startIndex <= node.startIndex &&
					node.endIndex <= c.node.endIndex,
			)
			const rangeText = defCap
				? sourceCode.substring(defCap.node.startIndex, defCap.node.endIndex)
				: sourceCode.substring(node.startIndex, node.endIndex)
			const startLine = node.startPosition.row + 1
			const endLine = node.endPosition.row + 1
			const snippetHash = generateSnippetHash(filePath, startLine, endLine, rangeText)
			if (seenHashes.has(snippetHash)) continue
			seenHashes.add(snippetHash)
			const name = sourceCode.substring(node.startIndex, node.endIndex)
			snippets.push({
				name,
				type: SnippetType.ClassOrInterfaceOrStructOrEnum,
				filePath,
				startLine,
				endLine,
				rangeText,
				definitionText: rangeText,
				implementText: rangeText,
				scope: [],
				fileHash,
				field: "",
				definition: { name, type: outlineType },
				language: CodeLanguageType.Java,
			})
		}
	}

	// 处理 field（字段）- 直接处理每个字段名称
	const fieldNameCaptures = captures.filter((c) => c.name === "name.definition.field")
	for (const nameCap of fieldNameCaptures) {
		const name = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)

		// 找到包含这个名称的field_declaration节点
		const fieldCap = captures.find(
			(c) =>
				c.name === "definition.field" &&
				c.node.startIndex <= nameCap.node.startIndex &&
				nameCap.node.endIndex <= c.node.endIndex,
		)

		if (!fieldCap) continue

		const node = fieldCap.node
		const startLine = node.startPosition.row + 1
		const endLine = node.endPosition.row + 1

		// 判断是否在方法内（局部变量）
		const isInMethod = captures.some(
			(fc) =>
				(fc.name === "definition.method" || fc.name === "definition.constructor") &&
				fc.node.startIndex < node.startIndex &&
				node.endIndex < fc.node.endIndex,
		)
		if (isInMethod) continue

		const rangeText = sourceCode.substring(node.startIndex, node.endIndex)
		// 对于字段，使用变量名的位置来生成hash，避免同一行多个变量被去重
		const nameStartLine = nameCap.node.startPosition.row + 1
		const nameEndLine = nameCap.node.endPosition.row + 1
		const nameRangeText = sourceCode.substring(nameCap.node.startIndex, nameCap.node.endIndex)
		const snippetHash = generateSnippetHash(filePath, nameStartLine, nameEndLine, nameRangeText)
		if (seenHashes.has(snippetHash)) continue
		seenHashes.add(snippetHash)

		// 查找归属的类/接口/枚举/record
		let field: string | undefined = undefined
		const parentTypes = ["class", "interface", "enum", "record", "annotation"]
		for (const parentType of parentTypes) {
			const parentCap = captures.find(
				(c) =>
					c.name === `definition.${parentType}` &&
					c.node.startIndex < node.startIndex &&
					node.endIndex < c.node.endIndex,
			)
			if (parentCap) {
				const parentNameCap = captures.find(
					(c) =>
						c.name === `name.definition.${parentType}` &&
						c.node.startIndex >= parentCap.node.startIndex &&
						c.node.endIndex <= parentCap.node.endIndex,
				)
				if (parentNameCap) {
					field = sourceCode.substring(parentNameCap.node.startIndex, parentNameCap.node.endIndex)
					break
				}
			}
		}

		const scope = buildScopeChain(node)
		snippets.push({
			name,
			type: SnippetType.VariableOrConstant,
			filePath,
			startLine,
			endLine,
			rangeText,
			definitionText: rangeText,
			implementText: rangeText,
			scope,
			fileHash,
			field: field || "",
			definition: { name, type: "field" },
			language: CodeLanguageType.Java,
		})
	}

	// filter snippets by snippetTypes
	return snippets.filter((snippet) => snippetTypes.includes(snippet.type)).sort((a, b) => a.startLine - b.startLine)
}

// VSCode Outline Entry 类型
export interface OutlineEntry {
	type: string
	name: string
	file: string // 表示当前文件
	field?: string // 空字符串代表当前文件的global
	description?: string
}

/**
 * 根据 Java 文件的所有 snippet 生成大纲（Outline），支持 import、class、interface、enum、record、annotation、method、constructor、field
 */
export function buildSummaryFromSnippets(snippets: ISnippetMeta[]): OutlineEntry[] {
	const outline: OutlineEntry[] = []

	// 在Java中，我们应该包含所有顶级定义（不在方法内部的）
	// 包括类、接口、方法、字段等
	const isTopLevel = (s: ISnippetMeta) => {
		// 如果scope为空或者只有一层（比如只有类名），则认为是顶级
		return !s.scope || s.scope.length <= 1
	}

	for (const s of snippets) {
		if (!isTopLevel(s)) continue
		if (s.type === SnippetType.ImportOrInclude) {
			outline.push({
				type: "import",
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.definitionText,
			})
		} else if (s.type === SnippetType.FunctionOrMethod) {
			if (s.field) {
				const methodType = s.definition?.type === "constructor" ? "constructor" : "method"
				outline.push({
					type: methodType,
					name: s.name,
					file: s.filePath,
					field: s.field,
					description: s.definitionText,
				})
			} else {
				outline.push({
					type: "method",
					name: s.name,
					file: s.filePath,
					field: "",
					description: s.definitionText,
				})
			}
		} else if (s.type === SnippetType.VariableOrConstant) {
			const fieldType = s.definition?.type === "field" ? "field" : "variable"
			// 对于outline显示，使用简单的字段名
			const simpleDescription = s.name
			outline.push({
				type: fieldType,
				name: s.name,
				file: s.filePath,
				field: s.field || "",
				description: simpleDescription,
			})
		} else if (s.type === SnippetType.ClassOrInterfaceOrStructOrEnum) {
			const t = s.definition?.type || "class"
			// 对于outline显示，使用简单的类名
			outline.push({
				type: t,
				name: s.name,
				file: s.filePath,
				field: "",
				description: s.name,
			})
		}
	}

	return outline
}

/**
 * 将 OutlineEntry[] 格式化为文本，同一个 field 的放在一起。
 * @param outline OutlineEntry[]
 * @returns string
 */
export function formatOutlineText(outline: OutlineEntry[]): string {
	// 按 field 分组
	const groups: Record<string, OutlineEntry[]> = {}
	for (const entry of outline) {
		const key = entry.field ?? ""
		if (!groups[key]) groups[key] = []
		groups[key].push(entry)
	}

	// 合成文本
	let result = ""
	for (const field of Object.keys(groups).sort()) {
		const group = groups[field]
		const title = field === "" ? "[global]" : `[${field}]`
		result += title + "\n"
		for (const entry of group) {
			let line = `  ${entry.type}`
			if (entry.description) line += `: ${entry.description.replace(/\n/g, " ")}`
			result += line + "\n"
		}
		result += "\n"
	}
	return result.trimEnd()
}

/**
 * 解析Java参数列表，保持完整的类型信息
 * @param paramText 参数文本，例如 "String name, int age, List<String> items"
 * @returns 参数数组，每个元素是 "Type paramName" 形式
 */
function parseJavaParameters(paramText: string): string[] {
	if (!paramText.trim()) return []

	const params: string[] = []
	let current = ""
	let bracketDepth = 0
	let i = 0

	while (i < paramText.length) {
		const char = paramText[i]

		if (char === "<") {
			bracketDepth++
			current += char
		} else if (char === ">") {
			bracketDepth--
			current += char
		} else if (char === "," && bracketDepth === 0) {
			// 到达参数分隔符
			if (current.trim()) {
				params.push(current.trim())
			}
			current = ""
		} else {
			current += char
		}
		i++
	}

	// 添加最后一个参数
	if (current.trim()) {
		params.push(current.trim())
	}

	return params
}
